<template>
  <div class="wrapper-ui-container-menu">
    <ui-logo
      :style="styleMenu"
      @click.native="home"
      class="ui-logo"
      :isCollapsed="collapse"
      :srcImageCollapsed="logoConfiguration.srcImageCollapsed"
      :srcImageNotCollapsed="logoConfiguration.srcImageNotCollapsed"
      :widthLogoCollapsed="logoConfiguration.widthLogoCollapsed"
      :heightLogoCollapsed="logoConfiguration.heightLogoCollapsed"
      :widthLogoNotCollapsed="logoConfiguration.widthLogoNotCollapsed"
      :heightLogoNotCollapsed="logoConfiguration.heightLogoNotCollapsed"
      :tooltipTitle="logoConfiguration.tooltipTitle"
      :tooltipSize="logoConfiguration.tooltipSize"
      :tooltipType="logoConfiguration.tooltipType"
      :tooltipPosition="logoConfiguration.tooltipPosition"
      :multilinedTooltip="logoConfiguration.multilinedTooltip"
      :isTooltipVisible="logoConfiguration.isTooltipVisible"
      :sizeUnit="logoConfiguration.sizeUnit"
    ></ui-logo>
    <div class="ui-container-menu" :style="styleMenu">
      <template v-if="topMenuItems">
        <ui-menu 
          :activeTextColor="activeTextColor"
          :backgroundColor="backgroundColor"
          class="top-menu-item"
          :collapse="collapse"
          :default-active="defaultActive"        
          :menuItems="topMenuItems"
          mode="vertical"
          @select="selectedMenuItem"
          :textColor="textColor"
        ></ui-menu>
      </template>
      <div></div>
      <div>
        <template v-if="bottomMenuItems">
          <ui-menu
            :activeTextColor="activeTextColor"
            :collapse="collapse"
            :backgroundColor="backgroundColor"
            :default-active="defaultActive"
            :menuItems="bottomMenuItems"
            mode="vertical"
            @select="selectedMenuItem"
            :textColor="textColor"
          ></ui-menu>
        </template>
        <div>
          <el-menu
            :collapse="collapse"
            :backgroundColor="collapseButtonColor"
            :textColor="collapseButtonColorText"
            mode="vertical"
            class="collapse-button-container"
            :active-text-color="collapseButtonColorText"
          >
            <el-menu-item 
              class="collapse-button has-text-centered"
              index="collapse-button"
              @click="collapseElements"
            >
              <i 
                v-show="collapse"
                class="el-icon-d-arrow-right"
              ></i>
              <i
                v-show="!collapse"
                class="el-icon-d-arrow-left"
              ></i>
              <span v-show="collapse" slot="title">{{ translateLabel('expand') }}</span>
              <span v-show="!collapse" slot="title">{{ translateLabel('collapse') }}</span>
            </el-menu-item>
          </el-menu>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import UiMenu from '../UIMenu'
import UiLogo from '../UILogo'

/**
   * @vue UIContainerMenu.vue
   * This component create a container to items menu
   * to facilitate navigation, through a set of configuration elements
   * this component has the following function:
   * * Emit the index of menu item selected
   * * Emit event collapse when the collapse button is clicked
   * @version 0.1.0
   * @since 0.1.0
   * @author Nelson Rodriguez [gitlab:@nelrod]
   * @copyright Karanta, CA. 2018
   */
export default {
  name: 'UiContainerMenu',
  components: {
    UiMenu,
    UiLogo
  },
  props: {

    /**
     * Color of the menu item name that is active
     * @prop {String} [activeTextColor = '#409EFF']
     */
    activeTextColor: {
      type: String,
      required: false,
      default: '#409EFF'
    },

    /**
     * Structure to create a navigation menu
     * in the bottom of the UIContainerMenu
     * @prop {Array} bottomMenuItems
     */
    bottomMenuItems: {
      type: Array,
      required: false
    },

    /**
     * Background color of the menu
     * @prop {String} [backgroundColor = '#ffffff']
     */
    backgroundColor: {
      type: String,
      required: false,
      default: '#ffffff'
    },

    /**
     * Enable the collapse of UIContainerMenu
     * @prop {Boolean} [collapse = false]
     */
    collapse: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * Background color of the collapse button
     * @prop {String} [collapseButtonColor = '#ffffff']
     */
    collapseButtonColor: {
      type: String,
      required: false,
      default: '#ffffff'
    },

    /**
     * Color of the text of the collpase button
     * @prop {String} [collapseButtonColorText = '#303133']
     */
    collapseButtonColorText: {
      type: String,
      required: false,
      default: '#303133'
    },

    /**
     * Index of the menu item to active
     * @prop {String} defaultActive
     */
    defaultActive: {
      type: String,
      required: false
    },

    /**
     * Color of the text in the navigation menu
     * @prop {String} [textColor  = '#303133']
     */
    textColor: {
      type: String,
      required: false,
      default: '#303133'
    },

    /**
     * Structure to create a navigation menu
     * in the top of the UIContainerMenu
     * @prop {Array} topMenuItems
     */
    topMenuItems: {
      type: Array,
      required: false
    },

    /**
     * Sets the UILogo configuration
     * @prop {Object} [logoConfiguration =
     * {
     *   srcImageCollapsed: '',
     *   srcImageNotCollapsed: '',
     *   widthLogoCollapsed: 10,
     *   heightLogoCollapsed: 25,
     *   widthLogoNotCollapsed: 200,
     *   heightLogoNotCollapsed: 200,
     *   sizeUnit: 'px',
     *   tooltipTitle: '',
     *   tooltipSize: 'is-medium',
     *   tooltipType: 'is-primary',
     *   tooltipPosition: 'is-right',
     *   multilinedTooltip: false,
     *   isTooltipVisible: true
     * }]
     */
    logoConfiguration: {
      type: Object,
      required: false,
      default: function () {
        return {
          srcImageCollapsed: '',
          srcImageNotCollapsed: '',
          widthLogoCollapsed: 10,
          heightLogoCollapsed: 25,
          widthLogoNotCollapsed: 200,
          heightLogoNotCollapsed: 200,
          sizeUnit: 'px',
          tooltipTitle: '',
          tooltipSize: 'is-medium',
          tooltipType: 'is-primary',
          tooltipPosition: 'is-right',
          multilinedTooltip: false,
          isTooltipVisible: true
        }
      }
    },

    /**
     * Sets the index of the logo
     * @prop {String} [index = '']
     */
    index: {
      type: String,
      required: false,
      default: ''
    }
  },
  computed: {
    /**
     * Add style to UIContainerMenu
     * @return {Object}
     */
    styleMenu () {
      return {
        backgroundColor: this.backgroundColor
      }
    }
  },
  methods: {

    /**
     * Emit event when click on the button collapse
     * @return {Event}
     * @public
     */
    collapseElements () {
      /**
       * Emits when Collapse button is clicked
       * @event collapse
       * @type {null}
       */
      this.$emit('collapse')
    },

    /**
     * Emits the index that it's passed via props when the logo in clicked
     * @return {Event}
     * @public
     */
    home () {
    /**
     * Emits the event menu-item-select when the logo is clicked
     * @event menu-item-select
     * @type {String}
     */
      this.$emit('menu-item-select', this.index)
    },

    /**
     * Emit event with the data of the selected menuItem
     * @param {String} data Data of the selected menuItem
     * @return {Event}
     * @public
     */
    selectedMenuItem (data) {
      /**
       * Emits when a menuItem is clicked and return Data of the selected menuItem
       * @event menu-item-select
       * @type {String}
       */
      this.$emit('menu-item-select', data)
    },

    /**
     * Translate the label of collapse button
     * @param {String} label to translate
     * @return {String}
     * @public
     */
    translateLabel (value) {
      return this.$i18n.t(`template.${value}.label`)
    }
  }
}
</script>

<style scoped>
* {
  border: none;
}

.wrapper-ui-container-menu {
  display: grid;
  grid-template-rows: auto 1fr;
  grid-template-areas:
    "ui-logo"
    "ui-container-menu";
  align-content: stretch;
}

.ui-container-menu {
  grid: ui-container-menu;
  display: grid;
  align-content: space-between;
  overflow-x: auto;
}

.ui-logo {
  grid: ui-logo;
  text-align: center;
}

.top-menu-item {
  overflow-y: auto;
}

::-webkit-scrollbar {
    width: 5px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background-color: rgba(0,0,0,0.2);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background-color: rgba(0,0,0,0.4);
}
</style>



