<template>
  <div>
   <title-component 
   :element="titlePage"
   titleClass="is-size-2"
   :isPage="true">
   </title-component>
    <hr>
    <div class="columns">
      <div class="column">
        <b-field
          label="Collapse"
          horizontal
        >
          <b-switch v-model="collapse">
          </b-switch>
        </b-field>
        <b-field
          label="Text Color"
          horizontal
        >
          <b-input
            type="color"
            v-model="textColor"
          >
          </b-input>
        </b-field>
        <b-field
          label="Active Text Color"
          horizontal
        >
          <b-input
            type="color"
            v-model="activeTextColor"
          >
          </b-input>
        </b-field>
        <b-field
          label="Background Color"
          horizontal
        >
          <b-input
            type="color"
            v-model="backgroundColor"
          >
          </b-input>
        </b-field>
        <b-field
          label="Collapse Button Text Color"
          horizontal
        >
          <b-input
            type="color"
            v-model="collapseButtonColorText"
          >
          </b-input>
        </b-field>
        <b-field
          label="Collapse Button Color"
          horizontal
        >
          <b-input
            type="color"
            v-model="collapseButtonColor"
          >
          </b-input>
        </b-field>
      </div>
      <div class="column">
        <b-message
          title="Console"
          :closable="false"
          >
					<b-message
            :title="eventname"
            size="is-small"
            :closable="false"
          >
            <pre
              class="scroll-able event-size"
            >{{ eventListener }}</pre>
          </b-message>
				</b-message>
      </div>
    </div>
    <div class="columns">
      <div class="column">
        <ui-container-menu
          @collapse="collapseEvent"
          @menu-item-select="menuItemSelectEvent"
          :active-text-color="activeTextColor"
          :bottom-menu-items="bottomMenuItems"
          :background-color="backgroundColor"
          :defaultActive="defaultActive"
          :collapse="collapse"
          :collapse-button-color-text="collapseButtonColorText"
          :collapse-button-color="collapseButtonColor"
          :class="collapseTest"
          :top-menu-items="topMenuItems"
          :text-color="textColor"
          :logoConfiguration="logoConfiguration"
        ></ui-container-menu>
      </div>
      <div class="column">
        <ui-container-menu
          :style="{height: '400px'}"
          @collapse="collapseEvent"
          @menu-item-select="menuItemSelectEvent"
          :active-text-color="activeTextColor"
          :bottom-menu-items="bottomMenuItems"
          :background-color="backgroundColor"
          :defaultActive="defaultActive"
          :collapse="collapse"
          :collapse-button-color-text="collapseButtonColorText"
          :collapse-button-color="collapseButtonColor"
          :class="collapseTest"
          :top-menu-items="topMenuItems"
          :text-color="textColor"
          :logoConfiguration="logoConfiguration2"
        ></ui-container-menu>
      </div>
       <div class="positionRight">
          <b-tooltip
          label="Changelog"
          :active="true"
          position="is-left"
          type="is-dark">
            <ui-panel-container
              :panelHeight= "550"
              :panelWidth= "600"
               unitHeight="px"
               unitWidth="px"
              backgroundColor="white"
              panelBorder="ridge"
              panelBorderColor="black"
              :collapseButtonEnabled="true"
              collapseButtonPosition="left"
              collapseButtonBackgroundColor="#C2C2C2"
              :style="collapseLeft"
              @on-collapse="collapsePanelLeft">
                <markdown></markdown>
              </ui-panel-container>
          </b-tooltip>
        </div>
    </div>
  </div>
</template>

<script>
import UiContainerMenu from '../../../components/common/UIContainerMenu'
import UiPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import { updates } from '../../../libraryUpdates.js'
import readme from '../../../components/common/UIContainerMenu/changelog.md'
import titleComponent from '../titleComponent.vue'

export default {
  name: 'UIContainerMenuPage',
  components: {
    UiContainerMenu,
    UiPanelContainer,
    'markdown': readme,
    titleComponent
  },
  data () {
    return {
      collapseLeft: {},
      titlePage: updates.ContainerMenu,
      activeTextColor: '#ffd04b',
      backgroundColor: '#545c64',
      bottomMenuItems: [
        {
          label: 'user',
          icon: 'fas fa-user',
          props: {
            index: 'user'
          }
        }
      ],
      defaultActive: 'nav-1',
      collapse: true,
      collapseButtonColor: '#545c64',
      collapseButtonColorText: '#ffffff',
      eventListener: '',
      eventname: '',
      textColor: '#ffffff',
      topMenuItems: [
        {
          label: 'wellManager',
          icon: 'far fa-check-circle',
          props: {
            index: 'nav-1',
            class: 'selected-element'
          },
          children: [
            {
              label: 'well',
              icon: 'far fa-check-circle',
              props: {
                index: 'nav-1-1'
              }
            },
            {
              label: 'field',
              icon: 'far fa-check-circle',
              props: {
                index: 'nav-1-2'
              }
            },
            {
              label: 'pool',
              icon: 'far fa-check-circle',
              props: {
                index: 'nav-1-3',
                disabled: true
              }
            }
          ]
        },
        {
          label: 'wellTest',
          icon: 'far fa-check-circle',
          props: {
            index: 'nav-2'
          },
          children: [
            {
              label: 'productionTest',
              icon: 'far fa-check-circle',
              props: {
                index: 'nav-2-1'
              }
            }
          ]
        },
        {
          label: 'wellAccounting',
          icon: 'far fa-check-circle',
          props: {
            index: 'nav-3'
          }
        },
        {
          label: 'facilityManager',
          icon: 'far fa-check-circle',
          props: {
            index: 'nav-4',
            disabled: true
          }
        }
      ],
      /*
       * Configuration of the firts UILogo
       */
      logoConfiguration: {
        srcImageCollapsed: 'src/pages/common/UIContainerMenu/mydasLogoWhite.svg',
        srcImageNotCollapsed: 'src/pages/common/UIContainerMenu/mydasLogoWhiteName.svg',
        widthLogoCollapsed: 65,
        heightLogoCollapsed: 57,
        widthLogoNotCollapsed: 300,
        heightLogoNotCollapsed: 120,
        sizeUnit: 'px',
        tooltipTitle: '',
        tooltipSize: 'is-medium',
        tooltipType: 'is-black',
        tooltipPosition: 'is-right',
        multilinedTooltip: false,
        isTooltipVisible: true
      },
      /*
       * Configuration of the second UILogo
       */
      logoConfiguration2: {
        srcImageCollapsed: 'src/pages/common/UIContainerMenu/mydasLogoWhiteLine.svg',
        srcImageNotCollapsed: 'src/pages/common/UIContainerMenu/mydasLogoWhiteLineName.svg',
        widthLogoCollapsed: 65,
        heightLogoCollapsed: 57,
        widthLogoNotCollapsed: 300,
        heightLogoNotCollapsed: 120,
        sizeUnit: 'px',
        tooltipTitle: '',
        tooltipSize: 'is-medium',
        tooltipType: 'is-black',
        tooltipPosition: 'is-right',
        multilinedTooltip: false,
        isTooltipVisible: true
      }

    }
  },
  computed: {
    collapseTest () {
      return this.collapse ? 'test--collapse' : 'test'
    }
  },
  methods: {
    collapseEvent () {
      this.eventname = 'Collapse'
      this.eventListener = ''
      this.collapse = !this.collapse
    },
    menuItemSelectEvent (data) {
      this.eventname = 'menuItemSelect'
      this.eventListener = data
      this.defaultActive = data
    },
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.test {
  height: 600px;
  width: 300px;
  transition: width 0.5s;
}

.test--collapse {
  height: 600px;
  width: 64px;
  transition: width 0.5s;  
}

.positionRight {
  display: flex;
  justify-content: flex-end;
}

</style>



