<template>
  <div>
    <b-field v-if="searchable">
      <b-input
        :placeholder="translate('search')"
        type="search"
        icon="search"
        v-model="filterText"
      >
      </b-input>
    </b-field>
    <el-tree
      ref="kpsTree"
      :accordion="accordion"
      :data="data"
      :default-expanded-keys="defaultExpandedKeys"
      :node-key="nodeKey"
      :props="defaultProps"
      :show-checkbox="showCheckbox"
      :filter-node-method="filterNode"
      :highlight-current="true"
      @node-click="selectedNode"
      @check="checkedNode"
    >
      <span slot-scope="{ node, data }">
        <span
          class="icon-node"
          v-show="node.icon && showIcon">
          <i :class="node.icon"></i>
        </span>
        <span>{{ node.label }}</span>
        <span
          class="number-children"
          v-show="data.children && numberChildren">
          ({{ numberLeaf(data) }})
        </span>
      </span>
    </el-tree>
  </div>
</template>

<script>
export default {
  /**
   * @vue UINavigationTree.vue
   * Should component create a navigation tree through
   * a object with a structure that is
   * defined in the documentation.
   * this component has the following function:
   * * Render a navigation tree
   * * Emit the object of the node selected of node tree
   * @version 0.1.0
   * @since 0.1.0
   * @author Nelson Rodriguez [gitlab:@nelrod]
   * @copyright Karanta, CA. 2018
   */
  name: 'UiNavigationTree',
  props: {
    /**
     * Enable to accordion
     * @prop {Boolean} [accordion = false]
     */
    accordion: {
      type: Boolean,
      required: false,
      default: false
    },
    /**
     * Structure used to create the navigation tree (node-tree)
     * @prop {Array} data
     */
    data: {
      type: Array,
      required: true
    },

    /**
      * Props for the nodes in the node-tree
      * @prop {Object} [defaultProps = {children: 'children', label: 'label', icon: 'icon',disabled: 'disabled'}]
      */
    defaultProps: {
      type: Object,
      required: false,
      default: function () {
        return {
          children: 'children',
          label: 'label',
          icon: 'icon',
          disabled: 'disabled'
        }
      }
    },
    /**
     * Expand element for default
     * @prop {Object}
     */
    defaultExpandedKeys: {
      type: Array,
      required: false
    },
    /**
      * Unique identifier in the whole tree for the nodes
      * @prop {String} [nodeKey = 'id']
      */
    nodeKey: {
      type: String,
      required: false,
      default: 'id'
    },

    /**
     * Enable to show number of children
     * @prop {Boolean} [number = false]
     */
    numberChildren: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * Enable search input in navigation tree
     * @prop {Boolean} [searchable = false]
     */
    searchable: {
      type: Boolean,
      required: false,
      default: true
    },

    /**
     * Name of attribute to search in the node object
     * @prop {String} [searchAtt = 'label']
     */
    searchAtt: {
      type: String,
      required: false,
      default: 'label'
    },
    /**
     * Enable the checkbox for the node-tree
     * @prop {Boolean} [showCheckbox = false]
     */
    showCheckbox: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * Enable the icons for the node-tree
     * @prop {Boolean} [showIcon = false]
     */
    showIcon: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  data () {
    return {
      /**
       * Text to search in the node-tree
       * @type {String}
       */
      filterText: '',
      /**
       * Value of the first primary key
       * @type {Array}
       */
      dataKeyExpand: []
    }
  },
  watch: {

    /**
     * Watch change in the filterText to find
     * @param {String} value Value to search
     */
    filterText (value) {
      this.$refs.kpsTree.filter(value)
    }
  },
  methods: {
    /**
     * Filter the node in the node-tree
     * @param {String} value Value of atribute to search
     * @param {Object} data Object of the node in the node tree
     * @public
     */
    filterNode (value, data) {
      if (!value) return true
      if (!data[this.searchAtt]) return false
      return data[this.searchAtt].toUpperCase().indexOf(value.toUpperCase()) !== -1
    },

    /**
     * Emit data object of the selected node
     * @param data {Object} Data object
     * @public
     */
    selectedNode (data) {
      /**
      * @event selected-node
      * @type {Object}
      */
      this.$emit('selected-node', data)
    },

    /**
     * Emit array of data objects of the checked nodes
     * @param node {Object} Object of the selected node
     * @param data {Array} Array of objects of the selected node
     */
    checkedNode (node, data) {
      /**
      * @event checked-node
      * @type {Array}
      */
      this.$emit('checked-node', data.checkedNodes)
    },

    /**
     * Count the number of leaves on node-tree
     * @param data {Array} Array of objects of the node-tree
     * @return {Number}
     * @public
     */
    numberLeaf (data) {
      let count = 0
      if (data.children) {
        for (let i = 0; i < data.children.length; i++) {
          count += this.numberLeaf(data.children[i])
        }
        return count
      } else {
        return 1
      }
    },

    /**
     * translates message to UINavigationTree
     * @param {String} message Represents the placeholder to be translated
     * @return {String} message translated
     * @public
     */
    translate (message) {
      return this.$i18n.t(`template.${message}.label`)
    }
  }
}
</script>

<style scoped>

</style>



