<template>
  <div>
   <title-component 
   :element="titlePage"
   titleClass="is-size-2"
   :isPage="true">
   </title-component>
    <hr>
    <div class="columns">
      <div class="column">
        <label>Loading</label>
        <b-switch v-model="props.loading">
        </b-switch>
      </div>
      <div class="column">
        <label>{{editionPreview}}</label>
        <b-switch v-model="properties.edition">
        </b-switch>
      </div>
      <div class="column">
        <label>{{openClose}}</label>
        <b-switch v-model="properties.isOpen">
        </b-switch>
      </div>
      <div class="column">
        <label>Horizontal</label>
        <b-switch v-model="props.horizontal">
        </b-switch>
      </div>
      <div>
       <b-field label="Gap" v-if="properties.edition">
            <b-select
            placeholder="Select gap" 
            v-model="properties.gapToolBar">
                <option
                v-for="(sizes, key) in gapToolBarDynamicClass"
                :key="key">
                {{sizes}}
                </option>
            </b-select>
      </b-field>
      </div>
      <div class="column">
        <label>Header Size</label>
        <b-select v-model="headerSize">
          <option
            v-for="size in 7" 
            :key=size.key 
            :value="size">
            {{size}}
          </option>
        </b-select>
      </div>
      <div class="column">
        <label>Header Style</label>
        <b-select v-model="headerStyle">
          <option
            v-for="style in ['uppercase', 'lowercase', 'italic', 'capitalized']" 
            :key="style.key" 
            :value="style">
            {{style}}
          </option>
        </b-select>
      </div>
      <div class="column">
        <label>Header Position</label>
        <b-select v-model="headerPosition">
          <option
            v-for="position in ['left', 'right', 'centered', 'justified']" 
            :key="position.key" 
            :value="position">
            {{position}}
          </option>
        </b-select>
      </div>
      <div class="column">
        <label>Close Size Button</label>
        <b-select v-model="properties.sizeCloseButton">
          <option
            v-for="size in ['is-small', 'is-medium', 'is-large']" 
            :key="size.key" 
            :value="size">
            {{size}}
          </option>
        </b-select>
      </div>
      <div class="column">
        <label>Toolbar Top Align</label>
        <b-select
          v-model="properties.toolbarTopAlign" 
          :disabled="properties.edition">
          <option
            v-for="position in ['left', 'right', 'center', 'none']" 
            :key="position.key"
            :value="position">
            {{position}}
          </option>
        </b-select>
      </div>
      <div class="column">
        <label>Toolbar Bottom Align</label>
        <b-select
          v-model="properties.toolbarBottomAlign" 
          :disabled="!properties.edition">
          <option
            v-for="position in ['left', 'right', 'center', 'none']" 
            :key="position.key" 
            :value="position">
            {{position}}
          </option>
        </b-select>
      </div>
    </div>
    <div class="columns">
      <div class="column">
        <ui-management-panel-edition
        v-model="value"
        v-bind.sync="props"
        @save="eventHandler('save', $event)"
        @default-button-bottom="eventHandler('default-button-bottom', $event)"
        @default-button-top="eventHandler('default-button-top', $event)">
      </ui-management-panel-edition>
      </div>
      <div class="column is-4">
        <b-message
        title="Console"
        :closable="false">
        <b-message v-if="eventLog"
          :title="eventLog.name"
          :closable="false">
          <pre>{{eventLog.event}}</pre>
        </b-message>
        </b-message>
      </div>
    </div>
    <hr>
      <div class="positionRight">
        <b-tooltip
        label="Changelog"
        :active="true"
        position="is-left"
        type="is-dark">
          <ui-panel-container
            :panelHeight= "250"
            :panelWidth= "600"
             unitHeight="px"
             unitWidth="px"
            backgroundColor="white"
            panelBorder="ridge"
            panelBorderColor="black"
            :collapseButtonEnabled="true"
            collapseButtonPosition="left"
            collapseButtonBackgroundColor="#C2C2C2"
            :style="collapseLeft"
            @on-collapse="collapsePanelLeft">
              <markdown></markdown>
            </ui-panel-container>
        </b-tooltip>
      </div>
  </div>
</template>

<script>
import UIManagementPanelEdition from '../../../components/common/UIManagementPanelEdition/index'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import titleComponent from '../titleComponent.vue'
import { updates } from '../../../libraryUpdates.js'
import readme from '../../../components/common/UIManagementPanelEdition/changelog.md'

export default {
  name: 'UIManagementPanelEditionPage',
  components: {
    'UiManagementPanelEdition': UIManagementPanelEdition,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },
  data () {
    return {
      titlePage: updates.ManagementPanelEdition,
      collapseLeft: {},
      eventLog: null,
      headerSize: 3,
      headerStyle: 'uppercase',
      headerPosition: 'centered',
      value: [
        {
          classTag: 'class1',
          children: [
            {
              propTag: 'id.areaId',
              edition: {
                editable: false,
                preview: false,
                type: 'ID',
                header: false,
                visibleLabel: true,
                component: {
                  name: 'b-input',
                  props: {
                    type: 'text'
                  },
                  attrs: {
                    disabled: true
                  }
                }
              }
            },
            {
              propTag: 'id.areaType',
              edition: {
                editable: false,
                preview: false,
                type: 'ID',
                header: false,
                visibleLabel: false,
                component: {
                  name: 'b-input',
                  props: {
                    type: 'text'
                  },
                  attrs: {
                    disabled: true
                  }
                }
              }
            },
            {
              propTag: 'ppdmGuid',
              edition: {
                editable: false,
                preview: false,
                type: 'STRING',
                header: false,
                visibleLabel: true,
                component: {
                  name: 'b-input',
                  props: {
                    disabled: true,
                    type: 'text'
                  },
                  attrs: {
                    disabled: true
                  }
                }
              }
            },
            {
              propTag: 'preferredName',
              edition: {
                preview: true,
                editable: true,
                type: 'STRING',
                header: true,
                visibleLabel: true,
                component: {
                  name: 'b-input',
                  props: {
                    type: 'text'
                  },
                  directives: [
                    {
                      name: 'validate',
                      value: {
                        required: true
                      }
                    }
                  ]
                }
              }
            },
            {
              propTag: 'activeInd',
              edition: {
                visibleLabel: true,
                editable: false,
                preview: false,
                header: false,
                type: 'STRING',
                component: {
                  name: 'b-select',
                  props: {},
                  directives: [
                    {
                      name: 'validate',
                      value: {
                        required: false
                      }
                    }
                  ]
                }
              }
            },
            {
              propTag: 'effectiveDate',
              edition: {
                editable: true,
                preview: true,
                header: false,
                type: 'DATE',
                visibleLabel: true,
                component: {
                  name: 'b-datepicker',
                  props: {
                    icon: 'fa fa-calendar-alt',
                    readonly: false
                  },
                  directives: [
                    {
                      name: 'validate',
                      value: {
                        required: true
                      }
                    }
                  ]
                }
              }
            },
            {
              propTag: 'expiryDate',
              edition: {
                editable: true,
                preview: true,
                header: false,
                type: 'DATE',
                visibleLabel: true,
                component: {
                  name: 'b-datepicker',
                  props: {
                    icon: 'fa fa-calendar-alt'
                  },
                  directives: [
                    {
                      name: 'validate',
                      value: {
                        required: true,
                        after: 'effectiveDate'
                      }
                    }
                  ]
                }
              }
            },
            {
              propTag: 'areaMaxLatitude',
              edition: {
                editable: true,
                preview: true,
                header: false,
                type: 'NUMBER',
                visibleLabel: true,
                component: {
                  name: 'b-input',
                  props: {
                    type: 'number'
                  },
                  directives: [
                    {
                      name: 'validate',
                      value: {
                        required: true
                      }
                    }
                  ]
                }
              }
            },
            {
              propTag: 'areaMinLatitude',
              edition: {
                editable: true,
                preview: true,
                header: false,
                type: 'NUMBER',
                visibleLabel: true,
                component: {
                  name: 'b-input',
                  props: {
                    type: 'number'
                  },
                  directives: [
                    {
                      name: 'validate',
                      value: {
                        required: true
                      }
                    }
                  ]
                }
              }
            },
            {
              propTag: 'areaMaxLongitude',
              edition: {
                editable: true,
                preview: true,
                header: false,
                type: 'NUMBER',
                visibleLabel: true,
                component: {
                  name: 'b-input',
                  props: {
                    type: 'number'
                  },
                  directives: [
                    {
                      name: 'validate',
                      value: {
                        required: true
                      }
                    }
                  ]
                }
              }
            },
            {
              propTag: 'areaMinLongitude',
              edition: {
                editable: true,
                preview: true,
                header: false,
                type: 'NUMBER',
                visibleLabel: true,
                component: {
                  name: 'b-input',
                  props: {
                    type: 'number'
                  },
                  directives: [
                    {
                      name: 'validate',
                      value: {
                        required: true
                      }
                    }
                  ]
                }
              }
            }
          ]
        },
        {
          classTag: 'class2',
          children: [
            {
              propTag: 'remark',
              edition: {
                editable: true,
                preview: true,
                header: false,
                type: 'STRING',
                visibleLabel: true,
                component: {
                  name: 'b-input',
                  props: {
                    type: 'textarea'
                  },
                  directives: [
                    {
                      name: 'validate',
                      value: {
                        required: true
                      }
                    }
                  ]
                }
              }
            }
          ]
        }
      ],
      properties: {
        entity: 'AREA',
        edition: false,
        gapToolBar: 'is-1',
        headerIcon: 'fa fa-map',
        horizontal: true,
        sizeCloseButton: 'is-medium',
        toolbarTopAlign: 'right',
        toolbarBottomAlign: 'center',
        loading: false,
        isOpen: true,
        data: {
          id: {
            areaId: '14_093',
            areaType: 'COUNTY'
          },
          activeInd: null,
          areaMaxLatitude: 10,
          areaMaxLongitude: 15,
          areaMinLatitude: 5,
          areaMinLongitude: 10,
          coordAcquisitionId: null,
          coordSystemId: null,
          effectiveDate: '2018-01-17T00:00:00.000+0000',
          expiryDate: '2018-01-17T00:00:00.000+0000',
          localCoordSystemId: null,
          ppdmGuid: '14_093 COUNTY',
          preferredName: 'venezuela',
          remark: 'Hola Mundo',
          rowChangedBy: 'UpdatedUser',
          rowChangedDate: '2018-04-17',
          rowCreatedBy: null,
          rowCreatedDate: null,
          rowEffectiveDate: null,
          rowExpiryDate: null,
          rowQuality: 'Real',
          source: 'PPDM',
          sourceDocumentId: null
        }
      }
    }
  },
  computed: {
    props () {
      let editionProps = this.properties
      editionProps['headerClass'] = `is-size-${this.headerSize} has-text-${this.headerPosition} is-${this.headerStyle}`
      return editionProps
    },
    editionPreview () {
      return (this.properties.edition) ? 'Edition' : 'Preview'
    },
    openClose () {
      return (this.properties.isOpen) ? 'Open' : 'Close'
    },
    gapToolBarDynamicClass () {
      let classes = [
        'is-0',
        'is-1',
        'is-2',
        'is-3',
        'is-4',
        'is-5',
        'is-6',
        'is-7',
        'is-8'
      ]
      return classes
    }
  },
  methods: {
    eventHandler (eventName, event) {
      this.eventLog = {
        name: eventName,
        event
      }
    },
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>

<style>
.console-header {
  background-color: black;
  border-radius: 15px 15px 0px 0px
}

.console-event {
  background-color: rgb(125, 125, 125);
  color: white
}

.positionRight {
  display: flex;
  justify-content: flex-end;
}
</style>



