<template>
  <div>
   <title-component 
   :element="titlePage"
   titleClass="is-size-2"
   :isPage="true">
   </title-component>      
   <div class="columns">
      <div class="column">
        <b-switch v-model="props.isOpen">
          {{(props.isOpen) ? 'Is Open' : 'Is Close'}}
        </b-switch>
      </div>
      <div class="column">
        <label>
          Header Size
        </label>
        <b-select v-model="headerSize">
          <option
            v-for="size in 7"
            :value="size">
            {{size}}
          </option>
        </b-select>
      </div>
      <div class="column">
        <label>
          Header Style
        </label>
        <b-select v-model="headerStyle">
          <option
            v-for="style in ['uppercase', 'italic', 'lowercase', 'capitalized']"
            :value="style">
            {{style}}
          </option>
        </b-select>
      </div>
      <div class="column">
        <label>
          Header Align
        </label>
        <b-select v-model="headerAlign">
          <option
            v-for="align in ['left', 'right', 'centered', 'justified']"
            :value="align">
            {{align}}
          </option>
        </b-select>
      </div>
      <div class="column">
        <label>
          Close Button Size
        </label>
        <b-select v-model="props.sizeCloseButton">
          <option
            v-for="size in ['is-small', 'is-medium', 'is-large']"
            :value="size">
            {{size}}
          </option>
        </b-select>
      </div>
      <div class="column">
        <label>
          Toolbar Align
        </label>
        <b-select v-model="props.toolbarAlign">
          <option
            v-for="align in ['left', 'right', 'center', 'none']"
            :value="align">
            {{align}}
          </option>
        </b-select>
      </div>
    </div>
    <ui-management-panel-preview
      v-model="config"
      :data="data"
      v-bind="previewProps">
    </ui-management-panel-preview>
    <hr>
      <div class="positionRight">
        <b-tooltip
        label="Changelog"
        :active="true"
        position="is-left"
        type="is-dark">
          <ui-panel-container
            :panelHeight= "150"
            :panelWidth= "600"
             unitHeight="px"
             unitWidth="px"
            backgroundColor="white"
            panelBorder="ridge"
            panelBorderColor="black"
            :collapseButtonEnabled="true"
            collapseButtonPosition="left"
            collapseButtonBackgroundColor="#C2C2C2"
            :style="collapseLeft"
            @on-collapse="collapsePanelLeft">
              <markdown></markdown>
            </ui-panel-container>
        </b-tooltip>
      </div>
  </div>
</template>

<script>
import UIManagementPanelPreview from '../../../components/common/UIManagementPanelPreview/index.js'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import titleComponent from '../titleComponent.vue'
import { updates } from '../../../libraryUpdates.js'
import readme from '../../../components/common/UIManagementPanelPreview/changelog.md'

export default {
  name: 'UIManagementPanelPreviewPage',

  components: {
    UiManagementPanelPreview: UIManagementPanelPreview,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },

  data () {
    return {
      /**
       * Version data
       */
      titlePage: updates.ManagementPanelPreview,
      collapseLeft: {},
      /**
       * Config struture for the entities used in v-model
       * @type {Object}
       */
      config: [
        {
          classTag: 'class1',
          childs: [
            {
              propTag: 'areaId',
              preview: {
                visible: true,
                header: false,
                visibleLabel: true,
                type: 'ID'
              }
            },
            {
              propTag: 'areaType',
              preview: {
                visible: true,
                header: false,
                visibleLabel: true,
                type: 'ID'
              }
            }
          ]
        },
        {
          classTag: 'class2',
          childs: [
            {
              propTag: 'preferredName',
              preview: {
                visible: true,
                header: true,
                visibleLabel: true
              }
            },
            {
              propTag: 'effectiveDate',
              preview: {
                visible: true,
                header: false,
                visibleLabel: true,
                type: 'date'
              }
            }
          ]
        }
      ],

      data: {
        id: {
          areaId: '1',
          areaType: 'COUNTRY'
        },
        preferredName: 'VENEZUELA',
        effectiveDate: new Date('2019/01/16')
      },

      headerSize: 3,
      headerStyle: 'uppercase',
      headerAlign: 'centered',
      /**
       * And object with the properties of the component, to make the html looks more clean
       * @type {Object}
       */
      props: {
        sizeCloseButton: 'is-medium',
        headerClass: 'is-size-3 is-uppercase has-text-centered',
        headerIcon: 'fa fa-tint',
        collapseIcon: 'fa fa-chevron-down',
        isOpen: true,
        entity: 'area',
        toolbar: {
          buttonConfigurations: [{
            buttonType: 'edition',
            visibleLabel: true,
            buttonClass: 'is-primary',
            iconType: 'fas fa-pencil-alt',
            buttonSize: 'is-medium',
            visibleIcon: true,
            isRounded: false,
            visibleToolTip: true,
            positionToolTip: 'is-top',
            typeToolTip: 'is-info',
            sizeToolTip: 'is-medium',
            multilinedToolTip: true
          }],
          // buttonQuantity: 1,
          buttonDropDownSize: 'is-large',
          dropDownAppear: false
        },
        toolbarAlign: 'right'
      }
    }
  },

  computed: {
    previewProps () {
      let previewProps = this.props
      previewProps['headerClass'] = `is-size-${this.headerSize} is-${this.headerStyle} has-text-${this.headerAlign}`
      return previewProps
    }
  },
  methods: {
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>

<style lang="css" scoped>
.positionRight {
  display: flex;
  justify-content: flex-end;
}
</style>



