const configSystem = {
  defaultLanguage: 'es-ES',
  rowsPerPage: 20,
  possibleRowsPerPage: [20, 50, 100],
  defaultIconPack: 'fa',
  googleMapsKey: null,
  defaultDateFormatter: date => {
    return `${date.getUTCFullYear()}-${date.getUTCMonth() + 1}-${date.getUTCDate()}`
  }
}

module.exports = configSystem



