<template>
  <div>
    <title-component 
    :element="titlePage"
    titleClass="is-size-2"
    :isPage="true">
    </title-component>
    <hr>
    <div class="columns">
      <b-field label="Alineacin">
        <b-select 
          placeholder="Selecciona la alineacin del contenido"
          rounded v-model="align"
        >
          <option value="left">left</option>
          <option value="center">center</option>
          <option value="right">right</option>
          <option value="none">none</option>
        </b-select>
      </b-field>
      <b-field label="Tamao del boton cerrar">
        <b-select
          placeholder="Selecciona el tamao del boton"
          rounded v-model="sizeCloseButton"
        >
          <option value="is-small">is-small</option>
          <option value="is-medium">is-medium</option>
          <option value="is-large">is-large</option>
        </b-select>
      </b-field>
      <div class="column">
        <b-switch
          type="is-info"
          v-model="closeable"
        >
          Boton cerrar visible
        </b-switch>
      </div>
    </div>
    <ui-container
      :align="align"
      :sizeCloseButton="sizeCloseButton"
      :closeable="closeable"
      @close="closeAction"
    >
      <h3 class="is-size-5">UI Management Panel Show Hide Columns</h3>
      <hr>
      <ui-management-panel-show-hide-columns
        v-model="config"
        v-bind="props"
      >
      </ui-management-panel-show-hide-columns>
    </ui-container>
    <hr>
    <div class="positionRight">
        <b-tooltip
        label="Changelog"
        :active="true"
        position="is-left"
        type="is-dark">
          <ui-panel-container
            :panelHeight= "210"
            :panelWidth= "600"
             unitHeight="px"
             unitWidth="px"
            backgroundColor="white"
            panelBorder="ridge"
            panelBorderColor="black"
            :collapseButtonEnabled="true"
            collapseButtonPosition="left"
            collapseButtonBackgroundColor="#C2C2C2"
            :style="collapseLeft"
            @on-collapse="collapsePanelLeft">
              <markdown></markdown>
            </ui-panel-container>
        </b-tooltip>
    </div>
  </div>
</template>
<script>
import UIContainer from '../../../components/common/UIContainer/index.js'
import UIManagementPanelShowHideColumns from '../../../components/common/UIManagementPanelShowHideColumns/index.js'
import { updates } from '../../../libraryUpdates.js'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import readme from '../../../components/common/UIContainer/changelog.md'
import titleComponent from '../titleComponent.vue'

export default {
  name: 'UIContainerPage',

  components: {
    'UiContainer': UIContainer,
    UiManagementPanelShowHideColumns: UIManagementPanelShowHideColumns,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },

  data () {
    return {
      /**
       * Version data
       */
      collapseLeft: {},
      titlePage: updates.Container,
      /**
       * Configurations
       */
      align: null,
      sizeCloseButton: null,
      closeable: true,
      /**
       * Config struture for the entities used in v-model
       * @type {Object}
       */
      config: [
        {
          classTag: 'class1',
          childs: [
            {
              propTag: 'id.areaId',
              tabulatedData: {
                type: 'string',
                visibleOnTable: true,
                sortable: true,
                centered: true,
                classes: ''
              }
            },
            {
              propTag: 'id.areaType',
              tabulatedData: {
                type: 'string',
                visibleOnTable: true,
                sortable: true,
                centered: true,
                classes: ''
              }
            }
          ]
        },
        {
          classTag: 'class2',
          childs: [
            {
              propTag: 'preferredName',
              tabulatedData: {
                type: 'string',
                visibleOnTable: true,
                sortable: true,
                centered: true,
                classes: ''
              }
            }
          ]
        }
      ],

      /**
       * And object with the properties of the component, to make the html looks more clean
       * @type {Object}
       */
      props: {
        entity: 'AREA',
        icon: 'fa fa-chevron-down',
        isOpen: true
      }
    }
  },

  methods: {
    closeAction (event) {
      console.log('Close')
    },
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>
<style>

.positionRight {
  display: flex;
  justify-content: flex-end;
}

</style>



