<template>
  <div>
    <title-component 
    :element="titlePage"
    titleClass="is-size-2"
    :isPage="true">
    </title-component>
    <hr>
    <hr>
    <h4 class = "is-size-5">Unit Of Measure</h4>
    <b-switch v-model="label">{{onOffLabel}}</b-switch>
    <b-switch v-model="rounded">{{onOffRounded}}</b-switch>
    <b-switch v-model="loading">{{onOffLoading}}</b-switch>
    <b-switch v-model="disabled">{{onOffDisabled}}</b-switch>
    <ui-units-of-measure
      v-model = "measure"
      magnitudeType = "Length"
      :isPlaceholderActivated="true"
      :data="Units"
      :rounded="rounded"
      :loading="loading"
      :labelOn = "label"
      :disabled="disabled">
    </ui-units-of-measure>
    <br>
    <h4 class = "is-size-5">Original Unit Of Measure</h4>
    <ui-units-of-measure
      v-model = "measure2"
      position="label-center"
      magnitudeType = "Length"
      :data="Units"
      :rounded="rounded"
      :loading="loading"
      :labelOn = "label"
      :disabled="disabled">
    </ui-units-of-measure>
    <br>
    <h4 class = "is-size-5">Unit Of Measure min, max and step</h4>
    <ui-units-of-measure
      v-model = "measure3"
      position="label-right"
      magnitudeType = "Length"
      :data="Units"
      :rounded="rounded"
      :loading="loading"
      :labelOn = "label"
      :disabled="disabled"
      :min = "0"
      :max = "20"
      :step = "5">
    </ui-units-of-measure>
    <br>
    <h4 class = "is-size-5">Unit Of Measure, unit system SI</h4>
    <ui-units-of-measure
      position="is-horizontal"
      v-model = "measure4"
      magnitudeType = "Length"
      unitSystem = "SI"
      :data="Units"
      :rounded="rounded"
      :loading="loading"
      :labelOn = "label"
      :disabled="disabled">
    </ui-units-of-measure>
      <hr>
      <div class="positionRight">
        <b-tooltip
        label="Changelog"
        :active="true"
        position="is-left"
        type="is-dark">
          <ui-panel-container
            :panelHeight= "150"
            :panelWidth= "600"
             unitHeight="px"
             unitWidth="px"
            backgroundColor="white"
            panelBorder="ridge"
            panelBorderColor="black"
            :collapseButtonEnabled="true"
            collapseButtonPosition="left"
            collapseButtonBackgroundColor="#C2C2C2"
            :style="collapseLeft"
            @on-collapse="collapsePanelLeft">
              <markdown></markdown>
            </ui-panel-container>
        </b-tooltip>
      </div>
  </div>
</template>

<script>

import UIUnitOfMeasure from '../../../components/inputs/UIUnitsOfMeasure/index'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import titleComponent from '../../common/titleComponent'
import { updatesInput } from '../../../libraryUpdates.js'
import readme from '../../../components/inputs/UIUnitsOfMeasure/changelog.md'

export default {
  name: 'UIUnitOfMeasurePage',
  components: {
    'UiUnitsOfMeasure': UIUnitOfMeasure,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },
  data () {
    return {
      /**
       * Version data
       */
      titlePage: updatesInput.UnitOfMeasure,
      collapseLeft: {},
      /**
       * Configurations
       */
      label: true,
      rounded: false,
      loading: false,
      disabled: false,
      measure: {
        drillTd: 750,
        drillTdUom: null
      },
      measure2: {
        baseDepth: null,
        baseDepthUom: null,
        baseDepthOuom: null
      },
      measure3: {
        maxTvd: null,
        maxTvdUom: null
      },
      measure4: {
        topDepth: null,
        topDepthUom: null,
        topDepthOuom: null
      },
      Units: [
        {uomId: 'Km', uomSystemId: 'SI', uomQuantityType: 'Length'},
        {uomId: 'm', uomSystemId: 'SI', uomQuantityType: 'Length'},
        {uomId: 'mm', uomSystemId: 'SI', uomQuantityType: 'Length'},
        {uomId: 'yd', uomSystemId: 'EN', uomQuantityType: 'Length'},
        {uomId: 'ft', uomSystemId: 'SI', uomQuantityType: 'Length'},
        {uomId: 'Kg', uomSystemId: 'SI', uomQuantityType: 'Mass'}
      ]
    }
  },
  computed: {
    onOffLabel () {
      return this.label ? 'Label On' : 'Label Off'
    },
    onOffRounded () {
      return this.rounded ? 'Rounded On' : 'Rounded Off'
    },
    onOffLoading () {
      return this.loading ? 'Loading On' : 'Loading Off'
    },
    onOffDisabled () {
      return this.disabled ? 'Disable On' : 'Disabled Off'
    }
  },
  methods: {
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>

<style>
.positionRight {
  display: flex;
  justify-content: flex-end;
}
</style>



