<template>
    <div class="level">
      <div class="level-left">
        <div class="level-item">
          <h1 :class="['titleComponent', titleClass]">{{element.name}}</h1>
          <div class="tags has-addons is-small">
            <span class="tag is-small has-text-weight-bold">Version</span>
            <span class="tag is-success is-small">{{element.version}}</span>
          </div>
        </div>
        <template v-if="isPage">
          <div class="tags" v-if="element.isUpdated">
            <span class="tag is-info has-text-weight-bold">
            NEW!
            </span>
            <span class="tag backgroundStar">
              <b-icon
                icon="star"
                type="is-warning"
                size="is-medium">
              </b-icon>
            </span>
          </div>
        </template>
      </div>
      
      <div v-if="!isPage">
        <div class="level-right" v-if="element.isUpdated">
          <div class="tags">
            <span class="tag is-info has-text-weight-bold">
            NEW!
            </span>
            <span class="tag backgroundStar">
              <b-icon
                icon="star"
                type="is-warning"
                size="is-medium">
              </b-icon>
            </span>
          </div>
        </div>
        
      </div>
    </div>
</template>
<script>
  export default {
    name: 'titleComponent',

    props: {
      element: {
        type: Object,
        required: true
      },

      titleClass: {
        type: String,
        required: false,
        default: 'is-size-4'
      },

      isPage: {
        type: Boolean,
        required: false,
        default: false
      }
    }
  }
</script>
<style scoped>
  .tags , .tag {
    margin: 0px;
  }
  .titleComponent {
    margin-right: 15px;
  }

  .titleComponent:hover {
    cursor: pointer;
    color: rgba(0, 0, 255, 0.3);
  }

  .backgroundStar {
    background: white;
  }
</style>


