<template>
  <div class="columns">
      <figure 
      class="image">
           <img 
           :src="imagePath" 
           :class="[dynamicClassShapes,dynamicClassShadown]"
           :style="{ width: widthImage + 'px' , height: heightImage + 'px'}"
           @mouseover="hoverImage"
           @mouseout="notHoverImage">
      </figure>
  </div>
</template>

<script>
  /**
   * Image container that set several configurations on the images
   * @vue UIImage.vue
   * @version 0.1.0
   * @since 0.1.0
   * @author Alfredo Garcia [gitlab:@garciareal45]
   * @copyright Karanta, CA. 2018
   */
  export default {
    name: 'UiImage',

    props: {

      /**
       * Property determines the image on the container
       * @type {String}
       */
      imagePath: {
        type: String,
        required: true
      },

      /**
       * Property that adjusts the height of the image in pixels
       * @type {String}
       */
      heightImage: {
        type: Number,
        required: false,
        default: 200
      },

      /**
       * Property that sets the hover capacity on the image
       * @type {Boolean}
       */
      hover: {
        type: Boolean,
        required: false,
        default: false
      },

      /**
       * Property that sets the shadow color of the hover event
       * @type {String}
       */
      hoverShadowColor: {
        type: String,
        required: false,
        default: 'rgba(0,0,0,.35)'
      },

      /**
       * Property sets the image shadow
       * @type {String}
       */
      shadow: {
        type: String,
        required: false,
        default: 'none'
      },

      /**
       * Property sets the shape of the image
       * @type {String}
       */
      shape: {
        type: String,
        required: false,
        default: ''
      },

      /**
       * Property that adjusts the width of the image in pixels
       * @type {String}
       */
      widthImage: {
        type: Number,
        required: false,
        default: 200
      }
    },

    /*
      Computed properties
     */
    computed: {

      /**
       * dynamicClassShadown determines the class about the image shadow
       * @return {String} class about the image shadow
       */
      dynamicClassShadown () {
        return {
          'image-shadow-none': (this.shadow === 'none'),
          'image-shadow': (this.shadow === 'shadow'),
          'image-shadow-small': (this.shadow === 'shadow-small'),
          'image-shadow-large': (this.shadow === 'shadow-large')
        }
      },

      /**
       * dynamicClassShapes determines the class about the image shape
       * @return {String} class about the shape of the image
       */
      dynamicClassShapes () {
        return {
          'image-shape-radius': (this.shape === 'rounded'),
          'image-shape-radius-hover': (this.shape === 'rounded-hover'),
          'image-shape-circle': (this.shape === 'circle'),
          'image-shape-circle-hover': (this.shape === 'circle-hover'),
          'image-shape-thumbnail': (this.shape === 'thumbnail'),
          'image-shape-thumbnail-hover': (this.shape === 'thumbnail-hover')
        }
      }
    },

    methods: {

      /**
       * Determines the shadow color of the image when the mouse is onto it
       * @param {Event}
       * @public
       */
      hoverImage (event) {
        if (event && this.hover) {
          event.target.style.boxShadow = `0 1rem 3rem ${this.hoverShadowColor}`
        }
      },

      /**
       * Desactives the shadow when the mouse is out of the image
       * @public
       */
      notHoverImage () {
        if (this.hover) {
          event.target.style.boxShadow = ''
        }
      }
    }
  }
</script>
<style scoped>
/**
 *  Shadow styles
 */
.image-shadow {
  box-shadow: 0 .5rem 1rem rgba(0,0,0,.30);
}

.image-shadow-none {
  box-shadow: none;
}

.image-shadow-small {
  box-shadow: 0 .250rem .250rem rgba(0,0,0,.30);
}

.image-shadow-large {
  box-shadow: 0 1rem 3rem rgba(0,0,0,.35);
}

/**
 * Image shapes style
 */

.image-shape-radius {
  border-radius: 8px;
}

.image-shape-circle {
  border-radius: 50%;
}

.image-shape-thumbnail {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 5px;
}

</style>


