<template>

   <b-modal 
   :active="modalActivated"
   @update:active="eventClose">
    <div class="modal-card">
      <header 
      class="modal-card-head has-text-right"
      :style="dynamicBackgroundColorHeader">
        <button 
        v-if="closeButtonAppear" 
        class="delete" 
        id="closeModalButton" 
        type="button" 
        @click="eventClose">
        </button>
        <!-- @slot uses this slot title -->
        <slot name="title"></slot>
      </header>
      <section 
      class="modal-card-body"
      :style="dynamicBackgroundColorBody">
      <!-- @slot uses slot body -->
        <slot name="body"></slot>
      </section>
      <footer 
      class="modal-card-foot"
      :style="dynamicBackgroundColorFooter">
      <!-- @slot uses this slot footer-->
        <slot name="footer"></slot>
      </footer>
    </div>   
   </b-modal>
  
</template>

<script>

/**
 * Component that represents a classic modal to show messages. it has the following
 * functions:
 * 1) Sets the background color on the header,body and footer of the modal
 * 2) Sets the visibility of close button on the modal
 * 3) Has the header, body and footer slots
 * @vue UIMessageBox.vue
 * @version 0.1.0
 * @since 0.1.0
 * @author Alfredo Garcia [gitlab:@garciareal45]
 * @copyright Karanta, CA. 2018
 */
export default {
  name: 'UiMessageBox',

  props: {

    /**
     * Property that actives the modal
     * @prop {Boolean} [ModalActived= false]
     */
    modalActivated: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * Property that sets the visibility the close button on the modal
     * @prop {Boolean} [closeButtonAppear= true]
     */
    closeButtonAppear: {
      type: Boolean,
      required: false,
      default: true
    },

    /**
     * Property that sets the background color of the header on the modal
     * @prop {String} [backgroundColorHeader= '#f5f5f5']
     */
    backgroundColorHeader: {
      type: String,
      required: false,
      default: '#f5f5f5'
    },

    /**
     * Property that sets the background color of the body on the modal
     * @prop {String} [backgroundColorContent= '#ffffff']
     */
    backgroundColorBody: {
      type: String,
      required: false,
      default: '#ffffff'
    },

    /**
     * Property that sets the background color of the footer on the modal
     * @prop {String} [backgroundColorFooter= '#f5f5f5']
     */
    backgroundColorFooter: {
      type: String,
      required: false,
      default: '#f5f5f5'
    }

  },

  data () {
    return {
      active: false
    }
  },

  computed: {

    /**
     * Computed property that sets the background color of the header on the modal
     * @return {Object}
     */
    dynamicBackgroundColorHeader () {
      return {
        'background-color': this.backgroundColorHeader
      }
    },

    /**
     * Computed property that sets the background color of the body on the modal
     * @return {Object}
     */
    dynamicBackgroundColorBody () {
      return {
        'background-color': this.backgroundColorBody
      }
    },

    /**
     * Computed property that sets the background color of the footer on the modal
     * @return {Object}
     */
    dynamicBackgroundColorFooter () {
      return {
        'background-color': this.backgroundColorFooter
      }
    }
  },

  methods: {
    /**
     * Method that changes the value of the modalActivated prop to negation
     * @return {Event}
     * @public
     */
    eventClose () {
      /**
       * Event that updates the value of the modalActivated prop
       * @event update:modalActivated
       * @type {Boolean}
       */
      this.$emit('update:modalActivated', !this.modalActivated)
    }

  }
}
</script>

<style scoped>
  .modal-card-head {
    display: block;
  }

  .modal-card-foot {
    display: block;
  }
</style>


