import Home from '../components/Home'
import Common from '../pages/common/Common'
import UIBreadcrumbPage from '../pages/common/UIBreadcrumb/UIBreadcrumbPage'
import GenericTabulatedDataPage from '../pages/common/generic-tabulated-data/GenericTabulatedDataPage'
import Metadata from '../pages/metadata/Metadata'
import UISearhSelectPage from '../pages/common/UISearchSelect/UISearchSelectPage'
import Inputs from '../pages/inputs/Inputs'
import UIUnitsOfMeasurePage from '../pages/inputs/UIUnitsOfMeasure/UIUnitsOfMeasurePage'
import UIManagementPanelFiltersPage from '../pages/common/UIManagementPanelFilters/UIManagementPanelFiltersPage'
import UIManagementPanelShowHideColumnsPage from '../pages/common/UIManagementPanelShowHideColumns/UIManagementPanelShowHideColumnsPage'
import UIManagementPanelPreviewPage from '../pages/common/UIManagementPanelPreview/UIManagementPanelPreviewPage'
import UIGenericSearchPage from '../pages/common/UIGenericSearch/UIGenericSearchPage'
import UIButton from '../pages/common/UIButton/UIButtonPage'
import UIDynamicPanelPage from '../pages/common/UIDynamicPanel/UIDynamicPanelPage'
import UIManagementPanelEditionPage from '../pages/common/UIManagementPanelEdition/UIManagementPanelEditionPage'
import UIContainer from '../pages/common/UIContainer/UIContainerPage'
import UIToolBar from '../pages/common/UIToolBar/UIToolBarPage'
import UIMapPage from '../pages/common/UIMap/UIMapPage'
import UIMenuPage from '../pages/common/UIMenu/UIMenuPage'
import UINavigationTreePage from '../pages/common/UINavigationTree/UINavigationTreePage'
import UIImage from '../pages/common/UIImage/UIImagePage'
import UIContainerMenuPage from '../pages/common/UIContainerMenu/UIContainerMenuPage'
import UITitle from '../pages/common/UITitle/UITitlePage'
import UIPanelContainer from '../pages/common/UIPanelContainer/UIPanelContainerPage'
import UIFrame from '../pages/common/UIFrame/UIFramePage'
import UIWizard from '../pages/common/UIWizard/UIWizardPage'
import UIMessageBox from '../pages/common/UIMessageBox/UIMessageBoxPage'
import UITransfer from '../pages/common/UITransfer/UITransferPage'
import UIChart from '../pages/common/UIChart/UIChartPage'
import UILogo from '../pages/common/UILogo/UILogoPage'
import UIUploadFilePage from '../pages/common/UIUploadFile/UIUploadFilePage'
const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/common',
    components: {
      default: Common
    },
    children: [
      {
        path: '/UIBreadcrumb',
        name: 'UIBreadcrumb',
        component: UIBreadcrumbPage
      },
      {
        path: '/UIGenericTabulatedData',
        name: 'UIGenericTabulatedData',
        component: GenericTabulatedDataPage
      },
      {
        path: '/UIManagementPanelFilters',
        name: 'UIManagementPanelFilters',
        component: UIManagementPanelFiltersPage
      },
      {
        path: '/UIManagementPanelShowHideColumns',
        name: 'UIManagementPanelShowHideColumns',
        component: UIManagementPanelShowHideColumnsPage
      },
      {
        path: '/UIManagementPanelEdition',
        name: 'UIManagementPanelEdition',
        component: UIManagementPanelEditionPage
      },
      {
        path: '/UIManagementPanelPreview',
        name: 'UIManagementPanelPreview',
        component: UIManagementPanelPreviewPage
      },
      {
        path: '/UIGenericSearch',
        name: 'UIGenericSearch',
        component: UIGenericSearchPage
      },
      {
        path: '/UISearchSelect',
        name: 'UISearchSelect',
        component: UISearhSelectPage
      },
      {
        path: '/UIButton',
        name: 'UIButton',
        component: UIButton
      },
      {
        path: '/UIDynamicPanel',
        name: 'UIDynamicPanel',
        component: UIDynamicPanelPage
      },
      {
        path: '/UIContainer',
        name: 'UIContainer',
        component: UIContainer
      },
      {
        path: '/UIToolBar',
        name: 'UIToolBar',
        component: UIToolBar
      },
      {
        path: '/UIMap',
        name: 'UIMap',
        component: UIMapPage
      },
      {
        path: '/UIMenu',
        name: 'UIMenu',
        component: UIMenuPage
      },
      {
        path: '/UINavigationTree',
        name: 'UINavigationTree',
        component: UINavigationTreePage
      },
      {
        path: '/UIImage',
        name: 'UIImage',
        component: UIImage
      },
      {
        path: '/UIContainerMenu',
        name: 'UIContainerMenu',
        component: UIContainerMenuPage
      },
      {
        path: '/UITitle',
        name: 'UITitle',
        component: UITitle
      },
      {
        path: '/UIPanelContainer',
        name: 'UIPanelContainer',
        component: UIPanelContainer
      },
      {
        path: 'UIFrame',
        name: 'UIFrame',
        component: UIFrame
      },
      {
        path: 'UIMessageBox',
        name: 'UIMessageBox',
        component: UIMessageBox
      },
      {
        path: '/UIWizard',
        name: 'UIWizard',
        component: UIWizard
      },
      {
        path: '/UITransfer',
        name: 'UITransfer',
        component: UITransfer
      },
      {
        path: '/UIChart',
        name: 'UIChart',
        component: UIChart
      },
      {
        path: '/UILogo',
        name: 'UILogo',
        component: UILogo
      },
      {
        path: '/UIUploadFile',
        name: 'UIUploadFile',
        component: UIUploadFilePage
      }
    ]
  },
  {
    path: '/metadata',
    components: {
      default: Metadata
    },
    children: []
  },
  {
    path: '/Inputs',
    components: {
      default: Inputs
    },
    children: [
      {
        path: '/UIUnitsOfMeasure',
        name: 'UIUnitsOfMeasure',
        component: UIUnitsOfMeasurePage
      }
    ]
  },
  {
    path: '*',
    redirect: '/'
  }
]

export default routes



