<template>
  <div class="columns">
  
    <article 
    :class="[{'message':true},dynamicClassColorHeader]">
    <div 
    v-if="withoutHeader" 
    :class="dynamicClassFrameType">

      <ui-title 
      :color="configurationTitle.color"
      :weight="configurationTitle.weight"
      :align="configurationTitle.align"
      :size="configurationTitle.size"
      :transformation="configurationTitle.transformation">
        <!-- @slot Use to add title -->
        <slot name="title"></slot>
      </ui-title>
    
    </div>
    <div 
    :style="dynamicContentColor" 
    :class="[{'message-body':true},dynamicClassAlignContent]">
      <!-- @slot Use to add content -->
      <slot name="content"></slot>
    </div>
  </article>
  </div>
</template>
<script>
 import UITitle from '../../../components/common/UITitle/index.js'
/**
 * Component that sets up a frame on a information.
 * This component has the following functions:
 * 1) Align the information title
 * 2) Align the information content
 * 3) Set up header type on the information
 * 4) Set up the class color helpers of the title text
 * 5) Set up the color of the content text
 * 6) Transform the title with the use of four text transformation helpers
 * @vue UIFrame.vue
 * @version 0.1.0
 * @since 0.1.0
 * @author Alfredo Garcia [gitlab:@garciareal45]
 * @copyright Karanta, CA. 2018
 */
 export default {

   name: 'UiFrame',

   components: {
     'UiTitle': UITitle
   },

   props: {

     /**
      * Property that sets the title configuration of the UIFrame
      * @prop {Object}
      */
     configurationTitle: {
       type: Object,
       required: false,
       default: function () {
         return {
           title: 'add',
           weight: 'normal',
           align: 'center',
           size: 3,
           color: 'black',
           transformation: 'capitalized'
         }
       }
     },

      /**
       * Property that sets the content align
       * @prop {String} [contentAlign = 'justified']
       */
     contentAlign: {
       type: String,
       required: false,
       default: 'justified'
     },

     /**
      * Property that sets the content color of the ui frame
      * @prop {String} [contentColor = '#070707']
      */
     contentColor: {
       type: String,
       required: false,
       default: '#070707'
     },

      /**
       * Property that sets the header type on the component information
       * @prop {String} [headerType= 'header-on']
       */
     headerType: {
       type: String,
       required: false,
       default: 'header-on'
     },

      /**
       * Property that indicates if the header appears
       * @prop {Boolean} [withoutHeader = true]
       */
     withoutHeader: {
       type: Boolean,
       required: false,
       default: true
     },

      /**
       * Property that determines the color class header
       * @prop {String} [headerColor= 'info']
       */
     headerColor: {
       type: String,
       required: false,
       default: 'info'
     }

   },

   computed: {

      /**
       * Computed property that determines the type frame class
       * @return {Object}
       */
     dynamicClassFrameType () {
       return {
         'message-header': (this.headerType === 'header-on'),
         'message-header-off': (this.headerType === 'header-off')
       }
     },

      /**
       * Computed property that determines the color class header
       * @return {String}
       */
     dynamicClassColorHeader () {
       let colors = [
         'dark',
         'info',
         'primary',
         'success',
         'link',
         'warning',
         'danger'
       ]

       if (colors.indexOf(this.headerColor.toLowerCase()) === -1) {
         console.error('Wrong color')
         return 'is-dark'
       }

       return 'is-' + this.headerColor.toLowerCase()
     },

      /**
       * Computed property that determines the align class content
       * @return {String}
       */
     dynamicClassAlignContent () {
       let alignments = [
         'centered',
         'right',
         'left',
         'justified'
       ]

       if (alignments.indexOf(this.contentAlign.toLowerCase()) === -1) {
         console.error('Wrong alignment')
         return 'has-text-justified'
       }

       return 'has-text-' + this.contentAlign.toLowerCase()
     },

     /**
      * Computed property that determines the content color of the <ui-frame>
      * @return {Object}
      */
     dynamicContentColor () {
       return {
         color: this.contentColor
       }
     }
   }

 }
</script>

<style scoped>
  .message-header {
    display: block;
  }

  .message {
    background-color: rgba(255,255,255,1)important!;
  }
</style>


