<template>
  <div>
   <title-component 
   :element="titlePage"
   titleClass="is-size-2"
   :isPage="true">
   </title-component>
    <hr>
    <div class="columns">
      <div class="column">
        <b-switch v-model="props.isOpen">{{openCloseLabel}}</b-switch>
      </div>
      <div class="column">
        <b-switch v-model="props.isHorizontal">Horizontal</b-switch>
      </div>
      <div class="column">
        <label>Header Size</label>
        <b-select
        v-model="headerSize"
        placeholder="Header Size">
        <option
        v-for="size in 7"
        :value="size">{{size}}</option>
      </b-select>
      </div>
      <div class="column">
        <label>Header Position</label>
        <b-select
        v-model="headerPosition"
        placeholder="Header Position">
        <option
        v-for="position in ['right', 'centered', 'left', 'justified']"
        :value="position">{{position}}</option>
      </b-select>
      </div>
      <div class="column">
        <label>Header Style</label>
        <b-select
        v-model="headerStyle"
        placeholder="Header Style">
        <option
        v-for="style in ['capitalized', 'uppercase', 'italic', 'lowercase']"
        :value="style">{{style}}</option>
      </b-select>
      </div>
      <div class="column">
        <label>Toolbar Position</label>
        <b-select
        v-model="props.toolbarPosition"
        placeholder="Toolbar Position">
        <option
        v-for="position in ['right', 'center', 'left', 'none']"
        :value="position">{{position}}</option>
      </b-select>
      </div>
      <div class="column">
        <label>Toolbar Button Size</label>
        <b-select
        v-model="props.toolbarButtonConfigurations[0].buttonSize"
        placeholder="Toolbar Button Size">
        <option
        v-for="size in ['is-small', 'is-medium', 'is-large']"
        :value="size">{{size}}</option>
      </b-select>
      </div>
      <div class="column">
        <label>Toolbar Button Style</label>
        <b-select
        v-model="props.toolbarButtonConfigurations[0].buttonClass"
        placeholder="Toolbar Button Class">
        <option
        v-for="style in ['is-primary', 'is-info', 'is-warning', 'is-danger']"
        :value="style">{{style}}</option>
      </b-select>
      </div>
      <div class="column">
        <b-switch v-model="props.toolbarButtonConfigurations[0].visibleIcon">{{visibleIconLabel}}</b-switch>
      </div>
      <div class="column">
        <b-switch v-model="props.toolbarButtonConfigurations[0].isRounded">{{roundedLabel}}</b-switch>
      </div>
    </div>
    <ui-management-panel-filters
      v-model="config"
      v-bind="propsComputed">
    </ui-management-panel-filters>
    <hr>
      <div class="positionRight">
        <b-tooltip
        label="Changelog"
        :active="true"
        position="is-left"
        type="is-dark">
          <ui-panel-container
            :panelHeight= "150"
            :panelWidth= "600"
             unitHeight="px"
             unitWidth="px"
            backgroundColor="white"
            panelBorder="ridge"
            panelBorderColor="black"
            :collapseButtonEnabled="true"
            collapseButtonPosition="left"
            collapseButtonBackgroundColor="#C2C2C2"
            :style="collapseLeft"
            @on-collapse="collapsePanelLeft">
              <markdown></markdown>
            </ui-panel-container>
        </b-tooltip>
      </div>
</div>
</template>

<script>
import UIManagementPanelFilters from '../../../components/common/UIManagementPanelFilters/index.js'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import titleComponent from '../titleComponent.vue'
import { updates } from '../../../libraryUpdates.js'
import readme from '../../../components/common/UIManagementPanelFilters/changelog.md'

export default {
  name: 'UIManagementPanelFiltersPage',

  components: {
    UiManagementPanelFilters: UIManagementPanelFilters,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },

  data () {
    return {
      titlePage: updates.ManagementPanelFilters,
      collapseLeft: {},
      headerSize: 3,
      headerPosition: 'centered',
      headerStyle: 'capitalized',
      /**
       * Config struture for the entities used in v-model
       * @type {Object}
       */
      config: [
        {
          classTag: 'class1',
          children: [
            {
              propTag: 'area',
              filters: {
                filterable: true,
                type: 'object',
                group: ['areaId', 'areaType'],
                visibleLabel: true,
                component: {
                  name: 'ui-search-select',
                  defaultValue: {'areaId': '1', areaType: 'Country'},
                  props: {
                    entity: 'AREA',
                    position: 'label-left',
                    labelOn: false,
                    optionIdentifier: 'areaId',
                    paramsToSee: ['areaId', 'areaType', 'preferredName'],
                    paramsToGet: ['areaId', 'areaType'],
                    data: [
                      {areaId: '1', areaType: 'County', preferredName: 'Puerto La Cruz'},
                      {areaId: '2', areaType: 'Country', preferredName: 'Puerto La Cruz'},
                      {areaId: '3', areaType: 'State', preferredName: 'Puerto La Cruz'},
                      {areaId: '4', areaType: 'County', preferredName: 'Puerto La Cruz'}
                    ]
                  },
                  attrs: {
                    // some html atributes
                  }
                }
              }
            },
            {
              propTag: 'id.areaId',
              filters: {
                filterable: false,
                type: 'ID'
              }
            },
            {
              propTag: 'id.areaType',
              filters: {
                filterable: false,
                type: 'ID'
              }
            },
            {
              propTag: 'activeInd',
              filters: {
                filterable: true,
                visibleLabel: true,
                type: 'String',
                component: {
                  name: 'b-input',
                  defaultValue: null,
                  props: {
                    type: 'text'
                  },
                  attrs: {}
                }
              }
            }
          ]
        },
        {
          classTag: 'class2',
          children: [
            {
              propTag: 'preferredName',
              filters: {
                filterable: true,
                type: 'String',
                visibleLabel: true,
                component: {
                  name: 'b-input',
                  defaultValue: null,
                  props: {
                    type: 'text'
                  },
                  attrs: {}
                }
              }
            },
            {
              propTag: 'effectiveDate',
              filters: {
                filterable: true,
                type: 'date',
                visibleLabel: true,
                component: {
                  name: 'b-datepicker',
                  defaultValue: null,
                  props: {
                    icon: 'fa fa-calendar-alt',
                    disabled: false
                  }
                }
              }
            },
            {
              propTag: 'expiryDate',
              filters: {
                filterable: true,
                type: 'date',
                visibleLabel: true,
                component: {
                  name: 'b-datepicker',
                  defaultValue: null,
                  props: {
                    icon: 'fa fa-calendar-alt',
                    disabled: false
                  }
                }
              }
            }
          ]
        }
      ],

      /**
       * And object with the properties of the component, to make the html
       * looks more clean
       * @type {Object}
       */
      props: {
        entity: 'AREA',
        icon: 'fa fa-chevron-down',
        isOpen: true,
        isHorizontal: false,
        toolbarButtonConfigurations: [
          {
            buttonType: 'restore',
            visibleLabel: true,
            buttonClass: 'is-primary',
            iconType: 'fa fa-sync-alt',
            buttonSize: 'is-medium',
            visibleIcon: true,
            isRounded: false,
            visibleToolTip: true,
            positionToolTip: 'is-top',
            typeToolTip: 'is-info',
            sizeToolTip: 'is-medium',
            multilinedToolTip: true
          }
        ],
        toolbarConfiguration: {
          dropDownAppear: false,
          gapContentToolBar: 'is-1'
        },
        toolbarPosition: 'center'
      }
    }
  },
  computed: {
    propsComputed () {
      this.props['headerClass'] = `is-size-${this.headerSize} has-text-${this.headerPosition} is-${this.headerStyle}`
      return this.props
    },
    openCloseLabel () {
      return (this.props.isOpen) ? 'Open' : 'Close'
    },
    roundedLabel () {
      return (this.props.toolbarButtonConfigurations[0].isRounded) ? 'Button Rounded' : ' Button Not Rounded'
    },
    visibleIconLabel () {
      return (this.props.toolbarButtonConfigurations[0].visibleIcon) ? 'Button Icon Visible' : ' Button Icon Not Visible'
    }
  },
  methods: {
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>

<style lang="css" scoped>
.positionRight {
  display: flex;
  justify-content: flex-end;
}
</style>



