<template>
	<div>
  <title-component 
   :element="titlePage"
   titleClass="is-size-2"
   :isPage="true">
  </title-component>
  <hr>
    <div class="columns">
      <div class="column">
        <b-field
          horizontal
          label="Collapse"
        >
          <b-switch
            v-model="collapse"
            type="is-info"
          ></b-switch>
        </b-field>
        <b-field
          horizontal
          label="Unique Opened"
        >
          <b-switch
            v-model="uniqueOpened"
            type="is-info"
          ></b-switch>
        </b-field>
        <b-field
          horizontal
          label="Router"
        >
          <b-switch
            v-model="router"
            type="is-info"
          ></b-switch>
        </b-field>
      </div>
      <div class="column">
        <b-field
          horizontal
          label="Item Selected"
        >
          <b-select v-model="defaultActive">
            <option value="nav-1">Adminstrador de Pozos</option>
            <option value="nav-1-1">Pozos</option>
            <option value="nav-1-2">Campo</option>
            <option value="nav-1-3">Yacimiento</option>
            <option value="nav-2">Prueba de Pozos</option>
            <option value="nav-2-1">Pruebas de Produccion</option>
            <option value="nav-3">Contabilidad de Pozos</option>
            <option value="nav-4">Administrador de Instalaciones</option>
          </b-select>
        </b-field>
        <b-field
          horizontal
          label="Menu Trigger"
        >
          <b-select v-model="menuTriger">
            <option value="click">Click</option>
            <option value="hover">Hover</option>
          </b-select>
        </b-field>
      </div>
      <div class="column">
        <b-field
          horizontal
          label="Background Color"
        >
          <b-input
            type="color"
            v-model="backgroundColor"
          >
          </b-input>
        </b-field>
        <b-field
          horizontal
          label="Text Color"
        >
          <b-input
            type="color"
            v-model="textColor"
          >
          </b-input>
        </b-field>
        <b-field
          horizontal
          label="Active Text"
        >
          <b-input
            type="color"
            v-model="activeTextColor"
          >
          </b-input>
        </b-field>
      </div>
    </div>
    <ui-menu
      :active-text-color="activeTextColor"
      :background-color="backgroundColor"
      :default-active="defaultActive"
      :menu-items="menuItems"
      :menu-trigger="menuTriger"
      mode="horizontal"
      :router="router"
      :text-color="textColor"
      @select="indexSelected"
    ></ui-menu>
    <br>
    <ui-menu
      :active-text-color="activeTextColor"
      class="el-menu-vertical-demo"
      :background-color="backgroundColor"
      :collapse="collapse"
      :default-active="defaultActive"
      :menu-items="menuItems"
      mode="vertical"
      :router="router"
      :text-color="textColor"
      :unique-opened="uniqueOpened"
      @select="indexSelected"
    ></ui-menu>
      <hr>
      <div class="positionRight">
        <b-tooltip
        label="Changelog"
        :active="true"
        position="is-left"
        type="is-dark">
          <ui-panel-container
            :panelHeight= "150"
            :panelWidth= "600"
             unitHeight="px"
             unitWidth="px"
            backgroundColor="white"
            panelBorder="ridge"
            panelBorderColor="black"
            :collapseButtonEnabled="true"
            collapseButtonPosition="left"
            collapseButtonBackgroundColor="#C2C2C2"
            :style="collapseLeft"
            @on-collapse="collapsePanelLeft">
              <markdown></markdown>
            </ui-panel-container>
        </b-tooltip>
      </div>
	</div>
</template>

<script>
import UiMenu from '../../../components/common/UIMenu'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import titleComponent from '../titleComponent.vue'
import { updates } from '../../../libraryUpdates.js'
import readme from '../../../components/common/UIMenu/changelog.md'

export default {
  name: 'UIMenuPage',
  components: {
    UiMenu,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },
  data () {
    return {
      /**
       * Version data
       */
      titlePage: updates.Menu,
      collapseLeft: {},
      menuItems: [
        {
          label: 'wellManager',
          icon: 'far fa-check-circle',
          props: {
            index: 'nav-1',
            class: 'selected-element'
          },
          children: [
            {
              label: 'well',
              icon: 'far fa-check-circle',
              props: {
                index: 'nav-1-1'
              }
            },
            {
              label: 'field',
              icon: 'far fa-check-circle',
              props: {
                index: 'nav-1-2'
              }
            },
            {
              label: 'pool',
              icon: 'far fa-check-circle',
              props: {
                index: 'nav-1-3',
                disabled: true
              }
            }
          ]
        },
        {
          label: 'wellTest',
          icon: 'far fa-check-circle',
          props: {
            index: 'nav-2'
          },
          children: [
            {
              label: 'productionTest',
              icon: 'far fa-check-circle',
              props: {
                index: 'nav-2-1'
              }
            }
          ]
        },
        {
          label: 'wellAccounting',
          icon: 'far fa-check-circle',
          props: {
            index: 'nav-3'
          }
        },
        {
          label: 'facilityManager',
          icon: 'far fa-check-circle',
          props: {
            index: 'nav-4',
            disabled: true
          }
        }
      ],
      activeTextColor: '#ffd04b',
      backgroundColor: '#545c64',
      collapse: false,
      defaultActive: 'nav-3',
      menuTriger: 'hover',
      mode: 'horizontal',
      router: false,
      textColor: '#ffffff',
      uniqueOpened: true
    }
  },
  methods: {
    indexSelected (index) {
      this.defaultActive = index
      console.log(index)
    },
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>

<style scoped>
  .el-menu-vertical-demo {
    height: 400px;    
  }
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 300px;
    min-height: 400px;
    height: 400px;        
  }
  .positionRight {
  display: flex;
  justify-content: flex-end;
  }
</style>



