<template>
  <div>
    <b-autocomplete
      v-if="autocomplete"
      v-model="query"
      :icon = "icon"
      :data="componentData"
      :placeholder="translateMessage('search')"
      :keep-first="keepFirst"
      :loading="loadingSearch"
      :field="identifiers[0]"
      @input="queryChanged">
      <template slot="empty">
        {{ $t('messages.noResults.label') }}
      </template>
      <template slot-scope="props">
        <div class="media-content">
          <span
            v-for="(show, key) in paramsToSee"
            :key="key">
            {{props.option[show] }}
          </span>
        </div>
      </template>
    </b-autocomplete>
    <b-input
      v-else
      v-model="query"
      :icon = "icon"
      :placeholder="translateMessage('search')"
      @input="queryChanged">
    </b-input>
  </div>
</template>

<script>
import debounce from 'lodash/debounce'
import {services} from '../../../services/UIGenericSearch'
/**
 * Search input to reload other component. Have the following functions:
 *  * Can be autocomplete
 *  * Send the query to parent after the typing
 * @vue UIGenericTabulatedData.vue
 * @version 0.1.0
 * @since 0.1.0
 * @author Cruz Ortiz [gitlab:@cortiz99]
 * @copyright Karanta, CA. 2018
 */
export default {
  name: 'UiGenericSearch',

  model: {
    prop: 'value',
    event: 'on-search'
  },

  props: {
    /**
    * Implement autocomplete in the search
    * @prop {Boolean} [autocomplete = false]
    */
    autocomplete: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * Entity's name where the search is performed
     * @prop {String} entity
     */
    entity: {
      type: String,
      required: true
    },

    /**
     * Represents the icon used on the input
     * @prop {String} icon
     */
    icon: {
      type: String,
      required: false,
      default: 'fa fa-search'
    },

    /**
    * Use to show the options
    * @prop {String} identifier
    */
    identifiers: {
      type: Array,
      required: true
    },

    /**
     * Preselect the first options
     * @prop {Boolean} [keepFirst = false]
     */
    keepFirst: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * Property allows to establish the parameters will be shown in the preview
     * of the search
     * @prop {Array} paramsToSee
     */
    paramsToSee: {
      type: Array,
      required: true
    },

    /**
     * Used to be able to use v-model
     * @prop {String} [value = '']
     */
    value: {
      type: String,
      required: false,
      default: ''
    }
  },

  data () {
    return {
      componentData: [],
      query: '',
      loadingSearch: false
    }
  },

  created () {
    this.query = this.value
  },

  methods: {
    /**
    * Makes the petition width a query
    * @param  {String} query  Represent the query
    * @param  {String} entity Represent the entity or model for the
    * component
    * @public
    */
    getData (query, entity) {
      this.loadingSearch = true
      this.componentData = []
      services[entity](query)
      .then((content) => {
        let self = this
        this.componentData = content.data.map((itemsList) => {
          let dataStrucutre = itemsList
          for (let item in self.identifiers) {
            if (itemsList.id && itemsList.id[self.identifiers[item]]) {
              itemsList[self.identifiers[item]] = `${itemsList.id[self.identifiers[item]]}`
            }
          }
          return dataStrucutre
        })
        this.loadingSearch = false
      })
      .catch((error) => {
        console.log(error)
      })
    },

    /**
     * Makes the request to the API and stores the data in
     * the 'componentData' array.
     * @public
     */
    getDataQuery: debounce(function (query, entity) {
      this.getData(query, entity)
    }, 500),

    /**
     * Emits the event to send the query to the parent
     * @param {String} query Represent a query sent in the even
     * @public
     */
    eventEmited: debounce(function (query) {
      /**
       * Represent a query sent in the even
       * @event on-search
       * @type {String}
       */
      this.$emit('on-search', query)
    },
    500),

    /**
     * Handles the respective procedure when input event occur
     * @param  {String} query Represent a query to the petition
     * @public
     */
    queryChanged (query) {
      if (this.autocomplete) {
        this.getDataQuery(query, this.entity)
      }
      this.eventEmited(query)
    },

    /**
     * Translates the received parameter
     * @returns {String}
     * @public
     **/
    translateMessage (value) {
      return this.$i18n.t(`template.${value}.label`)
    }
  }
}
</script>

<style scoped>

</style>



