<template>
  <div>
    <title-component :element="titlePage"
      titleClass="is-size-2"
      :isPage="true">
    </title-component>
    <hr>
    <!-- First row -->
    <div class="columns">
      <div class="column is-1 is-offset-3">
        <b-field label="Height">
          <b-input type="number"
            :value="panelHeight"
            @input="(event) => {panelHeight= +event}"
            rounded>
          </b-input>
        </b-field>
      </div>
      <div class="column is-1">
        <b-field label="Unit of Height">
          <b-select placeholder="Introduce the unit of height"
            v-model="unitHeight"
            rounded>
            <option v-for="(unit,key) in unitsH"
              :key="key">
              {{unit}}
            </option>
          </b-select>
        </b-field>
      </div>
      <div class="column is-1">
        <b-field label="Width">
          <b-input type="number"
            :value="panelWidth"
            @input="(event) => {panelWidth= +event}"
            rounded>
          </b-input>
        </b-field>
      </div>
      <div class="column is-1">
        <b-field label="Unit of Width">
          <b-select placeholder="Introduce the unit of widht"
            v-model="unitWidth"
            rounded>
            <option v-for="(unit,key) in unitsW"
              :key="key">
              {{unit}}
            </option>
          </b-select>
        </b-field>
      </div>
      <div class="column is-2">
        <b-field label="Background Color">
          <b-input type="color"
            v-model="backgroundColor"
            rounded>
          </b-input>
        </b-field>
      </div>
    </div>
    <!-- Panel container without collaspe button -->
    <div style="height: 70vh">
      <ui-panel-container :panelHeight="panelHeight"
        :panelWidth="panelWidth"
        :unitHeight="unitHeight"
        :unitWidth="unitWidth"
        :panelBorder="panelBorder"
        :backgroundColor="backgroundColor"
        :panelBorderColor="colorBorder">
        <p class="is-size-1 has-text-centered">
          UI Panel Container
        </p>
      </ui-panel-container>
    </div>
    <!-- Panel containers with collapse buttons -->
    <hr>
    <div>
      <h3 class="is-size-3">UI Panel Container</h3>
      <hr>
      <div class="columns">
        <div class="column is-2">
          <b-field label="Collapse button enabled">
            <b-switch v-model="collapseButtonEnabled"
              rounded>
            </b-switch>
          </b-field>
        </div>
        <div class="column is-1">
          <b-field label="Initial panel state">
            <b-switch v-model="isCollapsed"
              rounded>
            </b-switch>
          </b-field>
        </div>
        <div class="column is-2">
          <b-field label="Icon orientation - left">
            <b-switch v-model="isCollapsePanelLeft"
              rounded>
            </b-switch>
          </b-field>
        </div>
        <div class="column is-2">
          <b-field label="Icon orientation  - right">
            <b-switch v-model="isCollapsePanelRight"
              rounded>
            </b-switch>
          </b-field>
        </div>
        <div class="column is-1">
          <b-field label="Icon size">
            <b-select v-model="iconSize"
              rounded>
              <option v-for="(item,key) in dynamiciconSizes"
                :key="key">
                {{item}}
              </option>
            </b-select>
          </b-field>
        </div>
        <div class="column is-2">
          <b-field label="Icon class color">
            <b-select v-model="iconClassColor"
              rounded>
              <option v-for="(item,key) in dynamicIconClassColor"
                :key="key">
                {{item}}
              </option>
            </b-select>
          </b-field>
        </div>
        <div class="column is-1">
          <b-field label="cursor class">
            <b-select v-model="cursorClass"
              rounded>
              <option v-for="(item,key) in dynamicCursorClass"
                :key="key">
                {{item}}
              </option>
            </b-select>
          </b-field>
        </div>
        <div class="column is-2">
          <b-field label="Collapse button background Color">
            <b-input type="color"
              v-model="collapseButtonBackgroundColor"
              rounded>
            </b-input>
          </b-field>
        </div>
        <div class="column is-2">
          <b-field label="Mouse Over-Out Color">
            <b-input type="color"
              v-model="collapseMouseOverColor"
              rounded>
            </b-input>
          </b-field>
        </div>
      </div>
    </div>
    <div style="height: 70vh">
      <div class="columns">
        <div class="column">
          <ui-panel-container :panelHeight="panelHeight1"
            :panelWidth="panelWidth1"
            :unitHeight="unit"
            :unitWidth="unit"
            :panelBorder="panelBorder"
            :backgroundColor="backgroundColor"
            :panelBorderColor="colorBorder"
            :collapseButtonEnabled="collapseButtonEnabled"
            :collapseButtonPosition="collapseButtonPosition1"
            :collapseButtonBackgroundColor="collapseButtonBackgroundColor"
            :collapseMouseOverColor="collapseMouseOverColor"
            :iconClassColor="iconClassColor"
            :isCollapsed="isCollapsed"
            :iconSize="iconSize"
            :cursorClass="cursorClass"
            :style="collapseRight"
            :icon-orientation.sync="isCollapsePanelLeft"
            @on-collapse="collapsePanelRight">
            <p class="is-size-1 has-text-centered">
              UI Panel Container
            </p>
          </ui-panel-container>
        </div>
        <div class="column positionRight">
          <ui-panel-container :panelHeight="panelHeight1"
            :panelWidth="panelWidth1"
            :unitHeight="unit"
            :unitWidth="unit"
            :panelBorder="panelBorder"
            :backgroundColor="backgroundColor"
            :panelBorderColor="colorBorder"
            :collapseButtonEnabled="collapseButtonEnabled"
            :collapseButtonPosition="collapseButtonPosition2"
            :collapseButtonBackgroundColor="collapseButtonBackgroundColor"
            :collapseMouseOverColor="collapseMouseOverColor"
            :isCollapsed="isCollapsed"
            :iconClassColor="iconClassColor"
            :iconSize="iconSize"
            :cursorClass="cursorClass"
            :style="collapseLeft"
            :icon-orientation.sync="isCollapsePanelRight"
            @on-collapse="collapsePanelLeft">
            <p class="is-size-1 has-text-centered">
              UI Panel Container
            </p>
          </ui-panel-container>
        </div>
      </div>
    </div>
    <hr>
    <div class="positionRight">
      <b-tooltip label="Changelog"
        :active="true"
        position="is-left"
        type="is-dark">
        <ui-panel-container 
          :panelHeight="150"
          :panelWidth="600"
          unitHeight="px"
          unitWidth="px"
          backgroundColor="white"
          panelBorder="ridge"
          panelBorderColor="black"
          :collapseButtonEnabled="true"
          collapseButtonPosition="left"
          collapseButtonBackgroundColor="#C2C2C2"
          :style="dataCollapseLeft"
          :icon-orientation.sync="isCollapsePanelLeft"
          @on-collapse="dataCollapsePanelLeft">
          <markdown></markdown>
        </ui-panel-container>
      </b-tooltip>
    </div>
  </div>
</template>
<script>
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import titleComponent from '../titleComponent.vue'
import { updates } from '../../../libraryUpdates.js'
import readme from '../../../components/common/UIPanelContainer/changelog.md'
import {
  unitsHeight,
  unitsWidth
} from '../../../components/common/UIPanelContainer/config.js'
export default {
  name: 'UIPanelContainerPage',
  components: {
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },
  data () {
    return {
      /**
       * Version data
       */
      titlePage: updates.PanelContainer,
      dataCollapseLeft: {},
      /*
       * Flag that indicates if the panel is collapsed
       * @type {Boolean}
       */
      isCollapsed: true,
      /**
       * Height units and width units
       */
      unitsH: unitsHeight,
      unitsW: unitsWidth,
      /*
      Properties of the panel container without collapse button
        */
      panelHeight: 100,
      panelWidth: 100,
      unitHeight: '%',
      unitWidth: '%',
      panelBorder: 'none',
      colorBorder: '#2AFE74',
      backgroundColor: '#CDCACA',
      collapseButtonEnabled: true,
      collapseButtonBackgroundColor: '#157383',
      collapseMouseOverColor: '#202020',
      cursorClass: 'pointer',
      /**
       * style for the collapse action
       */
      collapseRight: {},
      collapseLeft: {},
      /**
       * Properties panel container left
       */
      panelHeight1: 700,
      panelWidth1: 440,
      unit: 'px',
      collapseButtonPosition1: 'right',
      /**
      * Properties panel container icon
      */
      iconSize: 'fa-2x',
      iconClassColor: 'has-text-primary',
      /**
       * Properties panel container right
       */
      collapseButtonPosition2: 'left',
      isCollapsePanelLeft: false,
      isCollapsePanelRight: false
    }
  },
  computed: {
    dynamiciconSizes () {
      let sizes = [
        'fa-1x',
        'fa-2x',
        'fa-5x'
      ]
      return sizes
    },
    dynamicIconClassColor () {
      let classes = [
        'has-text-primary',
        'has-text-info',
        'has-text-link',
        'has-text-success',
        'has-text-warning',
        'has-text-danger',
        'has-text-dark'
      ]
      return classes
    },
    dynamicCursorClass () {
      let classes = [
        'alias',
        'context-menu',
        'pointer',
        '-webkit-grab'
      ]
      return classes
    }
  },
  methods: {
    collapsePanelRight (data) {
      this.collapseRight = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    },
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    },
    dataCollapsePanelLeft (data) {
      this.dataCollapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>
<style scoped>
.positionRight {
  display: flex;
  justify-content: flex-end;
}
</style> 



