<template>
  <div>
   <title-component 
   :element="titlePage"
   titleClass="is-size-2"
   :isPage="true">
   </title-component>
    <hr>
    <b-switch v-model="labelOn">
      <p>Label {{onOffSwitch(labelOn)}}</p>
    </b-switch>
    <b-switch v-model="rounded">
      <p>Rounded {{onOffSwitch(rounded)}}</p>
    </b-switch>
    <b-switch v-model="disabled">
      <p>Disabled {{onOffSwitch(disabled)}}</p>
    </b-switch>
    <ui-search-select
      v-model = "selectedItem1"
      position = "label-left"
      :rounded = "rounded"
      :data="searchSelectData"
      :labelOn="labelOn"
      :entity = "AREA"
      :optionIdentifier = "identifier"
      :paramsToSee = "paramsToSee"
      :paramsToGet = "paramsToGet"
      :disabled="disabled">
    </ui-search-select>
    <ui-search-select
      v-model = "selectedItem2"
      position = "label-center"
      :rounded = "rounded"
      :data="searchSelectData"
      :labelOn="labelOn"
      :entity = "AREA"
      :optionIdentifier = "identifier"
      :paramsToSee = "paramsToSee"
      :paramsToGet = "paramsToGet"
      :disabled="disabled">
    </ui-search-select>
    <ui-search-select
      v-model = "selectedItem3"
      position = "label-right"
      :rounded = "rounded"
      :data="searchSelectData"
      :labelOn="labelOn"
      :entity = "AREA"
      :optionIdentifier = "identifier"
      :paramsToSee = "paramsToSee"
      :paramsToGet = "paramsToGet"
      :disabled="disabled">
    </ui-search-select>
    <ui-search-select
      v-model = "selectedItem4"
      position = "is-horizontal"
      :rounded = "rounded"
      :data="searchSelectData"
      :labelOn="labelOn"
      :entity = "AREA"
      :optionIdentifier = "identifier"
      :paramsToSee = "paramsToSee"
      :paramsToGet = "paramsToGet"
      :disabled="disabled">
    </ui-search-select>
    <hr>
    <b-field label="Type something">
      <b-input @input ="(event)=> {selectedItem5 = event}"/>
    </b-field>
    <ui-search-select
        v-model = "selectedItem5"
        position = "label-left"
        :entity = "AREA"
        :rounded = "rounded"
        :data="searchSelectData"
        :labelOn="labelOn"
        :optionIdentifier = "identifier"
        :paramsToSee = "paramsToSee"
        :paramsToGet = "paramsToGet"
        :disabled="disabled">
    </ui-search-select>
    <hr>
    <div class="positionRight">
      <b-tooltip
      label="Changelog"
      :active="true"
      position="is-left"
      type="is-dark">
        <ui-panel-container
          :panelHeight= "150"
          :panelWidth= "600"
            unitHeight="px"
            unitWidth="px"
          backgroundColor="white"
          panelBorder="ridge"
          panelBorderColor="black"
          :collapseButtonEnabled="true"
          collapseButtonPosition="left"
          collapseButtonBackgroundColor="#C2C2C2"
          :style="collapseLeft"
          @on-collapse="collapsePanelLeft">
            <markdown></markdown>
          </ui-panel-container>
      </b-tooltip>
    </div>
  </div>
</template>

<script>
import UISearchSelect from '../../../components/common/UISearchSelect/index'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import titleComponent from '../titleComponent.vue'
import { updates } from '../../../libraryUpdates.js'
import readme from '../../../components/common/UISearchSelect/changelog.md'

export default {
  name: 'UiSearchSelectPage',

  components: {
    'UiSearchSelect': UISearchSelect,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },

  data () {
    return {
      /**
       * Version data
       */
      titlePage: updates.SearhSelect,
      collapseLeft: {},
    /**
     * configuration
     */
      labelOn: true,
      disabled: false,
      AREA: 'AREA',

      autocomplete: true,

      identifier: 'areaId',

      paramsToSee: ['areaId', 'areaType', 'preferredName'],

      paramsToGet: ['areaId', 'areaType'],

      rounded: false,

      selectedItem1: null,
      selectedItem2: {
        areaId: '1',
        areaType: 'COUNTRY'
      },
      selectedItem3: {
        areaId: '2',
        areaType: 'CITY'
      },
      selectedItem4: {
        areaId: '3',
        areaType: 'COUNTY'
      },
      selectedItem5: 'default',
      searchSelectData: [
        {areaId: '1', areaType: 'County', preferredName: 'Puerto La Cruz'},
        {areaId: '2', areaType: 'Country', preferredName: 'Puerto La Cruz'},
        {areaId: '3', areaType: 'State', preferredName: 'Puerto La Cruz'},
        {areaId: '4', areaType: 'County', preferredName: 'Puerto La Cruz'}
      ]
    }
  },
  methods: {
    onOffSwitch (value) {
      return (value) ? 'On' : 'Off'
    },
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>

<style scoped lang="scss">
.positionRight {
  display: flex;
  justify-content: flex-end;
}
</style>



