<template>
  
  <div>
    <b-tooltip 
    :label="metaContent"
    :size="tooltipSize"
    :position="tooltipPosition"
    :active="isTooltipVisible"
    :type="tooltipType"
    :multilined="multilinedTooltip">
      <!-- The UILogo is not collapsed -->
      <figure 
      v-if="!isCollapsed" 
      class="image">
        <img 
        :src="dynamicSourceImage"
        :style="dynamicSizeLogo">
      </figure>
      <!-- The UILogo is collapsed -->
      <figure 
      v-if="isCollapsed"
      class="image">
        <img 
        :src="dynamicSourceImage"
        :style="dynamicSizeLogo">
      </figure>
    </b-tooltip>
  </div>
  
</template>

<script>
  /* Component that represents a logo, it has the following feature:
   * Switches between a collapsed logo image and not collapsed logo image.
   * Sets the height and width of the collapsed logo image and the not collapsed logo image.
   * Sets the configuration about the tooltip of the component like size, position, if it's activated
   * type,label and multilined.
   * @vue UILogo.vue
   * @version 0.1.0
   * @since 0.1.0
   * @author Alfredo Garcia [gitlab:@garciareal45]
   * @copyright Karanta, CA. 2018
   */
  export default {

    name: 'UiLogo',

    data () {
      return {

      }
    },

    props: {

      /**
       * Sets the height of the component when is collapsed
       * @prop {Number} [heightLogoCollapsed=100]
       */
      heightLogoCollapsed: {
        type: Number,
        required: false,
        default: 100
      },

      /**
       * Sets the width of the component when is collapsed
       * @prop {Number} [widthLogoCollapsed=100]
       */
      widthLogoCollapsed: {
        type: Number,
        required: false,
        default: 100
      },

      /**
       * Sets the height of the component when is not collapsed
       * @type {Number} [heightLogoNotCollapsed=100]
       */
      heightLogoNotCollapsed: {
        type: Number,
        required: false,
        default: 100
      },

      /**
       * Sets the width of the component when is not collapsed
       * @prop {Number} [widthLogoNotCollapsed=200]
       */
      widthLogoNotCollapsed: {
        type: Number,
        required: false,
        default: 200
      },

      /**
       * Sets the size unit of the component dimensions
       * @prop {String} [sizeUnit='px']
       */
      sizeUnit: {
        type: String,
        required: false,
        default: 'px'
      },
  
      /**
       * Sets the source of the image when the component is not collapsed
       * @prop {String}
       */
      srcImageCollapsed: {
        type: String,
        required: true
      },

      /**
       * Sets the source of the image when the component is not collapsed
       * @prop {String}
       */
      srcImageNotCollapsed: {
        type: String,
        required: true
      },

      /**
       *  Indicates if the icon is collapsed
       * @prop {Boolean} [isCollapsed= true]
       */
      isCollapsed: {
        type: Boolean,
        required: false,
        default: true
      },

      /**
       * Sets the content of the title on the tooltip of the component
       * @prop {String} [titleLogo= '']
       */
      tooltipTitle: {
        type: String,
        required: false,
        default: ''
      },

      /**
       * Sets the tooltip type on the component
       * @prop {String} [typeTooltip= 'is-primary']
       */
      tooltipType: {
        type: String,
        required: false,
        default: 'is-primary'
      },

      /**
      * Prop that determines if the tooltip is visible
      * @prop {Boolean} [isTooltipVisible= false]
      */
      isTooltipVisible: {
        type: Boolean,
        required: false,
        default: false
      },

      /**
       * Sets the tooltip position on the component
       * @prop {String} [tooltipPosition= 'is-right']
       */
      tooltipPosition: {
        type: String,
        required: false,
        default: 'is-right'
      },

      /**
      * Prop that determines the size of the toolTip
      * @prop {String} [tooltipSize= 'is-medium']
      */
      tooltipSize: {
        type: String,
        requerid: false,
        default: 'is-medium'
      },

      /**
      * Prop that determines if the tooltip is multilened
      * @prop {Boolean} [multilinedTooltip=false]
      */
      multilinedTooltip: {
        type: Boolean,
        required: false,
        default: false
      }
    },

    computed: {

      /**
       * Sets the size of the component when it is collapsed or is not collpased
       * @return {Object}
       */
      dynamicSizeLogo () {
        return (this.isCollapsed) ? {
          width: `${this.widthLogoCollapsed}${this.sizeUnit}`,
          height: `${this.heightLogoCollapsed}${this.sizeUnit}`
        } : {
          width: `${this.widthLogoNotCollapsed}${this.sizeUnit}`,
          height: `${this.heightLogoNotCollapsed}${this.sizeUnit}`
        }
      },

      /**
       * Sets the source of the image on the component
       * @return {String)
       */
      dynamicSourceImage () {
        return (this.isCollapsed) ? this.srcImageCollapsed : this.srcImageNotCollapsed
      },

      /**
       * Computed property that determines if it will show the tooltip custom message  or the tooltip
       * translated message
       * @return {String}
       */
      metaContent () {
        return (this.tooltipTitle || this.translateMeta('titleLogo'))
      }
    },

    methods: {
      /**
       * Translates the received parameter
       * @param  {String} value [parameter to translate]
       * @return {String}
       * @public
       */
      translateMeta (value) {
        return this.$i18n.t(`template.${value}.meta`)
      }
    }

  }
</script>
<style scoped>
 
</style>


