<template>
  <div>
   <title-component 
   :element="titlePage"
   titleClass="is-size-2"
   :isPage="true">
   </title-component>
   <hr>
    
      <div class="columns">
        
       <div class="column is-2">
          <b-field label="Close button appears">
            <b-switch
            v-model="closeButtonAppear" 
            rounded>
            </b-switch>
          </b-field>
       </div>
       <div class="column is-2">
         <b-field label="Background Color header"> 
            <b-input 
            type="color" 
            v-model="backgroundColorHeader"
            rounded>
              
            </b-input> 
         </b-field>
       </div>
        <div class="column is-2">
         <b-field label="Background Color body"> 
            <b-input 
            type="color" 
            v-model="backgroundColorBody"
            rounded>
              
            </b-input> 
         </b-field>
       </div>

        <div class="column is-2">
         <b-field label="Background Color footer"> 
            <b-input 
            type="color" 
            v-model="backgroundColorFooter"
            rounded>
              
            </b-input> 
         </b-field>
       </div>
       <div class="column">
         <button 
          class="button is-primary is-medium is-rounded"
          @click="() => {modalActivated = !modalActivated}">
            Launch modal
          </button>
       </div>

      </div>

    <ui-message-box
    :modalActivated.sync="modalActivated"
    :closeButtonAppear="closeButtonAppear"
    :backgroundColorHeader="backgroundColorHeader"
    :backgroundColorBody="backgroundColorBody"
    :backgroundColorFooter="backgroundColorFooter">
      <div 
      slot="title" 
      class="has-text-centered is-size-1">
        Title
      </div>

      <div slot="body">
        Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod
        tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam,
        quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo
        consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse
        cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non
        proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
        Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod
        tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam,
        quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo
        consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse
        cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non
        proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
      </div>

      <div 
      slot="footer">
        <ui-tool-bar 
        :buttonConfigurations = "configurationButtons"
        :alignContentToolBar = "alignContentToolBar"
        :gapContentToolBar = "gapContentToolBar"
       ></ui-tool-bar>
      </div>
    </ui-message-box>
      <hr>
      <div class="positionRight">
        <b-tooltip
        label="Changelog"
        :active="true"
        position="is-left"
        type="is-dark">
          <ui-panel-container
            :panelHeight= "150"
            :panelWidth= "600"
             unitHeight="px"
             unitWidth="px"
            backgroundColor="white"
            panelBorder="ridge"
            panelBorderColor="black"
            :collapseButtonEnabled="true"
            collapseButtonPosition="left"
            collapseButtonBackgroundColor="#C2C2C2"
            :style="collapseLeft"
            @on-collapse="collapsePanelLeft">
              <markdown></markdown>
            </ui-panel-container>
        </b-tooltip>
      </div>
  </div>
</template>

<script>
import UIMessageBox from '../../../components/common/UIMessageBox/index.js'
import UIToolBar from '../../../components/common/UIToolBar/index.js'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import titleComponent from '../titleComponent.vue'
import { updates } from '../../../libraryUpdates.js'
import readme from '../../../components/common/UIMessageBox/changelog.md'

export default {

  name: 'UIMessageBoxPage',

  components: {
    'UiMessageBox': UIMessageBox,
    'UiToolBar': UIToolBar,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },

  data () {
    return {
      /**
       * Version data
       */
      titlePage: updates.MessageBox,
      collapseLeft: {},
      /*
        Props of the UIMessageBox
       */
      modalActivated: true,
      closeButtonAppear: true,
      backgroundColorHeader: '#f5f5f5',
      backgroundColorBody: '#ffffff',
      backgroundColorFooter: '#f5f5f5',

      /*
       Props of the UIToolBar
       */
      alignContentToolBar: 'center',
      gapContentToolBar: 'is-3',
      configurationButtons: [{
        buttonType: 'cancel',
        visibleLabel: true,
        buttonClass: 'is-danger',
        iconType: 'fas fa-times',
        buttonSize: 'is-medium',
        visibleIcon: true,
        isRounded: true,
        visibleToolTip: false,
        positionToolTip: 'is-top',
        typeToolTip: 'is-primary',
        sizeToolTip: 'is-medium',
        multilinedToolTip: false
      },
      {
        buttonType: 'save',
        visibleLabel: true,
        buttonClass: 'is-success',
        iconType: 'fas fa-check-circle',
        buttonSize: 'is-medium',
        visibleIcon: true,
        isRounded: true,
        visibleToolTip: false,
        positionToolTip: 'is-top',
        typeToolTip: 'is-primary',
        sizeToolTip: 'is-medium',
        multilinedToolTip: false
      }]
    }
  },
  methods: {
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }

}
</script>
<style>
.positionRight {
  display: flex;
  justify-content: flex-end;
}
</style>



