<template>
  <div class="columns is-centered">
    <div class="column is-half">
    <hr>
    <h2 class="is-size-2">Inputs UI Components</h2>
    <hr>
    <h3 class="is-size-3 has-text-left">Inputs</h3>
    <br>
    <ul>
      <template v-for="(element, index) in updateskeys">
      <hr>
        <router-link :to="{name: elements[element].name}" tag="div">
          <title-component :element="elements[element]"></title-component>
        </router-link>
      </template>
    </ul>
    </div>
  </div>
</template>

<script>
import titleComponent from '../common/titleComponent.vue'
import {updatesInput} from '../../libraryUpdates.js'
export default {
  name: 'InputsSection',
  components: {
    titleComponent
  },

  data () {
    return {
      elements: updatesInput
    }
  },
  methods: {

  },

  computed: {
    updateskeys () {
      return Object.keys(this.elements).sort()
    }
  }
}
</script>

<style>

</style>



