<template>
  <div>
   <highcharts 
   v-if="vueChartType === 'highchart'" 
   :options="optionsHighchart">
   </highcharts>
   <highstock 
   v-if="vueChartType === 'highstock'" 
   :options="optionsHighstock">
   </highstock>
  </div>
</template>
<script>
  /**
   * Component that generates charts with the following features:
   * Mix between diferent charts
   * Generates bar, column, line, spline, area, areaspline
   * Generates History chart with time intervals search
   * Generates different scales between diferent charts
   * Generates colors for the scales, variable names
  * @vue UIChart.vue
  * @version 0.1.0
  * @since 0.1.0
  * @author Alfredo Garcia [gitlab:@garciareal45]
  * @copyright Karanta, CA. 2018
   */
  export default {
    name: 'UiChart',

    props: {
  
      /**
       * Property that determines what vue chart is shown
       * @prop {String} [vueChartType= 'highchart']
       */
      vueChartType: {
        type: String,
        required: false,
        default: 'highchart'
      },
  
      /**
       * Property that represents the chart type
       * @prop {String} [chartType= 'line']
       */
      chartType: {
        type: String,
        required: false,
        default: 'line'
      },

      /**
       * Property that represents the configuration about the chart title
       * @prop {Array} [title= {text: ''}]
       */
      title: {
        type: [Array, Object],
        required: false,
        default: function () {
          return {
            text: ''
          }
        }
      },

      /**
       * Property that represents the configuration about the chart subtitle
       * @prop {Array} [subtitle= {text:''}]
       */
      subtitle: {
        type: [Array, Object],
        required: false,
        default: function () {
          return {
            text: ''
          }
        }
      },

      /**
       * Property that represents the configuration about the Y-axis
       * @prop {Array} [yAxis= [{}] ]
       */
      yAxis: {
        type: [Array, Object],
        required: false,
        default: function () {
          return [{}]
        }
      },

      /**
       * Property that represents the configuration about the X-axis
       * @prop {Array} [xAxis= [{}] ]
       */
      xAxis: {
        type: [Array, Object],
        required: false,
        default: function () {
          return [{}]
        }
      },

      /**
       * Property that represents the configuration about the chart series
       * @prop {Array}
       */
      series: {
        type: [Array],
        required: true
      },

      /**
       * Property that represents the configuration about the range selector of the
       * chart
       * @prop {Object}  [rangeSelector= {}]
       */
      rangeSelector: {
        type: Object,
        required: false,
        default: function () {
          return {}
        }
      },

      /**
       * Property that represents the configuration about the navigator of the
       * highstock chart
       * @prop {Object} [navigator= {}]
       */
      navigator: {
        type: Object,
        required: false,
        default: function () {
          return {}
        }
      },

      /**
       * Property that represents the configuration about the legend of the chart
       * @prop {Object} [legend= {}]
       */
      legend: {
        type: Object,
        required: false,
        default: function () {
          return {}
        }
      },
      /**
       * Additional configurations for a chart
       * @prop {Object} [plotOptions = {}]
       */
      plotOptions: {
        type: Object,
        required: false,
        default: function () {
          return {}
        }
      }

    },
  
    data () {
      return {
        /**
         * Object that represents the highchart configuration
         * @type {Object}
         */
        optionsHighchart: {
          chart: {
            type: this.chartType,
            zoomType: 'xy'
          },

          title: this.title,

          xAxis: this.xAxis,

          yAxis: this.yAxis,

          series: this.series,

          legend: this.legend,

          plotOptions: this.plotOptions,

          credits: {
            enabled: false
          }
        },

        /**
         * Object that represents the highstock configuration
         * @type {Object}
         */
        optionsHighstock: {
          chart: {
            type: this.chartType,
            zoomType: 'xy'
          },

          /*
          Configuration about intervals on time on highstock
           */
          rangeSelector: this.rangeSelector,

          title: this.title,

          series: this.series,

          xAxis: {
            type: 'datetime'
          },

          yAxis: this.yAxis,

          credits: {
            enabled: false
          },

          plotOptions: this.plotOptions,

          navigator: this.navigator,

          legend: this.legend

        }
      }
    },

    computed: {
  
      /**
       * Computed property that validates the type of vue chart type.
       * @public
       */
      validateVueChartType () {
        let types = [
          'highchart',
          'highstock'
        ]

        if (types.indexOf(this.vueChartType.toLowerCase() === -1)) {
          console.log('Wrong vue chart type')
        }
      },
  
      /**
       * Computed property that validates the type of highchart.
       * @public
       */
      selectChart () {
        let chartTypes = [
          'line',
          'bar',
          'column',
          'area',
          'spline'
        ]

        if (chartTypes.indexOf(this.chartType.toLowerCase()) === -1) {
          console.error('Wrong type chart')
        }
      }
    },
    /**
     * This method makes the translation of the chart.
     * @public
     */
    created () {
      /*
      * Transalation of the chart
      */
      this.Highcharts.setOptions({
        lang: {
          rangeSelectorFrom: this.$i18n.t(`template.from`),
          rangeSelectorTo: this.$i18n.t(`template.to`),
          resetZoom: this.$i18n.t(`template.resetZoom`),
          weekdays: this.$i18n.t(`template.days`),
          shortMonths: this.$i18n.t(`template.months`)
        }
      })
    }
  }
</script>


