            positionToolTip: 'is-top',
            typeToolTip: 'is-info',
            sizeToolTip: 'is-medium',
            multilinedToolTip: true
          }
        ],
        toolbarConfiguration: {
          dropDownAppear: false,
          gapContentToolBar: 'is-1'
        },
        toolbarPosition: 'center',
        data: {
          id: {
            areaId: '14_093',
            areaType: 'COUNTY'
          },
          activeInd: null,
          areaMaxLatitude: null,
          areaMaxLongitude: null,
          areaMinLatitude: null,
          areaMinLongitude: null,
          coordAcquisitionId: null,
          coordSystemId: null,
          effectiveDate: '2018-03-31',
          expiryDate: null,
          localCoordSystemId: null,
          ppdmGuid: '14_093 COUNTY',
          preferredName: 'venezuela',
          remark: 'Hola Mundo',
          rowChangedBy: 'UpdatedUser',
          rowChangedDate: '2018-04-17',
          rowCreatedBy: null,
          rowCreatedDate: null,
          rowEffectiveDate: null,
          rowExpiryDate: null,
          rowQuality: 'Real',
          source: 'PPDM',
          sourceDocumentId: null
        }
      }

    }
  },

  computed: {
    dynamicSize () {
      let sizes = []
      for (var i = 2; i <= 9; i++) {
        sizes.push(i)
      }
      return sizes
    },

    configurationTitleEditable () {
      let vm = this
      let configuration = {}
      configuration.align = vm.titleAlign
      configuration.color = vm.titleColor
      configuration.size = vm.titleSize
      configuration.transformation = vm.titleTransformation
      configuration.weight = vm.titleWeight
      return configuration
    },

    titleColors () {
      let colors = [
        'white',
        'black',
        'light',
        'dark',
        'primary',
        'info',
        'link',
        'success',
        'warning',
        'danger',
        'black-bis',
        'black-ter',
        'grey-dark',
        'grey-darker',
        'grey',
        'grey-light',
        'grey-lighter',
        'white-ter',
        'white-bis'
      ]

      return colors
    },

    titleWeights () {
      let weights = [
        'light',
        'normal',
        'semibold',
        'bold'
      ]

      return weights
    },

    titleAlings () {
      let aligns = [
        'left',
        'centered',
        'right'
      ]

      return aligns
    },

    titleTransformations () {
      let transformations = [
        'italic',
        'capitalized',
        'uppercase',
        'lowercase',
        'italic capitalized',
        'italic uppercase',
        'italic lowercase'
      ]

      return transformations
    }

  },

  methods: {
    eventToolbar (value) {
      console.log('toolbar value:', value)
    },
    changeContent (component) {
      this.currentComponent = component
    },
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>
<style>
.positionRight {
  display: flex;
  justify-content: flex-end;
}
</style>



