<template>
  <div>
   <title-component 
   :element="titlePage"
   titleClass="is-size-2"
   :isPage="true">
   </title-component>
    <hr>
    <div class="columns">
      <b-field grouped group-multiline>
        <div class="control is-flex">
            <b-switch v-model="narrowed">Narrowed</b-switch>
        </div>
        <div class="control is-flex">
            <b-switch v-model="stripped">Stripped</b-switch>
        </div>

        <div class="control is-flex">
            <b-switch v-model="loading">Loading</b-switch>
        </div>

         <div class="control is-flex">
            <b-switch v-model="bordered">Bordered</b-switch>
        </div>

        <div class="control is-flex">
            <b-switch v-model="checkable">Checkable</b-switch>
        </div>

        <div class="control is-flex">
            <b-switch v-model="tooltipMultilined">Tooltip multilined</b-switch>
        </div>
             
        <b-field label= "Align UIContainer1">
           <b-select 
           v-model="alignUIContainer2" 
           rounded>
            <option 
            v-for="(align,key) in containerAligns"
            :key="key">
              {{align}}
            </option>
           </b-select>
        </b-field>
        <b-field label= "Align UIContainer2">
           <b-select 
           v-model="alignUIContainer1"
           rounded>
            <option 
            v-for="(align,key) in containerAligns"
            :key="key">
              {{align}}
            </option>
           </b-select>
        </b-field>
        <b-field label= "Align UIContainer3">
           <b-select 
           v-model="alignUIContainer3"
           rounded>
            <option 
            v-for="(align,key) in containerAligns"
            :key="key">
              {{align}}
            </option>
           </b-select>
        </b-field>

        <b-field label= "Tooltip type">
           <b-select 
           v-model="typeTooltip" 
           rounded>
            <option v-for="(type,key) in typeTooltips"
            :key='key'>{{type}}</option>
           </b-select>
        </b-field>

        <b-field label= "Tooltip position">
           <b-select 
           v-model="tooltipPosition"
           rounded>
            <option
              v-for="(position,key) in tooltipPositions"
              :key="key">
                {{position}}
            </option>
           </b-select>
        </b-field>

        <b-field label= "Tooltip size">
           <b-select 
           v-model="tooltipSize"
           rounded>
            <option
              v-for="(size,key) in tooltipSizes"
              :key="key">
                {{size}}
            </option>
           </b-select>
        </b-field>

        <div>
          <button class="button field is-danger" :disabled="!selected" @click="clearSelection('selected')">
            <b-icon icon="fa fa-times"></b-icon>
            <span>Clear Selected</span>
          </button>
        </div>

      </b-field>
    </div>
    <br>
    <ui-generic-tabulated-data
      :columns-config="columnsConfig"
      entity="AREA"
      :selected.sync="selected"
      :striped="stripped"
      :bordered="bordered"
      :checkable="checkable"
      :narrowed="narrowed"
      default-sort-field="preferredName"
      default-sort-order="asc"
      :default-page="1"
      :dataTable = "dataList"
      :loading = "loading"
      :total = "total"
      :tooltipType="typeTooltip"
      :tooltipPosition="tooltipPosition"
      :tooltipMultilined="tooltipMultilined"
      :tooltipSize="tooltipSize"
      :enabledRowPerPage="true"
      :enabledAsynchronousTable="asyncTable1"
      @on-row-select="selectRow"
      @on-load-data= "loadDataTable"
      @on-check-row="checkRow">

      <template slot="centerTop">
      <ui-container
      :align="alignUIContainer1"
      :closeable="closeable"
      >
        <div class="notification is-primary">
            <h1 class="title">UIContainer 2</h1>
        </div>
      </ui-container>

      </template>

      <template slot = "leftTop">
        <ui-container
        :align="alignUIContainer2"
        :closeable="closeable"
        >
          <div class="notification is-primary">
            <h1 class="title">UIContainer  1</h1>
          </div>

        </ui-container>

      </template>

      <template slot = "bottomTop">
        <ui-container
        :align="alignUIContainer3"
        :closeable="closeable"
        >
          <div class="notification is-primary">
            <h1 class="title">UIContainer  3</h1>
          </div>

        </ui-container>

      </template>
      </ui-generic-tabulated-data>
      <hr>
      <h1 class="is-size-3">UI Generic Tabulated Data with local data</h1>
      <hr>
      <div class="columns">
        <div class="control is-flex">
          <b-switch v-model="enabledRowPerPage">RowPerPage</b-switch>
        </div>
      </div>
      <div>
        <button class="button field is-danger" :disabled="!selectedTable2" @click="clearSelection('selectedTable2')">
          <b-icon icon="fa fa-times"></b-icon>
          <span>Clear Selected</span>
        </button>
      </div>
      <br>
      <ui-generic-tabulated-data
      :columns-config="columnsConfig"
      entity="AREA"
      :selected.sync="selectedTable2"
      :striped="stripped"
      :bordered="bordered"
      :checkable="checkable"
      :narrowed="narrowed"
      default-sort-field="preferredName"
      default-sort-order="asc"
      :default-page="1"
      :dataTable = "dataList2"
      :loading = "loading"
      :tooltipType="typeTooltip"
      :tooltipPosition="tooltipPosition"
      :tooltipMultilined="tooltipMultilined"
      :tooltipSize="tooltipSize"
      :enabledRowPerPage="enabledRowPerPage"
      :enabledAsynchronousTable="asyncTable2">

      <template slot="centerTop">
      <ui-container
      :align="alignUIContainer1"
      :closeable="closeable"
      >
        <div class="notification is-primary">
            <h1 class="title">UIContainer 2</h1>
        </div>
      </ui-container>

      </template>

      <template slot = "leftTop">
        <ui-container
        :align="alignUIContainer2"
        :closeable="closeable"
        >
          <div class="notification is-primary">
            <h1 class="title">UIContainer  1</h1>
          </div>

        </ui-container>

      </template>

      <template slot = "bottomTop">
        <ui-container
        :align="alignUIContainer3"
        :closeable="closeable"
        >
          <div class="notification is-primary">
            <h1 class="title">UIContainer  3</h1>
          </div>

        </ui-container>

      </template>

      </ui-generic-tabulated-data>
      <hr>
      <div class="positionRight">
        <b-tooltip
          label="Changelog"
          :active="true"
          position="is-left"
          type="is-dark">
            <ui-panel-container
              :panelHeight= "250"
              :panelWidth= "600"
              unitHeight="px"
              unitWidth="px"
              backgroundColor="white"
              panelBorder="ridge"
              panelBorderColor="black"
              :collapseButtonEnabled="true"
              collapseButtonPosition="left"
              collapseButtonBackgroundColor="#C2C2C2"
              :style="collapseLeft"
              @on-collapse="collapsePanelLeft">
                <markdown></markdown>
            </ui-panel-container>
        </b-tooltip>
      </div>
  </div>
</template>

<script>
import UIGenericTabulatedData from '../../../components/common/UIGenericTabulatedData/UIGenericTabulatedData'
import UIContainer from '../../../components/common/UIContainer/index.js'
import { updates } from '../../../libraryUpdates.js'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import titleComponent from '../titleComponent.vue'
import readme from '../../../components/common/UIGenericTabulatedData/changelog.md'
const datatable = require('./datatable.json')

export default {
  name: 'GenericTabulatedDataPage',
  components: {
    'ui-generic-tabulated-data': UIGenericTabulatedData,
    'UiContainer': UIContainer,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },
  data () {
    return {
      /*
      Props of the UIGenericTabulatedData
       */
      collapseLeft: {},
      titlePage: updates.GenericTabulatedData,
      dataList2: [],
      selected: null,
      selectedTable2: null,
      alignUIContainer1: 'center',
      alignUIContainer2: 'center',
      alignUIContainer3: 'none',
      typeTooltip: 'is-primary',
      tooltipMultilined: false,
      tooltipPosition: 'is-top',
      tooltipSize: 'is-medium',
      narrowed: true,
      stripped: true,
      closeable: true,
      checkable: false,
      alignContentContainer1: 'center',
      alignContentContainer2: 'left',
      dataList: [],
      bordered: false,
      total: 0,
      loading: false,
      enabledRowPerPage: true,
      asyncTable1: true,
      asyncTable2: false,
      /*
      Configuration of the columns on the table
       */
      columnsConfig: [
        {
          classTag: 'basicData',
          children: [
            {
              propTag: 'id.areaId',
              tabulatedData: {
                type: 'string',
                visibleOnTable: true,
                sortable: true,
                centered: true,
                classes: ''
              }
            },
            {
              propTag: 'id.areaType',
              tabulatedData: {
                type: 'string',
                visibleOnTable: true,
                sortable: true,
                centered: true,
                classes: ''
              }
            },
            {
              propTag: 'preferredName',
              tabulatedData: {
                type: 'string',
                visibleOnTable: true,
                sortable: true,
                centered: true,
                classes: ''
              }
            },
            {
              propTag: 'activeInd',
              tabulatedData: {
                type: 'string',
                visibleOnTable: true,
                sortable: false,
                centered: true,
                classes: 'tag',
                functionClass: function (value) {
                  return (value !== null && value.trim() === 'N') ? 'is-danger' : 'is-success'
                }/* ,
              iconConf: {
                family: 'fa',
                showText: true,
                startPosition: true,
                defaultValue: 'Y',
                iconConditions: [
                  {
                    value: 'Y',
                    icon: 'check-circle'
                  },
                  {
                    value: 'N',
                    icon: 'times-circle'
                  }
                ]
              } */
              }
            }
          ]
        },
        {
          classTag: 'areaMaxLocation',
          children: [
            {
              propTag: 'areaMaxLatitude',
              tabulatedData: {
                type: 'number',
                visibleOnTable: true,
                sortable: false,
                centered: true,
                classes: ''
              }
            },
            {
              propTag: 'areaMaxLongitude',
              tabulatedData: {
                type: 'number',
                visibleOnTable: true,
                sortable: false,
                centered: true,
                classes: ''
              }
            },
            {
              propTag: 'effectiveDate',
              tabulatedData: {
                type: 'date',
                visibleOnTable: true,
                sortable: false,
                centered: true,
                classes: ''
              }
            },
            {
              propTag: 'expiryDate',
              tabulatedData: {
                type: 'date',
                visibleOnTable: true,
                sortable: false,
                centered: true,
                classes: ''
              }
            }
          ]
        }
      ]
    }
  },

  computed: {
    filteredDataArray () {
      return this.dataList.filter((option) => {
        return option.id.areaId
                  .toString()
                  .toLowerCase()
                  .indexOf(this.name.toLowerCase()) >= 0
      })
    },

    typeTooltips () {
      let types = [
        'is-primary',
        'is-danger',
        'is-info',
        'is-link',
        'is-success',
        'is-warning',
        'is-white',
        'is-dark',
        'is-light',
        'is-black'
      ]

      return types
    },

    tooltipSizes () {
      let sizes = [
        'is-small',
        'is-medium',
        'is-large'
      ]

      return sizes
    },

    tooltipPositions () {
      let position = [
        'is-top',
        'is-bottom',
        'is-left',
        'is-right'
      ]

      return position
    },

    containerAligns () {
      let aligns = [
        'none',
        'left',
        'center',
        'right'
      ]

      return aligns
    }

  },

  created () {
    const records = datatable.content.map((record) => {
        // By each property in record
      for (let propsField in record) {
        if (record[propsField] !== null && typeof record[propsField] === 'object') {
          // If record property is an object, create the property at superior level joining the property with the intern property
          for (let internPropField in record[propsField]) {
            record[`${propsField}.${internPropField}`] = record[propsField][internPropField]
          }
        }
      }
      return record
    })

    this.dataList2 = this._.union([], records)
    let parameter = {
      page: 0,
      perPage: 20,
      sortField: 'id.areaId'
    }
    this.loadDataTable(parameter)
  },

  methods: {

    checkRow (listCheckedRows) {
      console.log(listCheckedRows)
    },

    selectRow (row, oldRow) {
      console.log('Fila seleccionada: ', row)
      console.log('Fila anterior: ', oldRow)
    },

    ordination (parameter, array) {
      array.sort(function (a, b) {
        if (parameter.sortOrder === 'asc') {
          if (a[parameter.sortField] > b[parameter.sortField]) {
            return 1
          }

          if (a[parameter.sortField] < b[parameter.sortField]) {
            return -1
          }

          return 0
        } else {
          if (a[parameter.sortField] < b[parameter.sortField]) {
            return 1
          }

          if (a[parameter.sortField] > b[parameter.sortField]) {
            return -1
          }

          return 0
        }
      })
    },

    pagination (parameters, array) {
      let start = parameters.page + (parameters.page * (parameters.perPage - 1))
      let end = start + parameters.perPage

      return array.slice(start, end)
    },

    loadDataTable (parameter) {
      const records = datatable.content.map((record) => {
        // By each property in record
        for (let propsField in record) {
          if (record[propsField] !== null && typeof record[propsField] === 'object') {
          // If record property is an object, create the property at superior level joining the property with the intern property
            for (let internPropField in record[propsField]) {
              record[`${propsField}.${internPropField}`] = record[propsField][internPropField]
            }
          }
        }
        return record
      })

      this.total = datatable.totalElements
      this.dataList = this._.union([], records)
      this.ordination(parameter, this.dataList)
      this.dataList = this.pagination(parameter, this.dataList)
    },
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    },

    clearSelection (property) {
      this[property] = null
    }
  }
}
</script>

<style scoped>
.positionRight {
  display: flex;
  justify-content: flex-end;
}
</style>



