<template>
    <b-tooltip :label="translateLabel(buttonType)"
                :size="toolTipSize"
                :position="positionToolTip"
                :type="typeToolTip"
                :active="visibleToolTip"
                :multilined="multilinedToolTip">

                <button
                  class="button"
                  :class="[dynamicColorActivation, dynamicClass]"
                  @click="clickButton"
                  v-bind="$attrs">
                  <label v-if="visibleLabel">
                    {{translateLabel(buttonType)}}
                  </label>

              <b-icon v-if="visibleIcon"
                    :pack="iconPack"
                    :icon="iconType"
                    :size="sizeIcon"
                    >
              </b-icon>

        </button>
    </b-tooltip>
</template>

<script>
/**
 * Button that allows to enable the functions of edit, restore or delete a
 * given registry. Have the following function:
 *  * Send the type of operation the father component
 * @vue UIButton.vue
 * @version 0.1.0
 * @since 0.1.0
 * @author Alfredo Garcia [gitlab:@garciareal45]
 * @copyright Karanta, CA. 2018
 */
export default {
  name: 'UiButton',
  data () {
    return {
      notification: false
    }
  },
  props: {
    /**
    * prop that allows add a class on click
    * @prop {Boolean} [default= false]
    */
    enableNotification: {
      type: Boolean,
      default: false
    },
    /**
    * prop that determines the added class associated with the button
    * @prop {String} [default= is-inverted]
    */
    addedClass: {
      type: String,
      default: 'is-inverted'
    },
    /**
    * prop that shows the added class
    * @prop {Boolean} [default = false]
    */
    visibleNotification: {
      type: Boolean,
      default: false
    },
    /**
    * prop that determines the type of operation button
    * @prop {String}
    */
    buttonType: {
      type: String,
      required: true
    },

    /**
    * Prop that allows to enable the visibility of button label
    * @prop {Boolean} [default= false]
    */
    visibleLabel: {
      type: Boolean,
      required: false,
      default: true
    },

    /**
    * Prop that determines the type of icon on the button
    * @prop {Boolean}
    */
    iconType: {
      type: String,
      required: true
    },

    /**
    * Prop that determines if the icon is visible or not
    * @prop {Boolean} [visibleIcon='true']
    */
    visibleIcon: {
      type: Boolean,
      required: false,
      default: true
    },

    /**
    * Prop that determines the pack of icon on the button
    * @prop {String} [iconPack=fas]
    */
    iconPack: {
      type: String,
      required: false,
      default: 'fas'
    },

    /**
    * Prop that determines the size of icon on the button
    * @prop {String} [sizeIcon=is-small]
    */
    sizeIcon: {
      type: String,
      required: false,
      default: 'is-small'
    },

    /**
    * Prop that determines if the button is rounded
    * @prop {Boolean} [buttonIsRounded= false]
    */
    buttonIsRounded: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
    * Prop that determines the class associated with the button
    * @prop {String} [buttonClass= is-primary]
    */
    buttonClass: {
      type: String,
      required: false,
      default: 'is-primary'
    },

    /**
    * Prop that determines the size of the button
    *@prop {String} [buttonSize= is-medium]
    */
    buttonSize: {
      type: String,
      required: false,
      default: 'is-medium'
    },

    /**
    * Prop that determines if the tooltip is visible
    * @prop {Boolean} [visibleToolTip= false]
    */
    visibleToolTip: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
    * Prop that determines the position of the tooltip on the button
    * @prop {String} [positionToolTip= 'is-top']
    */
    positionToolTip: {
      type: String,
      required: false,
      default: 'is-top'
    },

    /**
    * Prop that determines the class of the tooltip on the button
    * @prop {String} [typeToolTip= 'is-success']
    */
    typeToolTip: {
      type: String,
      required: false,
      default: 'is-success'
    },

    /**
    * Prop that determines the size of the toolTip
    * @prop {String} [typeToolTip= 'is-medium']
    */
    toolTipSize: {
      type: String,
      requerid: false,
      default: 'is-medium'
    },

     /**
    * Prop that determines if the tooltip is multilened
    * @prop {String} [multilinedToolTip=false]
    */
    multilinedToolTip: {
      type: Boolean,
      required: false,
      default: false
    }
  },

  computed: {
    /**
    * Computed properties that determine the size of button
    */
    dynamicClass () {
      return {
        'is-rounded': this.buttonIsRounded,
        'is-small': (this.buttonSize === 'is-small'),
        'is-medium': (this.buttonSize === 'is-medium'),
        'is-large': (this.buttonSize === 'is-large')
      }
    },
    /**
     * Computed property that determines the added class
     */
    dynamicColorActivation () {
      return this.notification ? this.addedClass : this.buttonClass
    }
  },
  watch: {
  /**
   * Watch property that notifies when an added class is changed
   */
    visibleNotification () {
      if (this.enableNotification) {
        this.notification = this.visibleNotification
      }
    }
  },
  methods: {
    /**
     * Sends notifications to the father component
     * @public
     */
    clickButton () {
      /**
       * Event that sends a notification
       * @event click
       * @type {String}
       */
      this.$emit('click', this.buttonType)
      if (this.enableNotification) {
        this.notification = !this.notification
        /**
         * Event that updates the property visibleNotification
         * @event update:visibleNotification
         * @type {Boolean}
         */
        this.$emit('update:visibleNotification', !this.visibleNotification)
      }
    },
     /**
      * Translates the received parameter
      * @returns {String}
      * @param {String}
      * @public
      */
    translateLabel (value) {
      return this.$i18n.t(`template.${value}.label`)
    },

     /**
      * Translates the received parameter
      * @returns {String}
      * @param {String}
      * @public
      */
    translateMeta (value) {
      return this.$i18n.t(`template.${value}.meta`)
    }
  }
}
</script>



