import axios from 'axios'
const API_BASE = 'https://datos-de-areas.firebaseio.com/AREAS.json'

export const services = {
  AREA (query) {
    let path = API_BASE
    if (query.trim() !== '') {
      path = `${path}/?=${query}`
    }
    return axios.get(path)
  }
}



