<template>
<div>
  <title-component 
   :element="titlePage"
   titleClass="is-size-2"
   :isPage="true">
  </title-component>
  <hr>
  <div class="columns">
    <div class="column is-2 is-offset-1">
      <b-field label="filterable">
        <b-switch v-model="filterable"></b-switch>
      </b-field>
    </div>

     <div class="column is-3">
      <b-field label="Enable buttons text">
        <b-switch v-model="enabledButtonsText"></b-switch>
      </b-field>
    </div>

    <div class="column is-2">
      <b-field label="Title list left">
        <b-input 
        v-model="title1" 
        rounded>
        </b-input>
    </b-field>
    </div>

    <div class="column is-2">
      <b-field label="Title list right">
        <b-input 
        v-model="title2" 
        rounded>
        </b-input>
      </b-field>
    </div>
  </div>
  <div class="columns"> 
    <div class="column is-2 is-offset-4">
      <b-field label="Button text left">
        <b-input 
        v-model="buttonText1" 
        rounded>
        </b-input>
      </b-field>
    </div>

    <div class="column is-2">
      <b-field label="Button text right">
        <b-input 
        v-model="buttonText2" 
        rounded>
        </b-input>
      </b-field>
    </div>
  </div>
  <div class="columns">
    <div class="column is-offset-4">
      <ui-transfer
      :selectedItems="selectedItems"
      :dataList="dataList"
      :titles="[title1, title2]"
      :filterable="filterable"
      :button-texts="enableButtonText"
      @on-change-transfer="changeRightList">
      </ui-transfer>  
    </div>
  </div>
  <div class="columns">
    <div class="column is-4 is-offset-2">
      <b-message
        title="Console"
        :closable="false">
        <b-message
          title="Input"
          :closable="false">
          <pre>{{dataList}}</pre>
        </b-message>
        </b-message>
    </div>
    <div class="column is-4">
      <b-message
        title="Console"
        :closable="false">
        <b-message
          :title="eventName"
          :closable="false">
          <pre>{{eventData}}</pre>
        </b-message>
        </b-message>
    </div>
  </div>
      <hr>
      <div class="positionRight">
        <b-tooltip
        label="Changelog"
        :active="true"
        position="is-left"
        type="is-dark">
          <ui-panel-container
            :panelHeight= "150"
            :panelWidth= "600"
             unitHeight="px"
             unitWidth="px"
            backgroundColor="white"
            panelBorder="ridge"
            panelBorderColor="black"
            :collapseButtonEnabled="true"
            collapseButtonPosition="left"
            collapseButtonBackgroundColor="#C2C2C2"
            :style="collapseLeft"
            @on-collapse="collapsePanelLeft">
              <markdown></markdown>
            </ui-panel-container>
        </b-tooltip>
      </div>
</div>
</template>
<script>
import UITransfer from '../../../components/common/UITransfer/index.js'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import titleComponent from '../titleComponent.vue'
import { updates } from '../../../libraryUpdates.js'
import readme from '../../../components/common/UITransfer/changelog.md'

export default {
  name: 'UITransferPage',

  components: {
    'UiTransfer': UITransfer,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },

  data () {
    return {
      /**
       * Version data
       */
      titlePage: updates.Transfer,
      collapseLeft: {},
      /**
       * Selected item key
       */
      selectedItems: [1, 3],
      /**
       * configurations
       */
      dataList: [
        {key: 0, label: 'Option 1', disabled: true},
        {key: 1, label: 'Option 2'},
        {key: 2, label: 'Option 3'},
        {key: 3, label: 'Option 4'}
      ],
      title1: '',
      title2: '',
      filterable: false,
      enabledButtonsText: false,
      buttonText1: 'left',
      buttonText2: 'right',
      eventData: null,
      eventName: ''
    }
  },

  computed: {
    enableButtonText () {
      return (this.enabledButtonsText) ? [this.buttonText1, this.buttonText2] : []
    }
  },

  methods: {
    changeRightList (itemsRightList) {
      this.eventName = 'on-change-transfer'
      this.eventData = itemsRightList
      console.log(itemsRightList)
    },
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>
<style>
.positionRight {
  display: flex;
  justify-content: flex-end;
}
</style>



