<template>
  <div>
   <title-component 
   :element="titlePage"
   titleClass="is-size-2"
   :isPage="true">
   </title-component>
    <hr>
    <b-field grouped group-multiline>
        <div class="control">
          <b-field label="Title size">
            <b-select placeholder="select the title size" 
            v-model="titleSizeOption"
            rounded>
              <option v-for="size in 7" :value="size">{{size}}</option>
            </b-select>
          </b-field>
        </div>
        <div class="control">
          <b-field label="Title align">
            <b-select placeholder="select the title align" 
            v-model="titleAlign"
            rounded>
             <option
             v-for="(align,key) in titleAlings"
             :key="key">
               {{align}}
             </option>
            </b-select>
          </b-field>
        </div>

         <div class="control">
          <b-field label="Title transformation">
            <b-select placeholder="select the transformationt title" 
            v-model="titleTransformation"
            rounded>
              <option
              v-for="(transformation,key) in titleTransformations"
              :key="key">
                {{transformation}}
              </option>
            </b-select>
          </b-field>
        </div>

        <div class="control">
          <b-field label="Header type">
            <b-select placeholder="select the header type" 
            v-model="frameType"
            rounded>
             <option 
             v-for="(header,key) in headerTypes"
             :key="key">
               {{header}}
             </option>
            </b-select>
          </b-field>
        </div>
       
        <div class="control">
          <b-field label="header color">
            <b-select placeholder="select the header color" 
            v-model="headerColor"
            rounded>
              <option 
              v-for="(color,key) in headerColors"
              :key="key">
                {{color}}
              </option>
            </b-select>
          </b-field>
        </div>

        <div class="control">
          <b-field label="Content align">
            <b-select placeholder="select the content align" 
            v-model="contentAlign"
            rounded>
              <option 
              v-for="(align,key) in contentAligns"
              :key="key">
                {{align}}
              </option>
            </b-select>
          </b-field>
        </div>

        <div class="control">
          <b-field label="Title weight">
            <b-select placeholder="select the weight title" 
            v-model="titleWeight"
            rounded>
              <option 
              v-for="(weight,key) in titleWeights"
              :key="key">
                {{weight}}
              </option>
            </b-select>
          </b-field>
        </div>
         <div class="control">
          <b-field label="Title color">
            <b-select placeholder="select the title color" 
            v-model="titleColor"
            rounded>
              <option 
              v-for="(color,key) in titleColors"
              :key="key">
                {{color}}
              </option>
            </b-select>
          </b-field>
        </div>

        <div class="control">
          <b-field label="Content color">
            <b-input 
            type="color"
            v-model="colorContent" 
            rounded>
            </b-input>
          </b-field>
        </div>

        <b-switch v-model="withoutHeader">header</b-switch>
      </b-field>

     <div class="column is-4 is-offset-4">
      <ui-frame
        :headerType= "frameType"
        :withoutHeader ="withoutHeader"
        :headerColor= "headerColor"
        :configurationTitle= "dynamicTitleConfiguration"
        :contentAlign="contentAlign"
        :contentColor='colorContent'
        >

        <template slot="title">
          UI Frame
        </template>       
          
        <template slot="content">
          Lorem ipsum dolor sit amet, consectetur adipiscing elit. <strong>Pellentesque risus mi</strong>, tempus quis placerat ut, porta nec nulla. Vestibulum rhoncus ac ex sit amet fringilla. Nullam gravida purus diam, et dictum <a>felis venenatis</a> efficitur. Aenean ac <em>eleifend lacus</em>, in mollis lectus. Donec sodales, arcu et sollicitudin porttitor, tortor urna tempor ligula, id porttitor mi magna a neque. Donec dui urna, vehicula et sem eget, facilisis sodales sem.
        </template>
       
      </ui-frame>
    </div>
    <hr>
    <div class="positionRight">
        <b-tooltip
        label="Changelog"
        :active="true"
        position="is-left"
        type="is-dark">
          <ui-panel-container
            :panelHeight= "200"
            :panelWidth= "600"
             unitHeight="px"
             unitWidth="px"
            backgroundColor="white"
            panelBorder="ridge"
            panelBorderColor="black"
            :collapseButtonEnabled="true"
            collapseButtonPosition="left"
            collapseButtonBackgroundColor="#C2C2C2"
            :style="collapseLeft"
            @on-collapse="collapsePanelLeft">
              <markdown></markdown>
            </ui-panel-container>
        </b-tooltip>
      </div>
  </div>
</template>

<script>
import UIFrame from '../../../components/common/UIFrame/index.js'
import UIContainer from '../../../components/common/UIContainer/index.js'
import { updates } from '../../../libraryUpdates.js'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import readme from '../../../components/common/UIFrame/changelog.md'
import titleComponent from '../titleComponent.vue'
export default {

  name: 'UiFramePage',

  components: {
    'UiFrame': UIFrame,
    'UiContainer': UIContainer,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },

  data () {
    return {
      /**
       * Version data
       */
      titlePage: updates.Frame,
      collapseLeft: {},
      /*
      Properties of the <ui-title>
       */
      configurationTitle: {
        color: 'black',
        weight: 'normal',
        align: 'left',
        size: 1,
        transformation: 'italic'
      },

      /*
        properties of the <ui-frame>
       */
      title: 'add',
      titleSizeOption: 1,
      titleAlign: 'right',
      frameType: 'header-off',
      withoutHeader: true,
      titleColor: 'black',
      headerColor: 'dark',
      titleTransformation: 'italic',
      contentAlign: 'justified',
      titleWeight: 'bold',
      colorContent: '#070404'
    }
  },

  computed: {

    dynamicTitleConfiguration () {
      let vm = this
      let configuration = {
        color: vm.titleColor,
        weight: vm.titleWeight,
        size: vm.titleSizeOption,
        transformation: vm.titleTransformation,
        align: vm.titleAlign
      }

      return configuration
    },

    titleColors () {
      let colors = [
        'white',
        'black',
        'light',
        'dark',
        'primary',
        'info',
        'link',
        'success',
        'warning',
        'danger',
        'black-bis',
        'black-ter',
        'grey-dark',
        'grey-darker',
        'grey',
        'grey-light',
        'grey-lighter',
        'white-ter',
        'white-bis'
      ]

      return colors
    },

    titleWeights () {
      let weights = [
        'light',
        'normal',
        'semibold',
        'bold'
      ]

      return weights
    },

    headerColors () {
      let colors = [
        'primary',
        'dark',
        'success',
        'warning',
        'danger',
        'info',
        'link'
      ]

      return colors
    },

    titleTransformations () {
      let transformations = [
        'italic',
        'capitalized',
        'uppercase',
        'lowercase',
        'italic capitalized',
        'italic uppercase',
        'italic lowercase'
      ]

      return transformations
    },

    contentAligns () {
      let aligns = [
        'right',
        'centered',
        'left',
        'justified'
      ]

      return aligns
    },

    titleAlings () {
      let aligns = [
        'left',
        'centered',
        'right'
      ]

      return aligns
    },

    headerTypes () {
      let headers = [
        'header-on',
        'header-off'
      ]

      return headers
    }

  },
  methods: {
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>
<style>
.positionRight {
  display: flex;
  justify-content: flex-end;
}
</style>




