<template>
  <div>
   <title-component 
   :element="titlePage"
   titleClass="is-size-2"
   :isPage="true">
   </title-component>
    <hr>
    <h3 class = "is-size-6 has-text-left">Search</h3>
    <ui-generic-search
      v-model = "query1"
      :entity = "entity"
      :paramsToSee = "paramsToSee"
      :identifiers = "identifiers">
    </ui-generic-search>
    <br>
    <h3 class = "is-size-6 has-text-left">Autocomplete Function</h3>
    <b-switch v-model="keepFirst">Keep First</b-switch>
    <ui-generic-search
      v-model = "query2"
      :entity = "entity"
      :paramsToSee = "paramsToSee"
      :identifiers = "identifiers"
      :keepFirst = "keepFirst"
      autocomplete>
    </ui-generic-search>
    <hr>
    <div class="positionRight">
        <b-tooltip
        label="Changelog"
        :active="true"
        position="is-left"
        type="is-dark">
          <ui-panel-container
            :panelHeight= "150"
            :panelWidth= "600"
             unitHeight="px"
             unitWidth="px"
            backgroundColor="white"
            panelBorder="ridge"
            panelBorderColor="black"
            :collapseButtonEnabled="true"
            collapseButtonPosition="left"
            collapseButtonBackgroundColor="#C2C2C2"
            :style="collapseLeft"
            @on-collapse="collapsePanelLeft">
              <markdown></markdown>
            </ui-panel-container>
        </b-tooltip>
      </div>
  </div>
</template>

<script>
import UIGenericSearch from '../../../components/common/UIGenericSearch/index.js'
import { updates } from '../../../libraryUpdates.js'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import readme from '../../../components/common/UIGenericSearch/changelog.md'
import titleComponent from '../titleComponent.vue'
export default {
  name: 'UIGenericSearchPage',

  components: {
    'UiGenericSearch': UIGenericSearch,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },

  data () {
    return {
      query1: '',
      query2: '',
      entity: 'AREA',
      paramsToSee: ['areaId', 'areaType', 'preferredName'],
      identifiers: ['areaId', 'areaType'],
      keepFirst: true,
      /**
       * version data
       */
      titlePage: updates.GenericSearch,
      collapseLeft: {}
    }
  },
  methods: {
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>

<style lang="css">

.positionRight {
  display: flex;
  justify-content: flex-end;
}

</style>



