<template>
<div>
   <title-component 
   :element="titlePage"
   titleClass="is-size-2"
   :isPage="true">
   </title-component>
   <hr>
  <div class="columns">
    <div class="column">
      <b-switch v-model="props.isOpen">Collapse {{itsOpenLabel}}</b-switch>
    </div>
    <div class="column">
      <b-switch v-model="props.iconCloseable">Icon closeable</b-switch>
    </div>
    <div class="column">
      <div class="columns">
        <div class="column">
          <b-select v-model="props.sizeCloseButton">
            <option
            v-for="value in ['is-small', 'is-medium', 'is-large']"
            :value="value">
            {{value}}
          </option>
        </b-select>
        </div>
        <div class="column">
          <label>
            Close Button Size
          </label>
        </div>
      </div>
    </div>
    <div class="column">
      <div class="columns">
        <div class="column">
          <b-select v-model="headerSize">
            <option
            v-for="size in 7"
            :value="size">
            {{size}}
          </option>
        </b-select>
        </div>
        <div class="column">
          <label>
            Header Size
          </label>
        </div>
      </div>
    </div>
    <div class="column">
      <div class="columns">
        <div class="column">
          <b-select v-model="headerPlace">
            <option
            v-for="place in ['centered', 'justified', 'left', 'right']"
            :value="place">
            {{place}}
          </option>
        </b-select>
        </div>
        <div class="column">
          <label>
            Header Align
          </label>
        </div>
      </div>
    </div>
    <div class="column">
      <div class="columns">
        <div class="column">
          <b-select v-model="headerStyle">
            <option
            v-for="style in ['capitalized', 'lowercase', 'uppercase', 'italic']"
            :value="style">
            {{style}}
            </option>
          </b-select>
        </div>
        <div class="column">
          <label>
            Header Style
          </label>
        </div>
      </div>
    </div>
  </div>
  <ui-management-panel-show-hide-columns
    v-model="config"
    v-bind="computedProps">
  </ui-management-panel-show-hide-columns>
      <hr>
      <div class="positionRight">
        <b-tooltip
        label="Changelog"
        :active="true"
        position="is-left"
        type="is-dark">
          <ui-panel-container
            :panelHeight= "200"
            :panelWidth= "600"
             unitHeight="px"
             unitWidth="px"
            backgroundColor="white"
            panelBorder="ridge"
            panelBorderColor="black"
            :collapseButtonEnabled="true"
            collapseButtonPosition="left"
            collapseButtonBackgroundColor="#C2C2C2"
            :style="collapseLeft"
            @on-collapse="collapsePanelLeft">
              <markdown></markdown>
            </ui-panel-container>
        </b-tooltip>
      </div>
</div>
</template>

<script>
import UIManagementPanelShowHideColumns from '../../../components/common/UIManagementPanelShowHideColumns/index.js'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import titleComponent from '../titleComponent.vue'
import { updates } from '../../../libraryUpdates.js'
import readme from '../../../components/common/UIManagementPanelShowHideColumns/changelog.md'

export default {
  name: 'UiManagementPanelShowHideColumnsPage',

  components: {
    UiManagementPanelShowHideColumns: UIManagementPanelShowHideColumns,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },

  data () {
    return {
      titlePage: updates.ManagementPanelShowHideColumns,
      collapseLeft: {},
      headerSize: 3,
      headerPlace: 'centered',
      headerStyle: 'uppercase',
      /**
       * Config struture for the entities used in v-model
       * @type {Object}
       */
      config: [
        {
          classTag: 'class1',
          children: [
            {
              propTag: 'id.areaId',
              tabulatedData: {
                type: 'string',
                visibleOnTable: true,
                visibleCheckBox: true,
                sortable: true,
                centered: true,
                classes: ''
              }
            },
            {
              propTag: 'id.areaType',
              tabulatedData: {
                type: 'string',
                visibleOnTable: true,
                visibleCheckBox: true,
                sortable: true,
                centered: true,
                classes: ''
              }
            }
          ]
        },
        {
          classTag: 'class2',
          children: [
            {
              propTag: 'preferredName',
              tabulatedData: {
                type: 'string',
                visibleOnTable: true,
                visibleCheckBox: true,
                sortable: true,
                centered: true,
                classes: ''
              }
            }
          ]
        }
      ],

      /**
       * And object with the properties of the component, to make the html looks more clean
       * @type {Object}
       */
      props: {
        entity: 'AREA',
        icon: 'fa fa-chevron-down',
        isOpen: true,
        sizeCloseButton: 'is-medium',
        headerClasses: 'is-size-1'
      }
    }
  },

  computed: {
    itsOpenLabel () {
      return (this.props.isOpen) ? `Open` : `Close`
    },
    computedProps () {
      let finalProps = this.props
      finalProps['headerClasses'] = `is-size-${this.headerSize} has-text-${this.headerPlace} is-${this.headerStyle}`
      return finalProps
    }
  },
  methods: {
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>

<style lang="css" scoped>
.positionRight {
  display: flex;
  justify-content: flex-end;
}
</style>


