<script>
import UISearchSelect from '../UISearchSelect'
import UIUnitsOfMeasure from '../../inputs/UIUnitsOfMeasure'
import UIContainer from '../UIContainer/index'
import UIToolBar from '../UIToolBar/index'
import debounce from 'lodash/debounce'
/**
 * Should create dinamicly the form used on the management panel for the edition of
 * a register, with all the components involved
 * @vue UIManagementPanelEdition.vue
 * @version 0.1.0
 * @since 0.1.0
 * @author Cruz Ortiz [gitlab:@cortiz99]
 * @copyright Karanta, CA. 2018
 */
export default {
  name: 'UiManagementPanelEdition',

  components: {
    'UiSearchSelect': UISearchSelect,
    'UiUnitsOfMeasure': UIUnitsOfMeasure,
    'UiContainer': UIContainer,
    'UiToolbar': UIToolBar
  },

  model: {
    prop: 'value',
    event: 'edition'
  },

  props: {
    /**
     * Config structures used to build the formulary
     * @prop {Array} value
     */
    value: {
      type: Array,
      required: true
    },

    /**
     * Represents the data of the panel
     * @prop {Object} data
     */
    data: {
      type: Object,
      required: true
    },

    /**
     * Represents the entity or model represented by the component
     * @prop {String} entity
     */
    entity: {
      type: String,
      required: true
    },

    /**
     * Sets the preview or the edition of the data
     * @prop {Boolean} [edition = false]
     */
    edition: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * Sets the classes of the header
     * @prop {String} [headerClass = 'has-text-centered is-size-3 is-uppercase']
     */
    headerClass: {
      type: String,
      required: false,
      default: 'has-text-centered is-size-3 is-uppercase'
    },

    /**
     * Changes the position of  the labels of the inputs
     * @prop {Boolean} [horizontal = false]
     */
    horizontal: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * Shows the collapse open or close
     * @prop {Boolean} [isOpen = true]
     */
    isOpen: {
      type: Boolean,
      required: false,
      default: true
    },

    /**
     * Represents the icon on the header
     * @prop {String} [headerIcon = null]
     */
    headerIcon: {
      type: String,
      required: false,
      default: null
    },

    /**
    * Shows a loading state while it's wating for the petition response
    * @prop {Boolean} [loading = false]
    */
    loading: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * Sets the align of the toolbar in the edition
     * @prop {String} [toolbarBottomAlign = 'center']
     */
    toolbarBottomAlign: {
      type: String,
      required: false,
      default: 'center'
    },

    /**
     * Sets the configurations of the buttons on the bottom toolbar
     * @prop {Array} [toolbarBottomButtonConfigurations = [{
       buttonType: 'save',
       visibleLabel: true,
       buttonClass: 'is-primary',
       iconType: 'fa fa-save',
       buttonSize: 'is-medium',
       visibleIcon: true,
       isRounded: false,
       visibleToolTip: false
     },
     {
       buttonType: 'restore',
       visibleLabel: true,
       buttonClass: 'is-info',
       iconType: 'fa fa-sync-alt',
       buttonSize: 'is-medium',
       visibleIcon: true,
       isRounded: false,
       visibleToolTip: false
     },
     {
       buttonType: 'cancel',
       visibleLabel: true,
       buttonClass: 'is-danger',
       iconType: 'fa fa-ban',
       buttonSize: 'is-medium',
       visibleIcon: true,
       isRounded: false,
       visibleToolTip: false
     }]]
     */
    toolbarBottomButtonConfigurations: {
      type: Array,
      required: false,
      default: function () {
        return [{
          buttonType: 'save',
          visibleLabel: true,
          buttonClass: 'is-primary',
          iconType: 'fa fa-save',
          buttonSize: 'is-medium',
          visibleIcon: true,
          isRounded: false,
          visibleToolTip: false
        },
        {
          buttonType: 'restore',
          visibleLabel: true,
          buttonClass: 'is-info',
          iconType: 'fa fa-sync-alt',
          buttonSize: 'is-medium',
          visibleIcon: true,
          isRounded: false,
          visibleToolTip: false
        },
        {
          buttonType: 'cancel',
          visibleLabel: true,
          buttonClass: 'is-danger',
          iconType: 'fa fa-ban',
          buttonSize: 'is-medium',
          visibleIcon: true,
          isRounded: false,
          visibleToolTip: false
        },
        {
          buttonType: 'delete',
          visibleLabel: true,
          buttonClass: 'is-danger',
          iconType: 'fa fa-ban',
          buttonSize: 'is-medium',
          visibleIcon: true,
          isRounded: false,
          visibleToolTip: false
        }]
      }
    },

    /**
     * Sets the gap on the toolbar
     * @prop {String} [gapToolBar = 'is-1']
     */
    gapToolBar: {
      type: String,
      required: false,
      default: 'is-1'
    },

    /**
     * Sets the align of the toolbar in the preview
     * @prop {String} [toolbarTopAlign = 'right']
     */
    toolbarTopAlign: {
      type: String,
      required: false,
      default: 'right'
    },

    /**
     * Sets the configuration of the buttons of the toolbar
     * @prop {Array} [toolbarTopButtonConfigurations = [{
       buttonType: 'edition',
       visibleLabel: true,
       buttonClass: 'is-primary',
       iconType: 'fas fa-pencil-alt',
       buttonSize: 'is-medium',
       visibleIcon: true,
       isRounded: false,
       visibleToolTip: true,
       positionToolTip: 'is-top',
       typeToolTip: 'is-info',
       sizeToolTip: 'is-medium',
       multilinedToolTip: true
     }]]
     */
    toolbarTopButtonConfigurations: {
      type: Array,
      required: false,
      default: function () {
        return [{
          buttonType: 'edition',
          visibleLabel: true,
          buttonClass: 'is-primary',
          iconType: 'fas fa-pencil-alt',
          buttonSize: 'is-medium',
          visibleIcon: true,
          isRounded: false,
          visibleToolTip: true,
          positionToolTip: 'is-top',
          typeToolTip: 'is-info',
          sizeToolTip: 'is-medium',
          multilinedToolTip: true
        },
        {
          buttonType: 'delete',
          visibleLabel: true,
          buttonClass: 'is-danger',
          iconType: 'fa fa-ban',
          buttonSize: 'is-medium',
          visibleIcon: true,
          isRounded: false,
          visibleToolTip: false
        }]
      }
    },

    /**
     * Sets the size of the close button
     * @prop {String} [sizeCloseButton = 'is-medium']
     */
    sizeCloseButton: {
      type: String,
      required: false,
      default: 'is-medium'
    }
  },

  data () {
    return {
      /**
      * Represents the object received from the petition
      * @type {Object}
      */
      incommingObject: {},

      /**
       * Represents a flag to handle the loading state
       * @type {Object}
       */
      isLoading: null,

      /**
       * Represents the structure of the edition form
       * @type {Object}
       */
      objectToBind: {},

      /**
       * Represents the model to with the data to save
       * @type {Object}
       */
      outgoingObject: {}

    }
  },

  computed: {
    /**
     * Wath the errors on the form and activate and deactivate the save button
     * @return {Arrys} Configurations of the buttons on the bottom toolbar
     */
    computedToolbarBottomButtonConfigurations () {
      let configuration = this.toolbarBottomButtonConfigurations
      let saveIndex = configuration.findIndex((element) => {
        return element.buttonType === 'save'
      })
      configuration[saveIndex]['disabled'] = (this.errors.items.length !== 0)
      return configuration
    }
  },

  watch: {
    /**
     * Watching the edition state of the component
     */
    edition () {
      this.objectToBindCreator(this.value, this.incommingObject, this.objectToBind)
    },

    /**
     * Watching the loading state of the component
     */
    loading () {
      this.isLoading = this.loading
    }
  },

  created () {
    this.incommingObjectCreator(this.data, this.incommingObject)
    this.objectToBindCreator(this.value, this.incommingObject, this.objectToBind)
  },

  methods: {
    /**
     * Renders the form part of the component
     * @param  {Function} createElement Represents the render function
     * @return {Function} A render function with the components to show
     * @public
     */
    bodyRender (createElement) {
      let self = this
      return self.value.map(function (clasification) {
        // Building the clasification in the editon form
        // B-Collapse
        return createElement('b-collapse', {
          class: {
            card: true
          },
          props: {
            open: self.isOpen
          }
        },
          [
            // Card header
            createElement('div', {
              class: {
                'card-header': true
              },
              slot: 'trigger'
            },
              [
                // Title
                createElement('p', {
                  class: {
                    'card-header-title': true
                  }
                },
              self.entityTranslate(clasification['classTag'], self.entity)),
              // Header icon
                createElement('a', {
                  class: {
                    'card-header-icon': true
                  }
                },
                  [
                    createElement('b-icon', {
                      props: {
                        icon: 'fa fa-chevron-down'
                      }
                    })
                  ])
              ]),
              // Car content
            createElement('div', {
              class: {
                'card-content': true
              }
            },
              [
                // Content
                createElement('div', {
                  class: {
                    'content': true
                  }
                },
                  [
                    clasification['children'].map(function (prop, classIndex) {
                    // Filter the name of the propTag used in the value
                      let tag = self.filterPropName(prop['propTag'])
                      if (prop['edition'] && (prop['edition']['editable'] || prop['edition']['preview'])) {
                        prop['edition']['component']['props']['value'] = self.objectToBind[tag]
                        return createElement('b-field', {
                          style: {
                            display: ((self.edition && prop['edition']['editable']) || (!self.edition && prop['edition']['preview'])) ? '' : 'none'
                          },
                          props: {
                            // Label
                            label: (prop['edition']['visibleLabel']) ? self.translateLabelEntities(prop['propTag']) : null,
                            type: self.errors.has(`${tag}`) ? 'is-danger' : null,
                            message: self.errors.has(`${tag}`) ? self.errorMessage(prop['propTag']) : null,
                            horizontal: self.horizontal
                          },
                          key: classIndex
                        },
                          [
                          // Form component
                            createElement(`${prop['edition']['component']['name']}`, {
                              props: {
                                ...prop['edition']['component']['props'],
                                value: self.objectToBind[tag],
                                disabled: !(self.edition)
                              },
                              attrs: {
                                ...prop['edition']['component']['attrs'],
                                name: tag,
                                disabled: !(self.edition)
                              },
                              directives: (function () {
                                if (prop['edition']['component']['directives']) {
                                  let directives = [
                                    ...prop['edition']['component']['directives']
                                  ]
                                  return directives
                                }
                              })(),
                              on: {
                                input: function (val) {
                                  if (Array.isArray(val)) {
                                    self.childEventEmitterDebounced('input', val)
                                  } else {
                                    self.objectToBind[tag] = val
                                  }
                                },
                                select: function (val) {
                                  self.objectToBind[tag] = val
                                },
                                'on-measure-change': function (val) {
                                  self.objectToBind[tag] = val
                                }
                              },
                              ref: tag
                            })
                          ])
                      }
                    })
                  ])
              ])
          ])
      })
    },

    /**
    * This method send event when validations fail
    */
    validateEdition (edition) {
      /**
      * Notifies validation
      * @event edition-invalidated
      */
      this.$emit('edition-invalidated')
    },

    /**
    * Renders the bottom Toolbar of the component
    * @param  {Function} createElement Represents the render function
    * @return {Function} A render function with the components to show
    * @public
    */
    bottomToolbarRender (createElement) {
      let self = this
      return createElement('ui-container', {
        style: {
          display: (self.edition) ? '' : 'none'
        },
        props: {
          closeable: false,
          align: self.toolbarBottomAlign
        }
      },
        [
          createElement('ui-toolbar', {
            props: {
              buttonConfigurations: self.computedToolbarBottomButtonConfigurations,
              gapContentToolBar: self.gapToolBar
            },
            on: {
              click: function (event) {
                switch (event) {
                  case 'cancel':
                    self.changeView()
                    break
                  case 'save':
                    self.$validator.validateAll()
                      .then((result) => {
                        if (result) {
                          self.save()
                        } else {
                          self.validateEdition(self.edition)
                        }
                      })
                    break
                  case 'restore':
                    self.restore()
                    break
                  default:
                    self.bottomDefaultButton(event)
                }
              }
            }
          })
        ])
    },

    /**
    * Changes betwen preview and edition
    * @public
    */
    changeView () {
      /**
       * Changes betwen preview and edition
       * @event update:edition
       * @type {Boolean}
       */
      this.$emit('update:edition', !this.edition)
    },

    /**
     * Repeats the events of some children
     * @param  {String} eventName Name of the event
     * @param  {Event} event Payload of the event
     * @public
     */
    childEventEmitterDebounced: debounce(function (eventName, event) {
      /**
       * Payload of the event
       * @event  input
       * @type {Array}
       */
      this.$emit(eventName, event)
    }, 500),

    /**
     * Translates according with the entity messages
     * @param  {String} entityProp Point to the the property tha contains the tranlated string
     * @param  {String} entity     Entity or model represented by the component
     * @return {String}            The string with the translated message
     * @public
     */
    entityTranslate (entityProp, entity) {
      return this.$i18n.t(`entities.${entity.toUpperCase()}.${entityProp}.label`)
    },

    /**
     * Filters the name of the prop in the value prop
     * @param  {String} propName Represents the prop tag
     * @return {String} A string with the rigth prop
     * @public
     */
    filterPropName (propName) {
      let stringWithOutPoint = propName.split('.')
      if (stringWithOutPoint[1]) {
        return stringWithOutPoint[1]
      } else {
        return stringWithOutPoint[0]
      }
    },

    /**
    * Renders the header of the comoponent
    * @param  {Function} createElement Represents the render function
    * @return {Function}               A render function with the components to show
    * @public
    */
    headerRender (createElement) {
      let self = this
      return createElement('ui-container', {
        props: {
          closeable: false,
          align: 'none',
          sizeCloseButton: self.sizeCloseButton
        }
      },
        [
          createElement('div', {
            class: (function () {
              let headerClass = {}
              headerClass[self.headerClass] = true
              return headerClass
            })()
          },
            [
              createElement('b-icon', {
                props: {
                  icon: self.headerIcon
                }
              }),
              createElement('div', {
                style: {
                  display: 'inline-block',
                  margin: '0px 10px'
                }
              },
                [
                  self.value.map(function (clasification) {
                    return clasification['children'].map(function (prop) {
                      let tag = self.filterPropName(prop['propTag'])
                      if (prop['edition'] && prop['edition']['header']) {
                        return createElement('h3', [self.incommingObject[tag]])
                      }
                    })
                  })
                ])
            ])
        ])
    },

    /**
    * Build an object according with the structure that can be represented on the view
    * @param  {Object} objectToTransform represent the object that will be transform
    * @param {Object} objectTransformed Represent the object where structure will be build
    * @public
    */
    incommingObjectCreator (objectToTransform, objectTransformed) {
      for (let item in objectToTransform) {
        if (typeof (objectToTransform[item]) === 'object' && objectToTransform[item] !== null) {
          this.incommingObjectCreator(objectToTransform[item], objectTransformed)
        } else {
          objectTransformed[item] = objectToTransform[item]
        }
      }
    },

    /**
    * Builds the object with an structure according with the components to be implement
    * @param  {Array} value            It's the value prop of the component
    * @param  {Object} objectToTransform It's the object with the values but not the structure
    * @param  {Object} objectTransformed Represents the output object with the structure
    * @public
    */
    objectToBindCreator (value, objectToTransform, objectTransformed) {
      let self = this
      value.map(function (clasification) {
        return clasification['children'].map(function (prop) {
          // Filter the name of the propTag used in the value
          let tag = self.filterPropName(prop['propTag'])
          // used to determinate when to put the original data and when to do
          // the transformation
          let flag = false
          if (prop['edition'] && prop['edition']['type'].toUpperCase() === 'OBJECT') {
            objectTransformed[tag] = objectTransformed[tag] || {}
            for (let item of prop['edition']['group']) {
              objectTransformed[tag][item] = objectToTransform[item]
            }
            flag = true
          }
          if (prop['edition'] && prop['edition']['type'].toUpperCase() === 'DATE') {
            if (objectToTransform[tag] !== null) {
              let date = new Date(objectToTransform[tag])
              objectTransformed[tag] = date
            }
            flag = true
          }
          if (!flag) {
            objectTransformed[tag] = objectToTransform[tag]
          }
        })
      })
    },

    /**
    * Builds the object according with the structure spected for the backend
    * @param  {Array} value It's the value prop of the component
    * @param  {Object} objectToTransform It's the object with the values but not the structure
    * @param  {Object} objectTransformed Represents the output object with the structure
    * @public
    */
    outgoingObjectCreator (value, objectToTransform, objectTransformed) {
      let self = this
      value.map(function (clasification) {
        return clasification['children'].map(function (prop) {
          // Filter the name of the propTag used in the value
          let tag = self.filterPropName(prop['propTag'])
          // used to determinate when to put the original data and when to do
          // the transformation
          let flag = false
          // for identifiers, build the object id
          if (prop['edition'] && prop['edition']['type'].toUpperCase() === 'ID') {
            objectTransformed['id'] = objectTransformed['id'] || {}
            if (objectToTransform[tag]) {
              objectTransformed['id'][tag] = objectToTransform[tag]
            }
            flag = true
          }
          // for components with object binding, sets the corresponding strucutre
          if (prop['edition'] && prop['edition']['type'].toUpperCase() === 'OBJECT') {
            for (let item in objectToTransform[tag]) {
              objectTransformed[item] = objectToTransform[tag][item]
            }
            flag = true
          }
          if (!flag) {
            objectTransformed[tag] = objectToTransform[tag]
          }
        })
      })
    },

    /**
     * Reload the saved data
     * @public
     */
    restore () {
      this.isLoading = true
      this.incommingObjectCreator(this.data, this.incommingObject)
      this.objectToBindCreator(this.value, this.incommingObject, this.objectToBind)
      setTimeout(() => {
        this.isLoading = false
      })
    },

    /**
     * Sends the data to be saved
     * @public
     */
    save () {
      this.outgoingObjectCreator(this.value, this.objectToBind, this.outgoingObject)
      /**
       * Sends the data to be saved
       * @event save
       * @type {Array}
       */
      this.$emit('save', [this.outgoingObject, this.objectToBind])
      this.changeView()
    },
    /**
    * This method is up when the toolbar bottom default-Button is clicked
    * @param {String}
    * @returns {String}
    * @public
    */
    bottomDefaultButton (event) {
      /**
       * This event sends the toolbar bottom default-Button data
       * @event default-button-bottom
       * @type {String}
       */
      this.$emit('default-button-bottom', event)
    },
    /**
    * This method is up when the toolbar top default-Button is clicked
    * @param {String}
    * @returns {String}
    * @public
    */
    topDefaultButton (event) {
      /**
       * This event sends the toolbar top default-Button data
       * @event default-button-top
       * @type {String}
       */
      this.$emit('default-button-top', event)
    },
    /**
    * Translates according with the templates messages
    * @param  {String} templateProp Point to the the property tha contains the tranlated string
    * @return {String}              The string with the translated message
    * @public
    */
    translateTemplate (templateProp) {
      return this.$i18n.t(`template.${templateProp}.label`)
    },
    /**
     * This methods sends error message
     * @param {String} tag field name
     * @returns {String} error message
     */
    errorMessage (tag) {
      let translatedTag = this.translateLabelEntities(tag)
      return `${translatedTag} es invalida.`
    },
    /**
     * Translates prop of the other entities
     * @param {String} propTag label to translate
     */
    translateLabelEntities (propTag) {
      let label = propTag.split('.')
      let length = label.length
      if (length > 1) {
        label[0] = label[0].toUpperCase()
        if (label[0] !== 'ID') {
          return this.$i18n.t(`entities.${label[0]}.${label[1]}.label`)
        } else {
          return this.$i18n.t(`entities.${propTag}.label`)
        }
      } else {
        return this.$i18n.t(`entities.${this.entity.toUpperCase()}.${propTag}.label`)
      }
    },

    /**
    * Renders the top Toolbar of the component
    * @param  {Function} createElement Represents the render function
    * @return {Function} A render function with the components to show
    * @public
    */
    topToolbarRender (createElement) {
      let self = this
      return createElement('ui-container', {
        style: {
          display: (self.edition) ? 'none' : ''
        },
        props: {
          closeable: false,
          align: self.toolbarTopAlign
        }
      },
        [
          createElement('ui-toolbar', {
            props: {
              buttonConfigurations: self.toolbarTopButtonConfigurations
            },
            on: {
              click: function (event) {
                switch (event) {
                  case 'edition':
                    self.changeView()
                    break
                  default:
                    self.topDefaultButton(event)
                }
              }
            }
          })
        ])
    }
  },

  render: function (createElement) {
    let self = this
    // Should show a loader while the data it's loading
    // Loader
    if (self.isLoading) {
      return createElement('b-loading', {
        props: {
          active: true,
          'is-full-page': false
        }
      })
    } else {
      return createElement('div',
        [
        // Header
          self.headerRender(createElement),
        // Top Toolbar
          self.topToolbarRender(createElement),
          createElement('br'),
        // Body Render
          self.bodyRender(createElement),
          createElement('br'),
        // Bottom Toolbar
          self.bottomToolbarRender(createElement)
        ])
    }
  }
}
</script>

<style>

</style>



