<template>
  <div id="app">
    <section class="section">
    <div class="columns">
      <div class="column is-10">
        <h1 class="is-size-1">KPUI Project</h1>
        <h2 class="is-size-3">KPS User Interface Library</h2>
      </div>
      <div class="column">
        <b-field>
          <b-select
          v-model="locale" 
          placeholder="Language" 
          icon="globe"
          rounded>
            <option value="es-ES">es-ES</option>
            <option value="en-US">en-US</option>
            <option value="pt-PT">pt-PT</option>
          </b-select>
        </b-field>
      </div>
    </div>
    <br>
    <nav class="breadcrumb" aria-label="breadcrumbs">
      <ul>
        <router-link :to="{name: 'home'}" tag="li"><a>Home</a></router-link>
      </ul>
    </nav>
    <router-view></router-view>
    </section>
  </div>
</template>

<script>

export default {
  name: 'app',
  components: {

  },

  data () {
    return {
      locale: 'es-ES'
    }
  },

  watch: {
    locale (val) {
      this.$i18n.locale = val
    }
  }
}
</script>

<style lang="scss">
  // Import Bulma's core
  @import "~bulma/sass/utilities/_all";

   //Import Custom Library Color
  @import "assets/css/colors.scss";


  // Import Bulma and Buefy styles
  @import "~bulma";
  @import "~buefy/src/scss/buefy";

  #app {
    @import url('https://fonts.googleapis.com/css?family=Montserrat');
    @import url('https://fonts.googleapis.com/css?family=Open+Sans');

    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    margin-top: 60px;

    h1, h1, h2, h2, h3, h3, h4, h4, h5, h5, h6, h7 {
      font-family: 'Montserrat', sans-serif;
      -webkit-font-smoothing: antialiased;
    }
}

/****** Import Element-UI style ******/

/* Color del tema */
$--color-primary: teal;

/* Ubicacin de la fuente, obligatoria */
$--font-path: '~element-ui/lib/theme-chalk/fonts';

@import "~element-ui/packages/theme-chalk/src/index";
</style>



