<template>
  <div>
   <title-component 
   :element="titlePage"
   titleClass="is-size-2"
   :isPage="true">
   </title-component>
   <hr>
    <b-field group-multiline>

      <div class="columns">
        <div class="column is-2">
           <b-field label="Transformation title">
              <b-select placeholder="Select the transformation title"
              v-model= "transformation"
              rounded>
                <option value="capitalized">capitalized</option>
                <option value="italic">italic</option>
                <option value="lowercase">lowercase</option>
                <option value="uppercase">uppercase</option>
                <option value="italic capitalized">italic capitalized</option>
                <option value="italic lowercase">italic lowercase</option>
                <option value="italic uppercase">italic uppercase</option>
              </b-select>
            </b-field>
        </div>
       
       <div class="column is-2">
          <b-field label="Color title">
            <b-select placeholder="select the color title" 
            v-model="color"
            rounded>
            <option value="white">white</option>
            <option value="black">black</option>
            <option value="light">light</option>
            <option value="dark">dark</option>
            <option value="primary">primary</option>
            <option value="info">info</option>
            <option value="link">link</option>
            <option value="success">success</option>
            <option value="warning">warning</option>
            <option value="danger">danger</option>
            <option value="black-bis">black-bis</option>
            <option value="black-ter">black-ter</option>
            <option value="grey-darker">grey-darker</option>
            <option value="grey-dark">grey-dark</option>
            <option value="grey">grey</option>
            <option value="grey-light">grey-light</option>
            <option value="grey-lighter">grey-lighter</option>
            <option value="white-ter">white-ter</option>
            <option value="white-bis">white-bis</option>
          </b-select>
        </b-field>
       </div>

       <div class="column is-2">
         <b-field label="Weight title">
           <b-select placeholder="select the weight title"
            rounded
            v-model="weight">
             <option value="light">light</option>
             <option value="normal">normal</option>
             <option value="semibold">semibold</option>
             <option value="bold">bold</option>
           </b-select>
         </b-field>
       </div>

       <div class="column is-2">
         <b-field label="Align title">
           <b-select placeholder="select the align title"
           v-model="align"
           rounded>
            <option value="left">left</option>
            <option value="centered">centered</option>
            <option value="right">right</option>
           </b-select>
         </b-field>
       </div>

       <div class="column is-2">
         <b-field label="Size title">
           <b-select placeholder="Select the size title"
           v-model="size"
           rounded>
             <option v-for="size in 7" :value="size">{{size}}</option>
           </b-select>
         </b-field>
       </div>
       
  </div>
  
  </b-field>
   
   <ui-title
   :title= "title"
   :weight="weight"
   :align="align"
   :size='size'
   :transformation="transformation"
   :color="color">
    
   </ui-title>
   <hr>
  <div class="column is-2">
    <b-field label="Title">
      <b-input placeholder="Introduce text"
      v-model="text"
      rounded></b-input>
    </b-field>
  </div>
    <ui-title
   :weight="weight"
   :align="align"
   :size='size'
   :transformation="transformation"
   :color="color">
      {{text}}
   </ui-title>
    <hr>
      <div class="positionRight">
        <b-tooltip
        label="Changelog"
        :active="true"
        position="is-left"
        type="is-dark">
          <ui-panel-container
            :panelHeight= "200"
            :panelWidth= "600"
             unitHeight="px"
             unitWidth="px"
            backgroundColor="white"
            panelBorder="ridge"
            panelBorderColor="black"
            :collapseButtonEnabled="true"
            collapseButtonPosition="left"
            collapseButtonBackgroundColor="#C2C2C2"
            :style="collapseLeft"
            @on-collapse="collapsePanelLeft">
              <markdown></markdown>
            </ui-panel-container>
        </b-tooltip>
      </div>
  </div>
</template>

<script>

import UITitle from '../../../components/common/UITitle/index.js'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import titleComponent from '../titleComponent.vue'
import { updates } from '../../../libraryUpdates.js'
import readme from '../../../components/common/UITitle/changelog.md'

export default {
  name: 'UiTitlePage',

  components: {
    'UiTitle': UITitle,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },

  data () {
    return {
      /**
       * Version data
       */
      titlePage: updates.Title,
      collapseLeft: {},
      /**
       * data title
       */
      weight: 'bold',
      align: 'centered',
      size: 1,
      transformation: 'italic capitalized',
      color: 'info',
      title: 'add',
      text: 'UI Title'
    }
  },
  methods: {
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>
<style>
.positionRight {
  display: flex;
  justify-content: flex-end;
}
</style>



