<template>
<div>
  <title-component 
   :element="titlePage"
   titleClass="is-size-2"
   :isPage="true">
  </title-component>
  <hr>
  <div class="grid-container">
    <div class="columns">
      <div class="column is-1 is-offset-1">
        <b-field label="Class">
          <b-select placeholder="select a class"
          v-model="buttonType">
            <option value="is-warning">warning</option>
            <option value="is-light">light</option>
            <option value="is-primary">primary</option>
            <option value="is-info">info</option>
            <option value="is-black">black</option>
            <option value="is-dark">dark</option>
            <option value="is-success">success</option>
          </b-select>
        </b-field>
      </div>
      <div class="column is-1">
        <b-field label="Icon class">
          <b-select placeholder="select a class"
          v-model="iconType">
            <option value="is-warning">warning</option>
            <option value="is-light">light</option>
            <option value="is-primary">primary</option>
            <option value="is-info">info</option>
            <option value="is-black">black</option>
            <option value="is-dark">dark</option>
            <option value="is-success">success</option>
          </b-select>
        </b-field>
      </div>
      <div class="column is-2">
        <div class="block">
            <b-checkbox v-model="fileFormat"
                native-value="png">
                Png
            </b-checkbox>
            <b-checkbox v-model="fileFormat"
                native-value="jpeg">
                Jpeg
            </b-checkbox>
            <b-checkbox v-model="fileFormat"
                native-value="csv">
                Csv
            </b-checkbox>
        </div>
      </div>
      <div class="column is-1">
        <b-field label="Visible icon">
          <b-switch v-model="visibleIcon"></b-switch>
        </b-field>
      </div>
      <div class="column is-1">
        <b-field label="Visible label">
          <b-switch v-model="visibleLabel"></b-switch>
        </b-field>
      </div>
      <div class="column is-3 is-offset-1">
        <b-message
          title="Console"
          :closable="false"
        >
          <pre>{{ eventListener }}</pre>
        </b-message>
      </div>
    </div>
    <div class="containerCentered">
      <ui-upload-file
        :fileFormat="fileFormat"
        :buttonType="buttonType"
        :visibleIcon="visibleIcon"
        :visibleLabel="visibleLabel"
        :iconType="iconType"
        @uploadedFile="consoleBox"></ui-upload-file>
    </div>
  </div>
  <hr>
  <div class="positionRight">
    <b-tooltip
    label="Changelog"
    :active="true"
    position="is-left"
    type="is-dark">
      <ui-panel-container
        :panelHeight= "150"
        :panelWidth= "550"
          unitHeight="px"
          unitWidth="px"
        backgroundColor="white"
        panelBorder="ridge"
        panelBorderColor="black"
        :collapseButtonEnabled="true"
        collapseButtonPosition="left"
        collapseButtonBackgroundColor="#C2C2C2"
        :style="collapseLeft"
        @on-collapse="collapsePanelLeft">
          <markdown></markdown>
        </ui-panel-container>
    </b-tooltip>
  </div>
</div>
</template>
<script>
import titleComponent from '../titleComponent.vue'
import UIUploadFile from '../../../components/common/UIUploadFile/index.js'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import { updates } from '../../../libraryUpdates.js'
import readme from '../../../components/common/UIUploadFile/changelog.md'

export default {
  name: 'UiUploadFilePage',
  components: {
    'UiUploadFile': UIUploadFile,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },
  data () {
    return {
      /**
       * version information
       */
      titlePage: updates.Upload,
      /**
       * Class of upload
       */
      buttonType: 'is-black',
      /**
       * Icon type
       */
      iconType: 'is-info',
      /**
       * event data
       */
      eventListener: null,
      /**
       * Sets icon visibility
       */
      visibleIcon: true,
      /***
       * Sets label visibility
       */
      visibleLabel: true,
      /**
       * Panel container styles
       */
      collapseLeft: {},
      /**
       * file format
       */
      fileFormat: ['png', 'jpeg', 'csv']
    }
  },
  methods: {
    /**
     * Print imagen data on console
     * @param {Array}
     */
    consoleBox (data) {
      let objectFile = {
        fileName: data[0]['name'],
        size: data[0]['size'],
        type: data[0]['type']
      }
      this.eventListener = objectFile
    },
    /**
     * Sets panel container left
     * @param {Object}
     */
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>
<style scoped>
  .containerCentered {
    display: flex;
    margin-top: 5%;
    justify-content: center;
  }
  .grid-container {
    display: grid;
    grid-template-columns: auto 2;
  }
  .positionRight {
    display: flex;
    justify-content: flex-end;
  }
</style>




