<template>
<div>
  <div>
  <title-component 
  :element="titlePage"
  titleClass="is-size-2"
  :isPage="true">
  </title-component>
   <hr>
    <div class="columns">
      <div class="column">
        <b-field grouped>
          <b-field
            label="Size"
            expanded
          >
            <b-select
              v-model="size"
              placeholder="Breadcrumb size"
            >
              <option
                v-for="(option, index) in sizeOptions"
                :key="index"
                :value="option.value"
              >{{ option.label }}</option>
            </b-select>
          </b-field>
          <b-field
            label="Icon"
            expanded
          >
            <b-select
              v-model="iconSeparator"
              placeholder="Breadcrumb Icon"
            >
              <option
                v-for="(option, index) in iconOptions"
                :key="index"
                :value="option.value"
              >{{ option.label }}</option>
            </b-select>
          </b-field>
        </b-field>
        <b-field grouped>
          <b-field label="Visible Icon" expanded>
            <b-switch v-model="visibleIcon">{{ visibleIcon }}</b-switch>
          </b-field>
          <b-field label="Router" expanded>
            <b-switch v-model="router">{{ router }}</b-switch>
          </b-field>
        </b-field>
      </div>
      <div class="column">
        <b-message
          title="Console"
          :closable="false"
        >
					<pre>{{ eventListener }}</pre>
				</b-message>
      </div>
    </div>
    <hr>
    <div class="columns">
      <ui-breadcrumb
        :links="links"
        :iconSeparator="iconSeparator"
        :size="size"
        :visibleIcon="visibleIcon"
        :router="router"
        @select="(data) => eventListener = data"
      ></ui-breadcrumb>
    </div>
  </div>
  <hr>
  <div class="positionRight">
    <b-tooltip
      label="Changelog"
      :active="true"
      position="is-left"
      type="is-dark">
        <ui-panel-container
          :panelHeight= "150"
          :panelWidth= "600"
          unitHeight="px"
          unitWidth="px"
          backgroundColor="white"
          panelBorder="ridge"
          panelBorderColor="black"
          :collapseButtonEnabled="true"
          collapseButtonPosition="left"
          collapseButtonBackgroundColor="#C2C2C2"
          :style="collapseLeft"
          @on-collapse="collapsePanelLeft">
            <markdown></markdown>
        </ui-panel-container>
    </b-tooltip>
  </div>
</div>
</template>

<script>
import { updates } from '../../../libraryUpdates.js'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import readme from '../../../components/common/UIBreadcrumb/changelog.md'
import titleComponent from '../titleComponent.vue'
import UiBreadcrumb from '../../../components/common/UIBreadcrumb'

export default {
  components: {
    UiBreadcrumb,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },
  data () {
    return {
      collapseLeft: {},
      titlePage: updates.Breadcrumb,
      eventListener: '',
      links: [
        {
          label: 'RouteLabel1',
          path: 'route1',
          icon: 'fas fa-home'
        },
        {
          label: 'RouteLabel2',
          path: 'route2',
          icon: 'fas fa-book'
        },
        {
          label: 'RouteLabel3',
          path: 'route3',
          icon: 'fas fa-puzzle-piece'
        }
      ],
      iconSeparator: null,
      iconOptions: [
        { label: 'default', value: '' },
        { label: 'arrow', value: 'has-arrow-separator' },
        { label: 'bullet', value: 'has-bullet-separator' },
        { label: 'dot', value: 'has-dot-separator' },
        { label: 'succeeds', value: 'has-succeeds-separator' }
      ],
      size: '',
      sizeOptions: [
        { label: 'small', value: 'is-small' },
        { label: 'default', value: '' },
        { label: 'medium', value: 'is-medium' },
        { label: 'large', value: 'is-large ' }
      ],
      router: false,
      visibleIcon: true
    }
  },
  methods: {
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>

<style>
.positionRight {
  display: flex;
  justify-content: flex-end;
}
</style>



