<template>

<div class="columns" :class="dynamicClassAlign">
  <div class="column is-narrow">
    <div class="columns is-variable" :class = "dynamicClassGap">

        <div
          class="column"
          v-for="(configurations,key) in buttonToShowBeforeDropdown"
          :key="key">
          <ui-button
            :buttonType="configurations.buttonType"
            :visibleLabel="configurations.visibleLabel"
            :iconType="configurations.iconType"
            :icon-pack="configurations.iconPack"
            :visibleIcon="configurations.visibleIcon"
            :buttonClass= "configurations.buttonClass"
            :buttonSize= "configurations.buttonSize"
            :buttonIsRounded="configurations.isRounded"
            :buttonIsLarge="configurations.true"
            :visibleToolTip="configurations.visibleToolTip"
            :positionToolTip="configurations.positionToolTip"
            :toolTipSize="configurations.sizeToolTip"
            :typeToolTip="configurations.typeToolTip"
            :sizeIcon="configurations.sizeIcon"
            :multilinedToolTip="configurations.multilinedToolTip"
            :disabled="configurations.disabled"
            :enableNotification="configurations.enableNotification"
            :visibleNotification.sync="configurations.visibleNotification"
            :addedClass= "configurations.addedClass"
            @click="buttonClicked"
          ></ui-button>
        </div>

<!-- Dropdown with tool tip  -->
      <div class="column">
      <b-tooltip :label="labelContentTooltip"
      :size="toolTipSizeDropDown"
      :position="toolTipPositionDropDown"
      :type="toolTipTypeDropDown"
      :active="visibleToolTip"
      :multilined="multilinedToolTipDropDown">
      <b-dropdown
        v-if= "visibleDropDown"
        :position="positionDropDown"
        :hoverable="hoverable">

          <button class="button"
          :class="dynamicClass"
          slot="trigger">
            <span v-if="visibleLabel">{{labelContent}}</span>
            <b-icon v-if="visibleIcon"
            :icon="iconDropDownType"
            :pack="iconPack"
            :size="iconSize">
            >
          </b-icon>
        </button>

          <b-dropdown-item

          v-for="(confi,key) in buttonIntoDropdown"
          :key="key">
            <ui-button
            :buttonType="confi.buttonType"
            :visibleLabel="confi.visibleLabel"
            :iconType="confi.iconType"
            :icon-pack="confi.iconPack"
            :sizeIcon="confi.sizeIcon"
            :visibleIcon="confi.visibleIcon"
            :buttonClass= "confi.buttonClass"
            :buttonSize= "confi.buttonSize"
            :buttonIsRounded="confi.isRounded"
            :buttonIsLarge="confi.true"
            :visibleToolTip="confi.visibleToolTip"
            :positionToolTip="confi.positionToolTip"
            :toolTipSize="confi.sizeToolTip"
            :typeToolTip="confi.typeToolTip"
            :multilinedToolTip="confi.multilinedToolTip"
            :enableNotification="confi.enableNotification"
            :visibleNotification.sync="confi.visibleNotification"
            :addedClass= "confi.addedClass"
            @click="buttonClicked"
          ></ui-button>

        </b-dropdown-item>

      </b-dropdown>

   </b-tooltip>
    </div>
<!-- End dropdown with tooltip -->
</div>

  </div>
</div>



</template>
<script>
  import UIButton from '../UIButton/index.js'
  /**
   * Component that allows to enable functions specifed trough the configuration
   * returning the actions to the father component. It has the following functions:
   * * Send the type of operation the father Component.
   * * Add a dropdown to the view trough a property 'visibleDropDown' and another
   *   property 'positionDropDownAppear' that adjust on what UIBotton will be.
   * @version 0.1.0
   * @since 0.1.0
   * @author Alfredo Garcia [gitlab:@garciareal45]
   * @copyright Karanta, CA. 2018
   */
  export default {

    name: 'UiToolBar',

    components: {
      'UiButton': UIButton
    },

    props: {
      /**
       * alingContentToolBar Property that determines the align of the content toolbar
       * @prop {String} [ alignContentToolBar = center ]
       */
      alignContentToolBar: {
        type: String,
        required: false,
        default: 'center'
      },

      /**
      * Prop sets up the configurations of UIBotton shown on the component *UIToolBar example:
      *[{
      *  buttonType: 'edition',
      *  visibleLabel: true,
      *  buttonClass: 'is-success',
      *  iconType: 'fas fa-pencil-alt',
      *  buttonSize: 'is-medium',
      *  visibleIcon: true,
      *  isRounded: true,
      *  visibleToolTip: true,
      *  positionToolTip: 'is-top',
      *  typeToolTip: 'is-primary',
      *  sizeToolTip: 'is-medium',
      *  multilinedToolTip: false
      *},
      *{
      *  buttonType: 'delete',
      *  visibleLabel: true,
      *  buttonClass: 'is-warning',
      *  iconType: 'fas fa-pencil-alt',
      *  buttonSize: 'is-small',
      *  visibleIcon: true,
      *  isRounded: true,
      *  visibleToolTip: true,
      *  positionToolTip: 'is-top',
      *  typeToolTip: 'is-primary',
      * sizeToolTip: 'is-medium',
      * multilinedToolTip: false
      *}
      * @prop {Array}
      */
      buttonConfigurations: {
        type: Array,
        required: true
      },

      /**
       * Prop that determines the class associated with the dropdown button
       * @prop {String} [buttonClassDropDown = is-sucess]
       */
      buttonClassDropDown: {
        type: String,
        required: false,
        default: 'is-success'
      },

      /**
       * Prop that determines if the dropdown button is rounded
       * @prop {Boolean} [buttonDropDownRounded=false]
       */
      buttonDropDownRounded: {
        type: Boolean,
        required: false,
        default: false
      },

      /**
       * Prop that determines the size of the dropdown button
       * @prop {String} [buttonDropDownSize= is-medium]
       */
      buttonDropDownSize: {
        type: String,
        required: false,
        default: 'is-medium'
      },

       /**
       * gapContentToolBar Property that detemines the gap type of the content toolbar
       * @prop {String} [ gapContentToolBar = is-1 ]
       */
      gapContentToolBar: {
        type: String,
        required: false,
        default: 'is-1'
      },

      /**
       * Prop that determines if the dropdown is hoverable
       * @prop {Boolean} [hoverable= true]
       */
      hoverable: {
        type: Boolean,
        required: false,
        default: true
      },

      /**
       * Prop that determines the icon type on the dropdown button
       * @prop {String} [iconDropDownType = plus]
       */
      iconDropDownType: {
        type: String,
        required: false,
        default: 'plus'
      },

      /**
      * Prop that determines the pack of icon on the button of the dropDown
      * @prop {String} [iconPack=fas]
      */
      iconPack: {
        type: String,
        required: false,
        default: 'fas'
      },

      /**
      * Prop that determines the size of icon on the dropdown button
      * @prop {String} [sizeIcon= is-medium]
      */
      iconSize: {
        type: String,
        required: false,
        default: 'is-medium'
      },

      /**
       * Prop that sets the content of the dropdown label
       * @prop {String} [label= null]
       */
      label: {
        type: String,
        required: false,
        default: null
      },

      /**
      * Prop that determines if the dropdown icon is visible or not
      * @prop {Boolean} [visibleIcon= true]
      */
      visibleIcon: {
        type: Boolean,
        required: false,
        default: true
      },

      /**
      * Prop that determines if the dropdown tooltip is visible
      * @prop {Boolean} [visibleToolTip= false]
      */
      visibleToolTip: {
        type: Boolean,
        required: false,
        default: false
      },

      /**
       * Prop that determines if the label is visible or not on the dropdown and dropdown tooltip
       * @prop {Boolean} [visibleLabel= false]
       */
      visibleLabel: {
        type: Boolean,
        required: false,
        default: false
      },

      /**
      * Prop that determines the position of the dropdown tooltip on the button
      * @prop {String} [positionToolTip= 'is-top']
      */
      toolTipPositionDropDown: {
        type: String,
        required: false,
        default: 'is-top'
      },

      /**
      * Prop that determines the class of the dropdown tooltip on the button
      * @prop {String} [typeToolTip= 'is-success']
      */
      toolTipTypeDropDown: {
        type: String,
        required: false,
        default: 'is-success'
      },

      /**
      * Prop that determines the size of the dropdown toolTip
      * @prop {String} [typeToolTip= 'is-medium']
      */
      toolTipSizeDropDown: {
        type: String,
        requerid: false,
        default: 'is-medium'
      },

      /**
       * Prop that determines the label on the dropdown tooltip
       * @prop {String} [tooltipLabel= null]
       */
      toolTipLabel: {
        type: String,
        required: false,
        default: null
      },

      /**
      * Prop that determines if the dropdown tooltip is multilened
      * @prop {Boolean} [multilinedToolTip=false]
      */
      multilinedToolTipDropDown: {
        type: Boolean,
        required: false,
        default: false
      },

      /**
       * Prop that detemines the position in the button configurations array
       * where must appear the dropdown
       * @prop {Number} [positionDropDownAppear= 0]
       */
      positionDropDownAppear: {
        type: Number,
        required: false,
        default: 0
      },

      /**
       * Prop that determines the position of the dropdown
       * @prop {String} [positionDropDown= is-bottom-left]
       */
      positionDropDown: {
        type: String,
        required: false,
        default: 'is-bottom-left'
      },

      /**
       * Prop that determines the visibility of the dropdown on the UIToolBar
       * @prop {Boolean} [visibleDropDown= false]
       */
      visibleDropDown: {
        type: Boolean,
        required: false,
        default: false
      }
    },

    computed: {

      /**
       * Determines the class on the dropdown button dynamically
       * @return {String}
       */
      dynamicClass () {
        return {
          'is-rounded': this.buttonDropDownRounded,
          'is-primary': (this.buttonClassDropDown === 'is-primary'),
          'is-link': (this.buttonClassDropDown === 'is-link'),
          'is-info': (this.buttonClassDropDown === 'is-info'),
          'is-success': (this.buttonClassDropDown === 'is-success'),
          'is-warning': (this.buttonClassDropDown === 'is-warning'),
          'is-danger': (this.buttonClassDropDown === 'is-danger'),
          'is-small': (this.buttonDropDownSize === 'is-small'),
          'is-medium': (this.buttonDropDownSize === 'is-medium'),
          'is-large': (this.buttonDropDownSize === 'is-large')
        }
      },
      /**
       * dynamicClassGap determines the gap type between column
       * @return {String}
       */
      dynamicClassGap () {
        return {
          'is-pulled-left': true,
          'is-0': (this.gapContentToolBar === 'is-0'),
          'is-1': (this.gapContentToolBar === 'is-1'),
          'is-2': (this.gapContentToolBar === 'is-2'),
          'is-3': (this.gapContentToolBar === 'is-3'),
          'is-4': (this.gapContentToolBar === 'is-4'),
          'is-5': (this.gapContentToolBar === 'is-5'),
          'is-6': (this.gapContentToolBar === 'is-6'),
          'is-7': (this.gapContentToolBar === 'is-7'),
          'is-8': (this.gapContentToolBar === 'is-8')
        }
      },

      /**
       *  dynamicClassAlign determines the align type of the toolbar content
       * @return {String}
       */
      dynamicClassAlign () {
        return {
          'is-centered': (this.alignContentToolBar === 'center'),
          'is-pulled-right': (this.alignContentToolBar === 'right'),
          'is-pulled-left': (this.alignContentToolBar === 'left')
        }
      },

      /**
       * buttonToShowBeforeDropdown return the UIbutton array putted before the dropdown
       * @return {Array}
       */
      buttonToShowBeforeDropdown () {
        let buttonShow = []

        if (this.visibleDropDown === true) {
          for (var key in this.buttonConfigurations) {
            if (key <= (this.positionDropDownAppear - 1)) {
              buttonShow.push(this.buttonConfigurations[key])
            }
          }
        } else {
          for (var key2 in this.buttonConfigurations) {
            buttonShow.push(this.buttonConfigurations[key2])
          }
        }
        return buttonShow
      },

      /**
       * buttonIntoDropdown return the UIButton array that have to be into dropdown
       * @return {Arrray}
       */
      buttonIntoDropdown () {
        let buttonShow = []

        for (var key in this.buttonConfigurations) {
          if (key > (this.positionDropDownAppear - 1)) {
            buttonShow.push(this.buttonConfigurations[key])
          }
        }

        return buttonShow
      },

      /**
      * labelContent determines if it will show the label custom or the label traslated
      * @return {String}
      */
      labelContent () {
        return (this.label || this.translateLabel('dropdownMessage.label'))
      },

      /**
       * labelContentTooltip determines if it will show the label custom or
       * the label traslated on the tooltip
       * @return {String}
       */
      labelContentTooltip () {
        return (this.toolTipLabel || this.translateLabel('dropdownMessage.meta'))
      }
    },

    methods: {

       /**
       * @function translateLabel Translates the received parameter
       * @return {String}
       * @public
       **/
      translateLabel (value) {
        return this.$i18n.t(`template.${value}`)
      },

     /**
      * Sends the type operation to the father component
      * @param {Event} event is a event that recibes the UIToolBar component from the UIBotton
      * @public
      */
      buttonClicked (event) {
        /**
        * event that emits the type operation to the father component
        * @event click
        * @type {String}
        */
        this.$emit('click', event)
      }
    }
  }
</script>

<style scoped>
</style>



