<template>
<div>
  <title-component 
   :element="titlePage"
   titleClass="is-size-2"
   :isPage="true">
  </title-component>
  <hr>
  <div class="columns">
   <b-switch v-model="rounded">Buttons rounded</b-switch>
   <b-switch v-model="dropDownAppear">Dropdown able</b-switch>
   <b-switch v-model="hoverable">Hoverable Dropdown</b-switch>
   <b-switch v-model="visibleIconButton">Visible icons UIButton</b-switch>
   <b-switch v-model="visibleIcon">Visible icon Dropdown</b-switch>
   <b-switch v-model="visibleToolTipButton">Visible tooltip UIButton</b-switch>
   <b-switch v-model="visibleToolTip">Visible tooltip dropdown</b-switch>
   <b-switch v-model="visibleLabel">Visible dropdown label</b-switch>
   <b-switch v-model="configurationButtons[key].visibleNotification" v-for="(nButtons, key) in configurationButtons" :key="key">Disable visible notification {{configurationButtons[key].buttonType}}</b-switch>
  </div>

  <div class="columns">
 <div class="column is-1">
     <b-field label="Align UIToolBar">
          <b-select
          placeholder="Select a align"
          v-model="alignToolBar"
          rounded>
              <option value="left">left</option>
              <option value="center">center</option>
              <option value="right">right</option>
          </b-select>
    </b-field>
   </div>

   <div class="column is-1">
     <b-field label="Button Size">
          <b-select
          placeholder="Select a align"
          v-model="buttonDropDownSize"
          rounded>
              <option value="is-small">is-small</option>
              <option value="is-medium">is-medium</option>
              <option value="is-large">is-large</option>
          </b-select>
    </b-field>
   </div>

   <div class="column is-1">
     <b-field label="Class UIButton">
          <b-select
          placeholder="Select a UIButton class"
          v-model="classUIButton"
          rounded>
              <option value="is-primary">is-primary</option>
              <option value="is-danger">is-danger</option>
              <option value="is-warning">is-warning</option>
              <option value="is-success">is-success</option>
              <option value="is-link">is-link</option>
              <option value="is-info">is-info</option>
          </b-select>
    </b-field>
   </div>

    <div class="column is-1">
     <b-field label="UIButton notification class">
          <b-select
          placeholder="Select a UIButton class"
          v-model="addedClass"
          rounded>
              <option value="is-primary">is-primary</option>
              <option value="is-danger">is-danger</option>
              <option value="is-warning">is-warning</option>
              <option value="is-success">is-success</option>
              <option value="is-link">is-link</option>
              <option value="is-info">is-info</option>
          </b-select>
    </b-field>
   </div>

    <div class="column is-1">
     <b-field label="Class dropdown">
          <b-select
          placeholder="Select a dropdown class"
          v-model="dropDownClass"
          rounded>
              <option value="is-primary">is-primary</option>
              <option value="is-danger">is-danger</option>
              <option value="is-warning">is-warning</option>
              <option value="is-success">is-success</option>
              <option value="is-link">is-link</option>
              <option value="is-info">is-info</option>
          </b-select>
    </b-field>
   </div>
     <div class="column is-2">
     <b-field label="Position dropdown on UIButton">
          <b-select
          placeholder="Select the position dropdown"
          v-model="buttonQuantity"
          rounded>
              <option :value=0>0 - UIButton</option>
              <option :value=1>1 - UIButton</option>
              <option :value=2>2 - UIButton</option>

          </b-select>
    </b-field>
    </div>

     <div class="column is-1">
     <b-field label="Position tooltip UIButton">
          <b-select
          placeholder="Select the position tooltip on UIButton"
          v-model="positionToolTipButton"
          rounded>
              <option value="is-top">is-top</option>
              <option value="is-bottom">is-bottom</option>
              <option value="is-left">is-left</option>
              <option value="is-right">is-right</option>

          </b-select>
    </b-field>
    </div>

     <div class="column is-1">
     <b-field label="Position tooltip Dropdown">
          <b-select
          placeholder="Select the position tooltip on dropdown"
          v-model="positionToolTip"
          rounded>
              <option value="is-top">is-top</option>
              <option value="is-bottom">is-bottom</option>
              <option value="is-left">is-left</option>
              <option value="is-right">is-right</option>

          </b-select>
    </b-field>
    </div>

    <div class="column is-1">
     <b-field label="Position Dropdown">
          <b-select
          placeholder="Select the position how dropdown appear"
          v-model="positionDropDown"
          rounded>
              <option value="is-top-right">is-top-right</option>
              <option value="is-top-left">is-top-left</option>
              <option value="is-bottom-left">is-bottom-left</option>
              <option :value= "null">is-bottom-right</option>

          </b-select>
    </b-field>
    </div>

   <div class="column is-1">
       <b-field label="Class tooltip UIButton">
            <b-select
            placeholder="Select a tooltip UIButton class"
            v-model="classToolTipButton"
            rounded>
                <option value="is-primary">is-primary</option>
                <option value="is-danger">is-danger</option>
                <option value="is-warning">is-warning</option>
                <option value="is-success">is-success</option>
                <option value="is-link">is-link</option>
                <option value="is-info">is-info</option>
            </b-select>
      </b-field>
    </div>

     <div class="column is-1">
       <b-field label="Class tooltip dropdown">
            <b-select
            placeholder="Select a tooltip UIButton class"
            v-model="tooltipType"
            rounded>
                <option value="is-primary">is-primary</option>
                <option value="is-danger">is-danger</option>
                <option value="is-warning">is-warning</option>
                <option value="is-success">is-success</option>
                <option value="is-link">is-link</option>
                <option value="is-info">is-info</option>
            </b-select>
      </b-field>
    </div>

  </div>

  <div class="columns">

     <div class="column is-1">
       <b-field label="Gap">
            <b-select
            placeholder="Select gap"
            v-model="gapToolBar"
            rounded>
                <option value="is-0">is-0</option>
                <option value="is-1">is-1</option>
                <option value="is-2">is-2</option>
                <option value="is-3">is-3</option>
                <option value="is-4">is-4</option>
                <option value="is-5">is-5</option>
                <option value="is-6">is-6</option>
                <option value="is-7">is-7</option>
                <option value="is-8">is-8</option>
            </b-select>
      </b-field>
    </div>
     <div class="column is-2">
       <b-field label="Label text dropdown">
            <b-input
            v-model="label"
            placeholder="Introduce the label"
            rounded>
            </b-input>
        </b-field>
    </div>

    <div class="column is-2">
       <b-field label="Label text dropdown tooltip">
            <b-input
            v-model="toolTipLabel"
            placeholder="Introduce the label"
            rounded>
            </b-input>
        </b-field>
    </div>
  </div>

 <ui-tool-bar
  :buttonConfigurations = "configurationButtonsEditable"
  :positionDropDownAppear="buttonQuantity"
  :visibleDropDown="dropDownAppear"
  :iconDropDownType="iconDropDownType"
  :iconSize="iconSize"
  :visibleIcon="visibleIcon"
  :buttonClassDropDown="dropDownClass"
  :buttonDropDownRounded="rounded"
  :positionDropDown="positionDropDown"
  :buttonDropDownSize="buttonDropDownSize"
  :visibleToolTip="visibleToolTip"
  :toolTipPositionDropDown="positionToolTip"
  :alignContentToolBar = "alignToolBar"
  :gapContentToolBar = "gapToolBar"
  :visibleLabel="visibleLabel"
  :hoverable= "hoverable"
  :label = "label"
  :toolTipTypeDropDown= "tooltipType"
  :toolTipLabel = "toolTipLabel"
  @click="actionToolBar"
 ></ui-tool-bar>
      <hr>
      <div class="positionRight">
        <b-tooltip
        label="Changelog"
        :active="true"
        position="is-left"
        type="is-dark">
          <ui-panel-container
            :panelHeight= "150"
            :panelWidth= "600"
             unitHeight="px"
             unitWidth="px"
            backgroundColor="white"
            panelBorder="ridge"
            panelBorderColor="black"
            :collapseButtonEnabled="true"
            collapseButtonPosition="left"
            collapseButtonBackgroundColor="#C2C2C2"
            :style="collapseLeft"
            @on-collapse="collapsePanelLeft">
              <markdown></markdown>
            </ui-panel-container>
        </b-tooltip>
      </div>
</div>
</template>
<script>
import UIToolBar from '../../../components/common/UIToolBar/index.js'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import titleComponent from '../titleComponent.vue'
import { updates } from '../../../libraryUpdates.js'
import readme from '../../../components/common/UIToolBar/changelog.md'

export default {
  name: 'UIToolBarPage',

  components: {
    'UiToolBar': UIToolBar,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },
  data () {
    return {
      /**
       * Version data
       */
      titlePage: updates.ToolBar,
      collapseLeft: {},
      /**
       * Configurations
       */
      rounded: true,
      visibleIconButton: true,
      classUIButton: 'is-primary',
      visibleToolTipButton: true,
      positionToolTipButton: 'is-top',
      classToolTipButton: 'is-primary',
      tooltipType: 'is-primary',
      configurationButtons: [{
        buttonType: 'edition',
        visibleLabel: true,
        buttonClass: 'is-success',
        iconType: 'fas fa-pencil-alt',
        buttonSize: 'is-medium',
        visibleIcon: true,
        isRounded: this.rounded,
        visibleToolTip: true,
        positionToolTip: 'is-top',
        typeToolTip: 'is-primary',
        sizeToolTip: 'is-medium',
        multilinedToolTip: false
      },
      {
        buttonType: 'delete',
        visibleLabel: true,
        buttonClass: 'is-warning',
        iconType: 'fas fa-pencil-alt',
        buttonSize: 'is-small',
        visibleIcon: true,
        isRounded: true,
        visibleToolTip: true,
        positionToolTip: 'is-top',
        typeToolTip: 'is-primary',
        sizeToolTip: 'is-medium',
        multilinedToolTip: false
      },
      {
        buttonType: 'restore',
        visibleLabel: true,
        buttonClass: 'is-warning',
        iconType: 'fas fa-pencil-alt',
        buttonSize: 'is-large',
        visibleIcon: true,
        isRounded: true,
        visibleToolTip: true,
        positionToolTip: 'is-top',
        typeToolTip: 'is-warning',
        sizeToolTip: 'is-medium',
        multilinedToolTip: false
      }],
      buttonQuantity: 2,
      buttonDropDownSize: 'is-large',
      dropDownAppear: true,
      dropDownClass: 'is-danger',
      positionDropDown: 'is-bottom-left',
      visibleIcon: true,
      visibleToolTip: true,
      positionToolTip: 'is-right',
      label: 'Ajustes',
      toolTipLabel: 'Click aqui',
      visibleLabel: true,
      iconDropDownType: 'plus',
      gapToolBar: 'is-1',
      alignToolBar: 'center',
      hoverable: true,
      iconSize: 'is-small'
    }
  },
  computed: {
    configurationButtonsEditable () {
      let vm = this
      let configuration = this.configurationButtons.map((button) => {
        button.isRounded = vm.rounded
        button.buttonSize = vm.buttonDropDownSize
        button.visibleIcon = vm.visibleIconButton
        button.buttonClass = vm.classUIButton
        button.visibleToolTip = vm.visibleToolTipButton
        button.positionToolTip = vm.positionToolTipButton
        button.typeToolTip = vm.classToolTipButton
        return button
      })
      return configuration
    }
  },
  methods: {
    actionToolBar (evento) {
      console.log(evento)
    },
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>
<style>
.positionRight {
  display: flex;
  justify-content: flex-end;
}
</style>



