<template>
  <div class="columns is-centered">
    <div class="column is-half">
      <h2 class="is-size-2">Common UI Components</h2>
      <template v-for="(element, index) in updateskeys">
      <hr :key="index">
        <router-link :to="{name: elements[element].name}" tag="div" :key="index">
          <title-component :element="elements[element]"></title-component>
        </router-link>
      </template>
    </div>
  </div>
</template>

<script>

import titleComponent from './titleComponent.vue'
import {updates} from '../../libraryUpdates.js'

export default {
  name: 'CommonSection',
  components: {
    titleComponent
  },
  data () {
    return {
      elements: updates
    }
  },

  crated () {
    // console.log(this.ele)
  },
  methods: {

  },
  computed: {
    updateskeys () {
      return Object.keys(this.elements).sort()
    }
  }
}
</script>

<style>
</style>



