<script>
import { defaultDateFormatter } from '../../../config.js'
import UISearchSelect from '../UISearchSelect'
import UIContainer from '../UIContainer/index'
import UIToolbar from '../UIToolBar/index'
import UIButton from '../UIButton/index'
import debounce from 'lodash/debounce'
/**
 * @vue UIManagementPanelFilters.vue
 * A component made to display the properties on which be apply filters. This component has the following functions:
 * * Renders the component according with the config property
 * * Emits an event with the object to search and the entity/model represented
 * * Restore the default filters of the component
 * * Shows the filters classified
 * @author Cruz Ortiz [gitlab:@cortiz99]
 * @version 0.1.0
 * @since 0.1.0
 * @copyright Karanta C.A. 2018
 */
export default {
  name: 'UiManagementPanelFilters',

  components: {
    'UiSearchSelect': UISearchSelect,
    'UiContainer': UIContainer,
    'UiToolbar': UIToolbar,
    'UiButton': UIButton
  },

  // Represent the property and event associated when v-model is used
  model: {
    prop: 'value',
    // In this case some event that doesn't affect the config
    event: 'filters'
  },

  props: {
    /**
    * Set the configurations of the property of the element shown in the
    * preview.
    * @prop {Array} value
    */
    value: {
      type: Array,
      required: true
    },

    /**
    * Set the icon of the collapse elements
    * @prop {String} [collapseIcon = 'fa fa-chevron-down']
    */
    collapseIcon: {
      type: String,
      required: false,
      default: 'fa fa-chevron-down'
    },

    /**
    * Sets the input positions
    * @prop {Boolean} [isHorizontal = 'false']
    */
    isHorizontal: {
      type: Boolean,
      default: false
    },

    /**
     * Represent the data entity's
     * @prop {String} entity
     */
    entity: {
      type: String,
      required: true
    },

    /**
     * Sets the classes of the header label
     * @prop {String} [headerClass = 'is-size-3 has-text-centered is-uppercase']
     */
    headerClass: {
      type: String,
      required: false,
      default: 'is-size-3 has-text-centered is-uppercase'
    },

    /**
     * Represents the icon family used on the component
     * @prop {String} [iconPack = 'fa']
     */
    iconPack: {
      type: String,
      required: false,
      default: 'fa'
    },

    /**
    * Set the classifications open on start
    * @prop {Boolean} [isOpen = false]
    */
    isOpen: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * Sets the size of the close button
     * @prop {String} [sizeCloseButton = 'is-medium']
     */
    sizeCloseButton: {
      type: String,
      required: false,
      default: 'is-medium'
    },

    /**
     * Sets the configuration of the buttons toolbar
     * @prop {Array} [toolbarButtonConfigurations = [
       {
         buttonType: 'restore',
         visibleLabel: true,
         buttonClass: 'is-primary',
         iconType: 'fa fa-sync-alt',
         buttonSize: 'is-medium',
         visibleIcon: true,
         isRounded: false,
         visibleToolTip: true,
         positionToolTip: 'is-top',
         typeToolTip: 'is-info',
         sizeToolTip: 'is-medium',
         multilinedToolTip: true
       }
     ]]
     */
    toolbarButtonConfigurations: {
      type: Array,
      required: false,
      default: () => {
        return [
          {
            buttonType: 'restore',
            visibleLabel: true,
            buttonClass: 'is-primary',
            iconType: 'fa fa-sync-alt',
            buttonSize: 'is-medium',
            visibleIcon: true,
            isRounded: false,
            visibleToolTip: true,
            positionToolTip: 'is-top',
            typeToolTip: 'is-info',
            sizeToolTip: 'is-medium',
            multilinedToolTip: true
          }
        ]
      }
    },

    /**
     * Sets the general configuration of the toolbar
     * @prop {Object} [toolbarConfiguration = {
       dropDownAppear: false,
       gapContentToolBar: 'is-1'
     }]
     */
    toolbarConfiguration: {
      type: Object,
      required: false,
      default: () => {
        return {
          dropDownAppear: false,
          gapContentToolBar: 'is-1'
        }
      }
    },

    /**
     * Sets the position of the toolbar
     * @prop {String} [toolbarPosition = center]
     */
    toolbarPosition: {
      type: String,
      required: false,
      default: 'center'
    },
    /**
     * Shows the icon close on the UIContainer
     * @prop {string} [default = false]
     */
    iconCloseable: {
      type: Boolean,
      default: false
    }
  },

  data () {
    return {
      /**
      * Shows a loading state
      * @type {Boolean}
      */
      isLoading: false,

      /**
       * Represents the structure of the edition form
       * @type {Object}
       */
      objectToBind: {},

      /**
      * Object send to do the petition
      * @type {Object}
      */
      outgoingObject: {}
    }
  },

  created () {
    this.objectToBindCreator(this.value, this.objectToBind)
  },

  methods: {
    /**
    * Emits the event of the child
    * @param  {String} event  Represents the event repeated
    * @param  {String|Number|Object} params Represents the params of the event
    * @public
    */
    childEventEmitter (event, params) {
      /**
       * @event input
       * @type {String|Number|Object}
       */
      (params) ? this.$emit(event, params) : this.$emit(event)
    },

    /**
     * Emits the event of the child debounded
     * @param  {String} event  Represents the event repeated
     * @param  {String|Number|Object} params Represents the params of the event
     * @public
     */
    childEventEmitterDebounced: debounce(function (event, params) {
      this.childEventEmitter(event, params)
    }, 500),

    /**
    * Emits the event on-filters-apply when a filters is applied
    * @param {Array} queryObject Represents the query object to do the petition
    * @public
    */
    eventEmitter (queryObject) {
      /**
       * @event on-filters-apply
       * @type {Array}
       */
      this.$emit('on-filters-apply', [queryObject, this.entity])
    },

    /**
     * Emits the event on-filters-apply after a delay
     * @param {Object} queryObject Represents the query object to do the petition
     * @public
     */
    eventEmitterDebounced: debounce(
      function (queryObject) {
        this.eventEmitter(queryObject)
      }
      , 500),

    /**
     * Filters the name of the prop in the value prop
     * @param  {String} propName Represents the prop tag
     * @return {String}          A string with the rigth prop
     * @public
     */
    filterPropName (propName) {
      let stringWithOutPoint = propName.split('.')
      if (stringWithOutPoint[1]) {
        return stringWithOutPoint[1]
      } else {
        return stringWithOutPoint[0]
      }
    },

    /**
    * Builds the object with an structure according with the components to be implement
    * @param  {Array} value            It's the value prop of the component
    * @param  {Object} objectTransformed Represents the output object with the structure
    * @public
    */
    objectToBindCreator (value, objectTransformed) {
      let self = this
      value.map(function (clasification) {
        return clasification['children'].map(function (prop) {
          // Filter the name of the propTag used in the value
          let tag = self.filterPropName(prop['propTag'])
          // used to determinate when to put the original data and when to do
          // the transformation
          let flag = false
          if (prop['filters'] && prop['filters']['filterable']) {
            if (prop['filters']['type'].toUpperCase() === 'OBJECT') {
              objectTransformed[tag] = objectTransformed[tag] || {}
              for (let item of prop['filters']['group']) {
                objectTransformed[tag][item] = prop['filters']['component']['defaultValue'][item]
              }
              flag = true
            }
            if (prop['filters']['type'].toUpperCase() === 'DATE') {
              objectTransformed[tag] = (prop['filters']['component']['defaultValue'] !== null) ? new Date(prop['filters']['component']['defaultValue']) : null
              flag = true
            }
            if (!flag) {
              objectTransformed[tag] = prop['filters']['component']['defaultValue']
            }
          }
        })
      })
    },

    /**
    * Builds the object according with the structure spected for the backend
    * @param  {Array} value            It's the value prop of the component
    * @param  {Object} objectToTransform It's the object with the values but not the structure
    * @param  {Object} objectTransformed Represents the output object with the structure
    * @public
    */
    outgoingObjectCreator (value, objectToTransform, objectTransformed) {
      let self = this
      let objectValue = {}
      self.simpleObjectCreator(objectToTransform, objectValue)
      value.map(function (clasification) {
        return clasification['children'].map(function (prop) {
          // Filter the name of the propTag used in the value
          let tag = self.filterPropName(prop['propTag'])
          // used to determinate when to put the original data and when to do
          // the transformation
          let flag = false
          // for identifiers, build the object id
          if (prop['filters'] && prop['filters']['type'].toUpperCase() === 'ID') {
            objectTransformed['id'] = objectTransformed['id'] || {}
            if (objectValue[tag]) {
              objectTransformed['id'][tag] = objectValue[tag]
            }
            flag = true
          }
          // for components with object binding, sets the corresponding strucutre
          if (prop['filters'] && prop['filters']['type'].toUpperCase() === 'OBJECT') {
            for (let item in objectValue[tag]) {
              objectTransformed[item] = objectValue[tag][item]
            }
            flag = true
          }
          if (!flag) {
            objectTransformed[tag] = objectValue[tag]
          }
        })
      })
    },

    /**
    * Build  a one level object
    * @param  {Object} objectToTransform represent the object that will be transform
    * @param {Object} objectTransformed Represent the object where structure will be build
    * @public
    */
    simpleObjectCreator (objectToTransform, objectTransformed) {
      for (let item in objectToTransform) {
        if (typeof (objectToTransform[item]) === 'object' && objectToTransform[item] !== null) {
          this.simpleObjectCreator(objectToTransform[item], objectTransformed)
        } else {
          objectTransformed[item] = objectToTransform[item]
        }
      }
    },

    /**
    * Restore de default values of the filters
    * @public
    */
    restore () {
      let self = this
      this.isLoading = true
      this.objectToBindCreator(this.value, this.objectToBind)
      this.outgoingObjectCreator(this.value, this.objectToBind, this.outgoingObject)
      this.eventEmitter(this.outgoingObject)
      setTimeout(function () {
        self.isLoading = false
      })
    },

    /**
     * Translate according with the entity messages
     * @param  {String} entityProp Point to the the property tha contains the tranlated string
     * @param  {String} entity     Entity or model represented by the component
     * @return {String}            The string with the translated message
     * @public
     */
    entityTranslate (entityProp, entity) {
      return this.$i18n.t(`entities.${entity.toUpperCase()}.${entityProp}.label`)
    },

    /**
     * Makes the translations of the template
     * @param  {String} template [description]
     * @return {String}          [description]
     * @public
     */
    translateTemplate (templateProp) {
      return this.$i18n.t(`template.${templateProp}.label`)
    },
    /**
     * Translates prop of the other entities
     * @param {String} propTag label to translate
     */
    translateLabelEntities (propTag) {
      let label = propTag.split('.')
      let length = label.length
      if (length > 1) {
        label[0] = label[0].toUpperCase()
        if (label[0] !== 'ID') {
          return this.$i18n.t(`entities.${label[0]}.${label[1]}.label`)
        } else {
          return this.$i18n.t(`entities.${propTag}.label`)
        }
      } else {
        return this.$i18n.t(`entities.${this.entity.toUpperCase()}.${propTag}.label`)
      }
    }
  },
  render: function (createElement) {
    let self = this
    return createElement('div', [
      // Header
      createElement('ui-container', {
        props: {
          align: 'none',
          closeable: self.iconCloseable,
          sizeCloseButton: self.sizeCloseButton
        },
        on: {
          'close': function (val) {
            self.childEventEmitter('close', val)
          }
        }
      },
        [
          // Header Label
          createElement('div', {
            class: `header ${self.headerClass}`
          }, [
            createElement('h3', [self.translateTemplate('titlePanelFilters')])
          ]),
          createElement('br'),
          (function () {
            if (self.isLoading) {
              return createElement('b-loading', {
                props: {
                  active: true,
                  isFullPage: false
                }
              })
            } else {
              return self.value.map(function (clasification) {
                // Building the clasification in the editon form
                // B-Collapse
                return createElement('b-collapse', {
                  class: {
                    card: true
                  },
                  props: {
                    open: self.isOpen
                  }
                },
                  [
                    // Card header
                    createElement('div', {
                      class: {
                        'card-header': true
                      },
                      slot: 'trigger'
                    },
                      [
                        // Title
                        createElement('p', {
                          class: {
                            'card-header-title': true
                          }
                        },
                          self.entityTranslate(clasification['classTag'], self.entity)),
                        // Header icon
                        createElement('a', {
                          class: {
                            'card-header-icon': true
                          }
                        },
                          [
                            createElement('b-icon', {
                              props: {
                                icon: self.collapseIcon,
                                pack: self.iconPack
                              }
                            })
                          ])
                      ]),
                    // Car content
                    createElement('div', {
                      class: {
                        'card-content': true
                      }
                    },
                      [
                        // Content
                        createElement('div', {
                          class: {
                            'content': true
                          }
                        },
                          [
                            clasification['children'].map(function (prop) {
                              // Filter the name of the propTag used in the value prop
                              let tag = self.filterPropName(prop['propTag'])
                              if (prop['filters'] && prop['filters']['filterable']) {
                                prop['filters']['component']['props']['value'] = self.objectToBind[tag]
                                return createElement('b-field', {
                                  props: {
                                    // Label
                                    label: (prop['filters']['visibleLabel']) ? self.translateLabelEntities(prop['propTag']) : null,
                                    type: self.errors.has(`${prop['propTag']}`) ? 'is-danger' : null,
                                    message: self.errors.first(`${prop['propTag']}`),
                                    horizontal: self.isHorizontal
                                  }
                                },
                                  [
                                    // Form component
                                    createElement(`${prop['filters']['component']['name']}`, {
                                      props: {
                                        ...prop['filters']['component']['props'],
                                        value: self.objectToBind[tag]
                                      },
                                      attrs: {
                                        ...prop['filters']['component']['attrs'],
                                        name: prop['propTag']
                                      },
                                      directives: (function () {
                                        if (prop['filters']['component']['directives']) {
                                          let directives = [
                                            ...prop['filters']['component']['directives']
                                          ]
                                          return directives
                                        }
                                      })(),
                                      on: {
                                        input: function (val) {
                                          if (typeof (val) === 'string') {
                                            self.objectToBind[tag] = val
                                            self.outgoingObjectCreator(self.value, self.objectToBind, self.outgoingObject)
                                            self.eventEmitterDebounced(self.outgoingObject)
                                          } if (Object.prototype.toString.call(val) === '[object Date]') {
                                            self.objectToBind[tag] = defaultDateFormatter(val)
                                            self.outgoingObjectCreator(self.value, self.objectToBind, self.outgoingObject)
                                            self.eventEmitterDebounced(self.outgoingObject)
                                          } else {
                                            self.childEventEmitterDebounced('input', val)
                                          }
                                        },
                                        'select': function (val) {
                                          self.objectToBind[tag] = val
                                          self.outgoingObjectCreator(self.value, self.objectToBind, self.outgoingObject)
                                          self.eventEmitterDebounced(self.outgoingObject)
                                        }
                                      }
                                    })
                                  ])
                              }
                            })
                          ])
                      ])
                  ])
              })
            }
          })()
        ]),
      createElement('br'),
      // Toolbar
      createElement('ui-container', {
        props: {
          align: self.toolbarPosition,
          closeable: false
        }
      },
        [
          createElement('ui-toolbar', {
            props: {
              ...self.toolbarConfiguration,
              buttonConfigurations: self.toolbarButtonConfigurations
            },
            on: {
              'click': function (event) {
                if (event === 'restore') {
                  self.restore()
                }
              }
            }
          })
        ])
    ])
  }
}
</script>
<style>
</style>



