<template>
  <div>
    <ui-container
      align="none"
      :closeable="iconCloseable"
      :sizeCloseButton="sizeCloseButton"
      @close="eventChildEmitter('close', $event)"
    >
      <h3 :class="headerClasses">{{translateTemplate('titleHideColumns')}}</h3>
    </ui-container>
    <div class="card-content">
      <b-checkbox
        v-model="checkedAll"
        @input="checkAll"
      >
        {{translateMessages('allCheckbox')}}
      </b-checkbox>
    </div>
    <b-collapse
      v-for="(item1,key1) in configModel"
      class="card"
      :key="key1"
      :open="isOpen"
    >
      <div
        slot="trigger"
        slot-scope="props"
        class="card-header"
      >
        <p class="card-header-title">
          {{translateEntity(entity, item1['classTag'])}}
        </p>
        <a class="card-header-icon">
          <b-icon :icon="icon"></b-icon>
        </a>
      </div>
      <div class="card-content">
        <div class="content">
          <div class="content has-text-right">
            <b-checkbox
              v-model="checkedGroup[key1]"
              @input="classChecker($event, item1)"
            >
              {{translateMessages('classCheckbox')}}
            </b-checkbox>
          </div>
          <div
            v-for="(item2,key2) in item1['children']"
            :key="key2"
          >
            <b-checkbox
              v-if="(item2['tabulatedData']['visibleCheckBox'] === undefined) ? true : item2['tabulatedData']['visibleCheckBox']"
              v-model="item2['tabulatedData']['visibleOnTable']"
              @input="classUnchecker($event, key1)"
            >
              {{translateEntity(entity, filterPropName(item2['propTag']))}}
            </b-checkbox>
          </div>
        </div>
      </div>
    </b-collapse>
  </div>
</template>

<script>
/**
 * @vue UIManagementPanelShowHideColumns.vue
 A component made to select those columns to be shown on the tabulated data. This component have the following functions:
 * Allows to show the parameter of the entity
 * Allows to classificade the parameters
 * Allows to check or uncheck a column of the tabulated data to be show
 * Allows to check all tags within a classification
 * Allows to check all tags
 * @author Cruz Ortiz [gitlab:@cortiz99]
 * @version 0.1.0
 * @since 0.1.0
 * @copyright Karanta C.A. 2018
 */
import debounce from 'lodash/debounce'
import UIContainer from '../UIContainer/index'
export default {
  name: 'UiManagementPanelShowHideColumns',

  components: {
    'UiContainer': UIContainer
  },

  // Represent the property and event associated when v-model is used
  model: {
    prop: 'value',
    event: 'input'
  },

  props: {
    /**
    * Set the configurations of the property of the element shown
    * @prop {Array} value
    */
    value: {
      type: Array,
      required: true
    },

    /**
    * Represent the entity or model to show in the panel management preview
    * @prop {String} entity
    */
    entity: {
      type: String,
      required: true
    },

    /**
     * String with the classes to applied to the header
     * @prop {String} [headerClasses = 'is-size-3 is-uppercase has-text-centered']
     */
    headerClasses: {
      type: String,
      required: false,
      default: 'is-size-3 is-uppercase has-text-centered'
    },

    /**
    * Set the icon of the tags elements
    * @prop {String} [icon = 'fa fa-chevron-down']
    */
    icon: {
      type: String,
      required: false,
      default: 'fa fa-chevron-down'
    },

    /**
    * Set the classifications open on start
    * @prop {Boolean} [isOpen = false]
    */
    isOpen: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * The prop of the container implemented
     * @prop {String} [sizeCloseButton = 'is-medium']
     */
    sizeCloseButton: {
      type: String,
      required: false,
      default: 'is-medium'
    },
    /**
     * Sets the icon close on the UIContainer
     * @prop {Boolean} [iconCloseable = false]
     */
    iconCloseable: {
      type: Boolean,
      default: false
    }
  },

  data () {
    return {
      configModel: {},
      /**
       * A flag to indicate the state of the checked group
       * @type {Array}
       */
      checkedGroup: [],
      checkedAll: null
    }
  },

  /**
   * Set the initial conditions of the component
   */
  created () {
    this.configModel = this.value
  },

  methods: {
    /**
     * Check and uncheck all tags
     * @param  {Event} event Represents the value passed throw the event
     * @public
     */
    checkAll (event) {
      this.configModel.map((classification) => {
        classification['children'].map((prop) => {
          prop['tabulatedData']['visibleOnTable'] = event
          return prop
        })
        return classification
      })
      // use to update every component used
      this.$forceUpdate()
      this.eventEmitter()
    },

    /**
     * Checks every checkbox of a classification
     * @param  {Event} event          Represents the payload of the input
     * event
     * @param  {Object} classification Represents the classification of
     * the data
     * @public
     */
    classChecker (event, classification) {
      for (let item of classification['children']) {
        item['tabulatedData']['visibleOnTable'] = event
      }
      // use to update every component used
      this.$forceUpdate()
      this.eventEmitter()
    },

    /**
     * Uncheck the checkbox to select all within a classification
     * @param  {Event} event Represents the value passed throw the event
     * @param  {Object} classification Represents the current classification
     * @public
     */
    classUnchecker (event, classification) {
      if (!event) {
        // use to update every component used
        this.$forceUpdate()
      }
      this.eventEmitter()
    },

    /**
     * Emits the event of a child component
     * @param  {String} event Name of the child event
     * @param  {Object|Array|String|Number} param Params of the event of the child
     * @public
     */
    eventChildEmitter (event, param) {
        /**
        * @event event
        * @type {Object|Array|String|Number}
        */
      (param) ? this.$emit(event, param) : this.$emit(event)
    },

    /**
     * Emits an event with the new configModel after 500ms
     * @public
     */
    eventEmitter: debounce(function () {
      /**
       * event with the new configModel
       * @event input
       * @type {Array}
       */
      this.$emit('input', this.configModel)
    }, 500),

    /**
     * Makes the translations of the key
     * @param  {String} entity Represents the entity represented on the
     * component
     * @param  {String} key    Represents the key to be translated
     * @return {String}        The string translated
     * @public
     */
    translateEntity (entity, key) {
      return this.$i18n.t(`entities.${entity}.${key}.label`)
    },

    /**
     * Makes the translations for the messages
     * @param  {String} message Represent the key on the messages property
     * @return {String}         The message translated
     * @public
     */
    translateMessages (message) {
      return this.$i18n.t(`messages.${message}.label`)
    },

    /**
     * Makes the translations for the template
     * @param  {String} template Represent the key on the template property
     * @return {String}         The string translated
     * @public
     */
    translateTemplate (template) {
      return this.$i18n.t(`template.${template}.label`)
    },

    /**
     * Filters the name of the prop in the value prop
     * @param  {String} propName Represents the prop tag
     * @return {String}          A string with the rigth prop
     * @public
     */
    filterPropName (propName) {
      let stringWithOutPoint = propName.split('.')
      if (stringWithOutPoint[1]) {
        return stringWithOutPoint[1]
      } else {
        return stringWithOutPoint[0]
      }
    }
  }
}
</script>

<style scoped>
</style>


