<template>

 <div>

   <button
   v-if="closeable"
   class="delete is-pulled-right"
   :class="dynamicSizeCloseButton"
   @click= "closeAction">
   </button>

   <div :class="dynamicAlignContent">
     <div>
       <!-- @slot Use to add contennt -->
       <slot></slot>
     </div>
   </div>

</div>
</template>

<script>
  /**
   * This component that could be closeable contains other elements, it also allows to align
   * its content and it can notify when it requires to be closed.
   * * @vue UIContainer.vue
   * @version 0.1.0
   * @since 0.1.0
   * @author Alfredo Garcia [gitlab:@garciareal45]
   * @copyright Karanta, CA. 2018
  */
  export default {
    name: 'UiContainer',

    props: {

      /**
       * Property that determines the align of the UIContainer content
       * @prop {String} [align = 'center']
       */
      align: {
        type: String,
        required: false,
        default: 'center'
      },

      /**
       *  Property that indicate if the UIContainer can close itself or
       *  not
       * @prop {Boolean} [closeable = true]
       */
      closeable: {
        type: Boolean,
        required: false,
        default: true
      },

      /**
       * Property that determines the size of the button close
       * @prop {String} [buttonCloseSize = 'is-medium']
       */
      sizeCloseButton: {
        type: String,
        required: false,
        default: 'is-medium'
      }
    },

    computed: {

      /**
       * dynamicAlignContent determines the class about the align of the UIcontainer content
       * @return {String}
       */
      dynamicAlignContent () {
        return {
          'container-center': (this.align === 'center'),
          'container-left': (this.align === 'left'),
          'container-right': (this.align === 'right'),
          '': (this.align === 'none')
        }
      },

      /**
       * dynamicSizeCloseButton determines the class about the size of the close button
       * @return {String}
       */
      dynamicSizeCloseButton () {
        return {
          'is-medium': (this.sizeCloseButton === 'is-medium'),
          'is-small': (this.sizeCloseButton === 'is-small'),
          'is-large': (this.sizeCloseButton === 'is-large')
        }
      }
    },

    methods: {

      /**
       * closeAction send the close action to the father component
       * @return {Event}
       * @public
       */
      closeAction: function () {
        /**
         * Emits when Close button is clicked
         * @event close
         * @type {null}
         */
        this.$emit('close')
      }
    }
  }
</script>

<style scoped>

.container-center {
  display: flex;
  justify-content : center
}

.container-right {
  display: flex;
  justify-content: flex-end;
}

.container-left {
  display: flex;
  justify-content: flex-start;
}
</style>



