<template>
  <div
    :class="dinamicClassField">
    <div v-if="labelOn" :class="dinamicClassLabel">
      <label class="label">
        {{translateTemplate(this.entity,'label')}}
      </label>
    </div>
    <div :class="dinamicClassBody">
      <div :class="dinamicClassInput">
        <b-autocomplete
          :value="computedValue"
          open-on-focus
          :icon="icon"
          :icon-pack="iconPack"
          :placeholder="translateTemplate(this.entity,'placeholder')"
          :data="data"
          :field="optionIdentifier"
          :rounded="rounded"
          v-bind="$attrs"
          @input="inputEmitter"
          @select="selectEmitter"
          >
          <template slot-scope="props">
            <span
              v-for="(items, key) in paramsToSee"
              :key="key">
              {{props.option[`${items}`] }}
            </span>
          </template>
          <template slot = "empty">
            <p>
              {{translateMessage('noResults')}}
            </p>
          </template>
        </b-autocomplete>
      </div>
    </div>
  </div>
</template>

<script>
import debounce from 'lodash/debounce'
/**
 * @vue UISearchSelect.vue
* A component that represent a select with the capacity to receive user data
* and do a search of the option that match. The adicional functions are:
* * Allows to establish the parameter that you want to see and those that you
* want to get.
* * Allows to position the label to identify the input
* * Allows to receive a default option to be the first opcion selected
* @author Cruz Ortiz [gitlab:@cortiz99]
* @version 0.1.0
* @since 0.1.0
* @copyright Karanta C.A. 2018
*/
export default {
  name: 'UiSearchSelect',

  model: {
    prop: 'value',
    event: 'select'
  },

  props: {
    /**
     * Represents the option data to be selected
     * @prop {Array} data
     */
    data: {
      type: Array,
      required: true
    },

    /**
    * Type of select or entity/model related with the select
    * @prop {String} entity
    */
    entity: {
      type: String,
      required: true
    },

    /**
     * Represents the icon used on the component
     * @prop {String} [icon = 'fa fa-chevron-down']
     */
    icon: {
      type: String,
      required: false,
      default: 'fa fa-chevron-down'
    },

    /**
     * Represents the icon family used on the component
     * @prop {String} [iconPack = 'fa']
     */
    iconPack: {
      type: String,
      required: false,
      default: 'fa'
    },

    /**
     * Show or not the label
     * @prop {Boolean} [labelOn = false]
     */
    labelOn: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
    * Identifier used to show th options of the select
    * @prop {String}
    */
    optionIdentifier: {
      type: String,
      required: true
    },

    /**
      * Params you want to get from the options of the select, for example the
      * id. The params will be sent like an object.
      * @prop {Array}
      */
    paramsToGet: {
      type: Array,
      required: true
    },

    /**
      * Params that you want to see in the options of the select, for example
      * the id. The params will be shown from left to right according with the
      * position in the array.
      * @prop {Array}
      */
    paramsToSee: {
      type: Array,
      required: true
    },

    /**
    * Position of the label, it could be: label-left, label-right, label-center,
    *  is-horizontal(for horizontal forms)
    * @prop {String} [position = 'label-left']
    */
    position: {
      type: String,
      required: false,
      default: 'label-left'
    },

    /**
     * Sets if the component is rounded or not
     * @prop {Boolean} [rounded = false]
     */
    rounded: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
    * Represent de model to be associated with the v-model
    * @prop {Object|String} [value = null] represents the model from the parent, it most match with
    * the params to get
    */
    value: {
      type: Object | String,
      required: false,
      default: null
    }
  },

  data () {
    return {
      /**
        * Property used to save the option selected by the user.
        */
      selectedItem: null
    }
  },

  computed: {
    /**
      * Computed property used to select the CSS classes for the **label**
      * @return {object} With the classes to be use according with the property
      * **position**
      */
    dinamicClassLabel () {
      return {
        'field-label': true,
        'is-normal': true,
        'has-text-right': (this.position === 'label-right'),
        'has-text-left': (this.position === 'label-left'),
        'has-text-centered': (this.position === 'label-center')
      }
    },

    /**
      * Computed property used to select the CSS classes for the container
      * **field**
      * @return {object} With the classes to be use according with the property
      * **position**
      */
    dinamicClassField () {
      return {
        'field': true,
        'is-horizontal': (this.position === 'is-horizontal')
      }
    },

    /**
    * Computed property used to select the CSS classes for the **body** when
    * horizontal form is needed
    * @return {object} With the classes to be use according with the property
    * **position**
    */
    dinamicClassBody () {
      return {
        'field-body': (this.position === 'is-horizontal')
      }
    },

    /**
      * Computed property used to select the CSS classes for the **input area**
      * when horizontal form is needed
      * @return {object} With the classes to be use according with the property
      * **position**
      */
    dinamicClassInput () {
      return {
        'field': (this.position === 'is-horizontal')
      }
    },

    /**
     * Sets the value of the selected Item
     * @return {String|Number} Value of the selected Item
     */
    computedValue () {
      if (this.selectedItem && typeof this.selectedItem === 'string') {
        return this.selectedItem
      }
      return (this.selectedItem && this.selectedItem[this.optionIdentifier]) ? this.selectedItem[this.optionIdentifier] : ''
    }
  },

  /**
    * Sets the inicial configuracin and the data of the component
    */
  created () {
    this.selectedItem = this.value
  },

  watch: {
    value (newValue) {
      this.selectedItem = newValue
    }
  },

  methods: {
    /**
     * Emits the string introduced by the user
     * @param  {Object|String|Number} event The event received by th input
     * @public
     */
    inputEmitter: debounce(function (event) {
      /**
       * @event input
       * @type {Object|String|Number}
       */
      this.$emit('input', [event, this.entity])
    }, 500),

    /**
     * Emit the event select when an option is selected
     * @param  {Object|String|Number} selectedValue Represent the option
     * selected
     * @public
     */
    selectEmitter: debounce(function (selectedValue) {
      let option = {}
      this.selectedItem = selectedValue
      if (this.selectedItem) {
        for (let item of this.paramsToGet) {
          option[item] = this.selectedItem[item]
        }
      }
      /**
       * @event select
       * @type {Object}
       */
      this.$emit('select', option)
    }, 500),

    /**
     * Translate the label of the component
     * @param  {String} entity Represents the entity or type of search-select
     * @return {String}        Label translated
     * @public
     */
    translateTemplate (entity, message) {
      return this.$i18n.t(`entities.${entity}.${message}.label`)
    },

    /**
     * Translate the messages of the component
     * @param  {String} message Represents the message to be translated
     * @return {String}        Label translated
     * @public
     */
    translateMessage (message) {
      return this.$i18n.t(`messages.${message}.label`)
    }
  }
}
</script>

<style scoped>
</style>



