<template>
  <div :class="dynamicClassAlignTitle">
    <p :class="[dynamicClassWeightTitle,dynamicClassSizeTitle,dynamicClassTitleTransformation,dynamicClassColorTitle]">
     {{translateLabel(title)}}
     <!-- @slot uses this slot-->
     <slot></slot>
    </p>
  </div>
</template>

<script>
/**
 * Configurable title that allows to set the following functions:
 * * Color tile
 * * Transformation title
 * * Align title
 * * Weight title
 * * Size title
 * @vue UITitle.vue
 * @version 0.1.0
 * @since 0.1.0
 * @author Alfredo Garcia [gitlab:@garciareal45]
 * @copyright Karanta, CA. 2018
 */
export default {
  name: 'UiTitle',

  props: {

    /**
     * Property that receives a tag for translating on the title
     * @type {String}
     */
    title: {
      type: String,
      required: false
    },
    /**
     * Property that sets the color class of the title
     * @type {String} [color= black]
     */
    color: {
      type: String,
      required: false,
      default: 'black'
    },

    /**
     * Property that sets the weight class of the title
     * @type {String} [weight= normal]
     */
    weight: {
      type: String,
      required: false,
      default: 'normal'
    },

    /**
     * Property that sets the align class of the title
     * @type {String} [align= center]
     */
    align: {
      type: String,
      required: false,
      default: 'center'
    },

    /**
     * Property that sets the size class of the title
     * @type {Number} [size= 3]
     */
    size: {
      type: Number,
      required: false,
      default: 3
    },

    /**
     * Property that sets the transformation class of the title
     * @type {String} [transformation= none]
     */
    transformation: {
      type: String,
      required: false,
      default: 'none'
    }

  },

  computed: {

     /**
       * Computed property that determines the size class
       * @return {String}
       */
    dynamicClassSizeTitle () {
      if (this.size >= 1 && this.size <= 7) {
        return 'is-size-' + this.size
      }

      return 'is-size-3'
    },

     /**
       * Computed property that determines the align class title
       * @return {String}
       */
    dynamicClassAlignTitle () {
      if (['centered', 'left', 'right'].indexOf(this.align) !== -1) {
        return 'has-text-' + this.align
      }

      return 'has-text-centered'
    },

     /**
       * Computed property that determines the color class title
       * @return {String}
       */
    dynamicClassColorTitle () {
      let colores = [
        'white',
        'black',
        'light',
        'dark',
        'primary',
        'info',
        'link',
        'success',
        'warning',
        'danger',
        'black-bis',
        'black-ter',
        'grey-darker',
        'grey-dark',
        'grey',
        'grey-light',
        'grey-lighter',
        'white-ter',
        'white-bis'
      ]

      if (colores.indexOf(this.color) !== -1) {
        return 'has-text-' + this.color
      }

      return 'has-text-black'
    },

    /**
     * Computed property that determines the transformation class title
     * @return {String}
     */
    dynamicClassTitleTransformation () {
      return {
        '': (this.transformation === 'none'),
        'is-capitalized': (this.transformation === 'capitalized'),
        'is-lowercase': (this.transformation === 'lowercase'),
        'is-uppercase': (this.transformation === 'uppercase'),
        'is-italic': (this.transformation === 'italic'),
        'is-italic is-capitalized': (this.transformation === 'italic capitalized'),
        'is-italic is-lowercase': (this.transformation === 'italic lowercase'),
        'is-italic is-uppercase': (this.transformation === 'italic uppercase')
      }
    },

     /**
       * Computed property that determines the weight class title
       * @return {String}
       */
    dynamicClassWeightTitle () {
      if (['light', 'normal', 'semibold', 'bold'].indexOf(this.weight)) {
        return 'has-text-weight-' + this.weight
      }

      return 'has-text-weight-normal'
    }

  },

  methods: {

     /**
     * Method that translates label sent as parameter.
     * @returns {String}
     * @public
     **/
    translateLabel (value) {
      if (value !== undefined) {
        return (this.$slots.default === undefined) ? this.$i18n.t(`template.${value}.label`) : ''
      }
    }
  }
}
</script>

<style scoped>
</style>


