<template>
  <div>
  <title-component 
  :element="titlePage"
  titleClass="is-size-2"
  :isPage="true">
  </title-component>
  <hr>
  <div class="columns">
    <b-field label="Chart type 1">
      <b-select
      v-model="series[0].type"
      rounded>
        <option
        v-for="(item,key) in chartTypes"
        :key="key">
          {{item}}
        </option>
      </b-select>
    </b-field>
    <b-field label="Chart type 2">
      <b-select
      v-model="series[1].type"
      rounded>
        <option
        v-for="(item,key) in chartTypes"
        :key="key">
          {{item}}
        </option>
      </b-select>
    </b-field>
    <b-field label="Chart type 3">
      <b-select
      v-model="series[2].type"
      rounded>
        <option
        v-for="(item,key) in chartTypes"
        :key="key">
          {{item}}
        </option>
      </b-select>
    </b-field>
    <b-field label="Chart color 1">
      <b-input 
      type="color" 
      v-model="colorChart1"
      rounded>
      </b-input>
    </b-field>
    <b-field label="Chart color 2">
      <b-input 
      type="color" 
      v-model="colorChart2"
      rounded>
      </b-input>
    </b-field>
    <b-field label="Chart color 3">
      <b-input 
      type="color" 
      v-model="colorChart3"
      rounded>
      </b-input>
    </b-field>
  </div>
   <ui-chart
   :title="title"
   :chartType="chartType"
   :xAxis="xAxis"
   :yAxis="dynamicYAxis"
   :series="dynamicSeries">
   </ui-chart>
    <hr>
    <div class="column">
      <h4 class="is-size-3 is-bold has-text-centered has-text-weight-bold">John's</h4>
      <div class="columns">
        <b-field label="apples">
          <b-input type="number" v-model="apples" rounded min="0"/>
        </b-field>
        <b-field label="bananas">
          <b-input type="number" v-model="bananas" rounded min="0"/>
        </b-field>
        <b-field label="oranges">
          <b-input type="number" v-model="oranges" rounded min="0"/>
        </b-field>
        <b-field label="mangoes">
          <b-input type="number" v-model="mangoes" rounded min="0"/>
        </b-field>
        <b-field label="mamones">
          <b-input type="number" v-model="mamones" rounded min="0"/>
        </b-field>
      </div>
    </div>
    <ui-chart
      chartType="column"
      :series="dynamicJhonData"
      v-bind="stackedColumns"
    />
    <hr>
    <hr>
      <ui-chart
        chartType="bar"
        :series="dynamicJhonData"
        v-bind="stackedBars"
      />
    <hr>
   <div class="columns">
     <b-field label="Navigator enabled">
       <b-switch
       v-model="navigator.enabled"
       rounded></b-switch>
     </b-field>
   </div>
   <!-- Highstock -->
   <ui-chart
   :vueChartType="vueChartType"
   :title="title2"
   :yAxis="yAxis2"
   :series="data2"
   :navigator="navigator"
   :rangeSelector="rangeSelector"
   :legend="legend">
   </ui-chart>
    <hr>
    <div class="positionRight">
        <b-tooltip
        label="Changelog"
        :active="true"
        position="is-left"
        type="is-dark">
          <ui-panel-container
            :panelHeight= "210"
            :panelWidth= "600"
             unitHeight="px"
             unitWidth="px"
            backgroundColor="white"
            panelBorder="ridge"
            panelBorderColor="black"
            :collapseButtonEnabled="true"
            collapseButtonPosition="left"
            collapseButtonBackgroundColor="#C2C2C2"
            :style="collapseLeft"
            @on-collapse="collapsePanelLeft">
              <markdown></markdown>
            </ui-panel-container>
        </b-tooltip>
      </div>
  </div>
</template>
<script>
import UIChart from '../../../components/common/UIChart/index.js'
import { updates } from '../../../libraryUpdates.js'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import readme from '../../../components/common/UIChart/changelog.md'
import titleComponent from '../titleComponent.vue'

const dataChart = require('./data.json')
const dataChart2 = require('./data2.json')
export default {
  name: 'UiChartPage',

  components: {
    'UiChart': UIChart,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },

  data () {
    return {
      /**
       * Version data
       */
      collapseLeft: {},
      titlePage: updates.Chart,
      /*
      Variables for the interactive page
       */
      colorChart1: '#10BAEE',
      colorChart2: '#7DF32A',
      colorChart3: '#EEEF23',
      /**
       * Johns apples
       * @types {Number}
       */
      apples: 5,
      /**
       * Johns bananas
       * @types {Number}
       */
      bananas: 4,
      /**
       * Johns oranges
       * @types {Number}
       */
      oranges: 3,
      /**
       * Johns mangoes
       * @types {Number}
       */
      mangoes: 7,
      /**
       * Johns mamones
       * @types {Number}
       */
      mamones: 2,

      /*
       Data chart 1
       */
      chartType: 'line',

      title: {
        text: 'Fruit Consumption'
      },

      series: [{
        type: 'line',
        name: 'Chart 1',
        color: '#10BAEE',
        yAxis: 0,
        data: [1, 0, 4, 7, 9]
      },
      {
        type: 'spline',
        name: 'Chart 2',
        color: '#7DF32A',
        yAxis: 1,
        data: [5, 7, 3, 6, 10]
      },
      {
        type: 'column',
        name: 'Chart 3',
        color: '#EEEF23',
        yAxis: 2,
        data: [8, 2, 5, 3, 10]
      }
      ],

      xAxis: {
        categories: ['Apples', 'Bananas', 'Oranges', 'Mangoes', 'Mamones']
      },

      yAxis: [{
        title: {
          text: 'Fruit eaten',
          style: {
            color: '#10BAEE',
            fontWeight: 'bold'
          }
        },
        lineColor: '#10BAEE',
        lineWidth: '5',
        showEmpty: false
      },
      {
        title: {
          text: 'Fruit eaten',
          style: {
            color: '#7DF32A',
            fontWeight: 'bold'
          }

        },
        lineColor: '#7DF32A',
        lineWidth: '5',
        showEmpty: false,
        opposite: true
      },
      {
        title: {
          text: 'Fruit eaten',
          style: {
            color: '#EEEF23',
            fontWeight: 'bold'
          }

        },
        lineColor: '#EEEF23',
        lineWidth: '5',
        showEmpty: false,
        opposite: true
      }],
      /**
       * End
       */

      /*
      Data chart 2
       */
      vueChartType: 'highstock',
      yAxis2: [{
        title: {
          text: 'Variable 1',
          style: {
            color: '#F8194F',
            fontWeight: 'bold'
          }
        },
        lineColor: '#F8194F',
        lineWidth: '5',
        showEmpty: false,
        opposite: false
      },
      {
        title: {
          text: 'Variable 2',
          style: {
            color: '#0D1BD8',
            fontWeight: 'bold'
          }
        },
        lineColor: '#0D1BD8',
        lineWidth: '5',
        showEmpty: false,
        opposite: true
      }],

      data2: [{
        name: 'Variable 1',
        yAxis: 0,
        type: 'areaspline',
        data: dataChart2.data,
        color: '#F8194F',
        pointStart: dataChart2.pointStart,
        pointInterval: dataChart2.pointInterval,
        tooltip: {
          valueDecimals: 2
        }
      },
      {
        name: 'Variable 2',
        yAxis: 1,
        type: 'line',
        data: dataChart,
        color: '#0D1BD8',
        tooltip: {
          valueDecimals: 2
        }
      }],
      title2: {
        text: 'History chart'
      },
      navigator: {
        enabled: false
      },
      rangeSelector: {
        selected: 2,
        allButtonsEnabled: true,
        buttons: [
          {
            type: 'day',
            count: 1,
            text: 'Day'
          },
          {
            type: 'month',
            count: 1,
            text: 'Month'
          },
          {
            type: 'year',
            count: 1,
            text: 'Year'
          },
          {
            type: 'all',
            text: 'All'
          }
        ]
      },
      legend: {
        enabled: true,
        align: 'center',
        backgroundColor: '#FFFFFF',
        borderColor: 'black',
        borderWidth: 2,
        layout: 'horizontal'
      },
      stackedColumns: {
        chart: {
          type: 'column'
        },
        title: {
          text: 'Stacked column chart'
        },
        xAxis: {
          categories: ['Apples', 'Oranges', 'Pears', 'Grapes', 'Bananas']
        },
        yAxis: {
          min: 0,
          title: {
            text: 'Total fruit consumption'
          },
          stackLabels: {
            enabled: true,
            style: {
              fontWeight: 'bold'
            }
          }
        },
        plotOptions: {
          column: {
            stacking: 'normal',
            datalabels: {
              enabled: true
            }
          }
        },
        series: [{
          name: 'John',
          color: '#10BAEE',
          data: [5, 3, 4, 7, 2]
        },
        {
          name: 'Jane',
          color: '#7DF32A',
          data: [2, 2, 3, 2, 1]
        },
        {
          name: 'Joe',
          color: '#EEEF23',
          data: [3, 4, 4, 2, 5]
        }]
      },
      stackedBars: {
        chart: {
          type: 'bar'
        },
        title: {
          text: 'Stacked bar chart'
        },
        xAxis: {
          categories: ['Apples', 'Oranges', 'Pears', 'Grapes', 'Bananas']
        },
        yAxis: {
          min: 0,
          title: {
            text: 'Total fruit consumption'
          },
          stackLabels: {
            enabled: true,
            style: {
              fontWeight: 'bold'
            }
          }
        },
        plotOptions: {
          series: {
            stacking: 'normal',
            datalabels: {
              enabled: true
            }
          }
        },
        series: [{
          name: 'John',
          color: '#10BAEE',
          data: [5, 3, 4, 7, 2]
        },
        {
          name: 'Jane',
          color: '#7DF32A',
          data: [2, 2, 3, 2, 1]
        },
        {
          name: 'Joe',
          color: '#EEEF23',
          data: [3, 4, 4, 2, 5]
        }]
      }
    }
  },

  computed: {
    chartTypes () {
      let types = [
        'line',
        'spline',
        'bar',
        'column',
        'area',
        'areaspline'
      ]

      return types
    },

    dynamicSeries () {
      this.series[0].color = this.colorChart1
      this.series[1].color = this.colorChart2
      this.series[2].color = this.colorChart3
      this.stackedBars.series[0].color = this.colorChart1
      this.stackedBars.series[1].color = this.colorChart2
      this.stackedBars.series[2].color = this.colorChart3
      this.stackedColumns.series[0].color = this.colorChart1
      this.stackedColumns.series[1].color = this.colorChart2
      this.stackedColumns.series[2].color = this.colorChart3
      return this.series
    },

    dynamicYAxis () {
      this.yAxis[0].title.style.color = this.colorChart1
      this.yAxis[1].title.style.color = this.colorChart2
      this.yAxis[2].title.style.color = this.colorChart3
      this.yAxis[0].lineColor = this.colorChart1
      this.yAxis[1].lineColor = this.colorChart2
      this.yAxis[2].lineColor = this.colorChart3
      return this.yAxis
    },

    dynamicJhonData () {
      let data = [+this.apples, +this.bananas, +this.oranges, +this.mangoes, +this.mamones]
      this.stackedColumns.series.find(serie => serie.name === 'John').data = data
      return this.stackedColumns.series
    }
  },
  methods: {
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>
<style>
.positionRight {
  display: flex;
  justify-content: flex-end;
}
</style>



