<template>
  <div>
    <keep-alive>
      <component
        :is="currentComponent"
        v-model="modelComponent"
        v-bind.sync="propsComponent"
        v-on="eventListeners"
      ></component>
    </keep-alive>
  </div>
</template>

<script>

import UiManagementPanelEdition from '../../common/UIManagementPanelEdition'
import UiManagementPanelFilters from '../../common/UIManagementPanelFilters'
import UiManagementPanelShowHideColumns from '../../common/UIManagementPanelShowHideColumns'

/**
 * @vue UIDynamicComponent.vue
 * UIDynamic allows to render and manage the necessary
 * events and props of the components:
 *  * UIManagementPanelFilters
 *  * UIManagementPanelShowHideColumns
 *  * UIManagementPanelEdition
 * @version 0.1.0
 * @since 0.1.0
 * @author Nelson Rodriguez [gitlab:@nelrod]
 * @copyright Karanta, CA. 2018
 */

export default {
  name: 'UiDynamicPanel',
  components: {
    UiManagementPanelEdition,
    UiManagementPanelFilters,
    UiManagementPanelShowHideColumns
  },
  props: {

    /**
     * Name of the component that is rendered
     * @prop {String} [currentComponent = 'ui-management-panel-edition']
     */
    currentComponent: {
      type: String,
      default: 'ui-management-panel-edition',
      required: false
    },

    /**
     * Configuration object require to component rendered
     * @prop {Array} modelComponent
     */
    modelComponent: {
      type: Array,
      require: true
    },

    /**
     * Props object require to component rendered
     * @prop {Object} propsComponent
     */
    propsComponent: {
      type: Object,
      require: true
    },

    /**
     * Array of event to listen of the children components
     * @prop {Array} [eventsToListen = ['on-filters-apply', 'input', 'save', 'update:edition', 'close']]
     */
    eventsToListen: {
      type: Array,
      require: false,
      default: function () {
        return [
          'on-filters-apply',
          'input',
          'save',
          'update:edition',
          'close'
        ]
      }
    }
  },
  data () {
    return {
      /**
       * List of functions that handle the events of the children
       */
      eventslist: {}
    }
  },
  computed: {
    /**
     * Adds the list of functions that handle the events of the children components to listeners
     * @return {Objec} Object with the functions that handle the events of the children components
     * @public
     */
    eventListeners () {
      return Object.assign(
        {},
        this.$listeners,
        this.eventslist
      )
    }
  },
  watch: {
    /**
     * Watches the event object that handle events of the children components
     */
    eventsList: {
      handler: 'eventObject',
      immediate: true
    }
  },
  methods: {
    /**
     * Creates the event object to handler events of the children components
     * @public
     */
    eventObject () {
      const self = this
      for (const key in self.eventsToListen) {
        self.eventslist[self.eventsToListen[key]] = function (data) {
          data ? self.$emit(self.eventsToListen[key], data) : self.$emit(self.eventsToListen[key])
        }
      }
    }
  }
}
</script>

<style>

</style>



