<template>
  <div :class="dinamicClassField">
    <div v-if="labelOn" :class="dinamicClassLabel">
      <label>{{translateTemplate(paramsToGet[0])}}</label>
    </div>
      <div :class="dinamicClassBody">
        <div
        :class = "dinamicClassInput">
        <b-input
          v-model = "measure[paramsToGet[0]]"
          expanded
          type = "number"
          :min = "min"
          :max = "max"
          :step = "step"
          :placeholder = "translateMessage()"
          :rounded="rounded"
          :disabled="disabled"
          v-bind="$attrs"
          @input = "eventEmiter"
          >
        </b-input>
        <b-select
          v-model = "measure[paramsToGet[1]]"
          :placeholder = "translateMessage ('Uom')"
          :loading = "loading"
          :disabled = "loading || disabled"
          :rounded="rounded"
          v-bind="$attrs"
          @input = "eventEmiter"
          >
          <option
            value = null
            :disabled = "!measure[paramsToGet[1]] || measure[paramsToGet[1]] === 'null'">
          </option>
          <option
            v-for = "(item, key) in unitsOfMeasure"
            :key = "key"
            :value = "item['uomId']"
            >
            {{`${item['uomId']}-${item['uomSystemId']}`}}
          </option>
        </b-select>
        <b-select
          v-if = "paramsToGet[2]"
          v-model = "measure[paramsToGet[2]]"
          :placeholder = "translateMessage ('Ouom')"
          :loading = "loading"
          :disabled = "loading || disabled"
          :rounded="rounded"
          v-bind="$attrs"
          @input = "eventEmiter"
          >
          <option
            value = null
            :disabled = "!measure[paramsToGet[2]] || measure[paramsToGet[2]] === 'null'">
          </option>
          <option
            v-for = "(item, key) in unitsOfMeasure"
            :key = "key"
            :value = "item['uomId']"
            >
            {{`${item['uomId']}-${item['uomSystemId']}`}}
          </option>
        </b-select>
      </div>
    </div>
  </div>
</template>

<script>
import debounce from 'lodash/debounce'
/**
* @vue UIInputSelectUnitOfMeasure.vue
* @description <ui-units-of-measure> generic component that
* allows to establish a unit of measurement filtered by system of units,
* type of magnitude, default values. Also allows to establish the maximum
* values, minimum values and quantity of decimal numbers. Aditional to that,
* allows to show or not the label
* Sets the placeholders visibility on the component input and the selects
* @author Cruz Ortiz [gitlab:@cortiz99]
* @version 0.1.0
* @since 0.1.0
* @copyright Karanta CA. 2018
**/
export default {
  name: 'UiUnitOfMeasure',

  model: {
    prop: 'value',
    event: 'input'
  },

  props: {
    /**
     * Represents the data for the select
     * @prop {Array}
     */
    data: {
      type: Array,
      required: true
    },

    /**
     * Sets the component disabled
     * @prop {Boolean} [disabled = false]
     */
    disabled: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * Used to show or not the label
     * @prop {Boolean} [labelOn = false]
     */
    labelOn: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
    * Sets the loading state in the component
    * @prop {Boolean} [loading = false]
    */
    loading: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * Type of magnitude witch is measure
     * @prop {String} magnitudeType
     */
    magnitudeType: {
      type: String,
      required: true
    },

    /**
     * Represents the maximum value of the measure
     * @prop {Number} [max = null]
     */
    max: {
      type: Number,
      required: false,
      default: null
    },

    /**
     * Represents the minimum value of the measure
     * @prop {Number} [min = null]
     */
    min: {
      type: Number,
      required: false,
      default: null
    },

    /**
    * Position of the label, it could be: label-left, label-right, label-center,
    *  is-horizontal(for horizontal forms)
    * @prop {String} [position = 'label-left']
    */
    position: {
      type: String,
      required: false,
      default: 'label-left'
    },

    /**
     * Sets the elements rounded
     * @prop {Boolean} [rounded = false]
     */
    rounded: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * Represents the precision of the input and the step that can be made on
     * it self
     * @prop {Number} [step = null]
     */
    step: {
      type: Number,
      required: false,
      default: null
    },

    /**
     * Represents the unit system used
     * @prop {String} [unitSystem = '']
     */
    unitSystem: {
      type: String,
      required: false,
      default: ''
    },

    /**
     * Represents the model associated with the component
     * @prop {Object} [value = {measureValue: null, uomId: null, ouomId: null}]
     */
    value: {
      type: Object,
      required: false,
      default: {
        measureValue: null,
        uomId: null,
        ouomId: null
      }
    },

    /**
     * Sets the placeholders visibility on the component input and the selects
     * @prop {Boolean} [isPlaceholderActivated= true]
     */
    isPlaceholderActivated: {
      type: Boolean,
      required: false,
      default: true
    }
  },

  data () {
    return {
      measure: {},
      paramsToGet: null
    }
  },

  computed: {
    /**
    * Computed property used to select the CSS classes for the **body** when
    * horizontal form is needed
    * @public
    * @return {Object} With the classes to be use according with the property
    * **position**
    */
    dinamicClassBody () {
      return {
        'field-body': (this.position === 'is-horizontal')
      }
    },

    /**
    * Computed property used to select the CSS classes for the container
    * **field**
    * @public
    * @return {Object} With the classes to be use according with the property
    * **position**
    */
    dinamicClassField () {
      return {
        'field': true,
        'is-horizontal': (this.position === 'is-horizontal')
      }
    },

    /**
      * Computed property used to select the CSS classes for the container
      * **field**
      * @public
      * @return {Object} With the classes to be use according with the property
      * **position**
      */
    dinamicClassInput () {
      return {
        'field': true,
        'has-addons': true
      }
    },

    /**
      * Computed property used to select the CSS classes for the **label**
      * @public
      * @return {Object} With the classes to be use according with the property
      * **position**
      */
    dinamicClassLabel () {
      return {
        'field-label': true,
        'is-normal': true,
        'has-text-right': (this.position === 'label-right'),
        'has-text-left': (this.position === 'label-left'),
        'has-text-centered': (this.position === 'label-center')
      }
    },

    /**
     * Sets the options according with specified
     * @return {Array} Represents the data filtrated
     * @public
     */
    unitsOfMeasure () {
      let dataFiltrared = []
      for (let measure of this.data) {
        if (this.selectFilter(measure)) {
          dataFiltrared.push(measure)
        }
      }
      return dataFiltrared
    }
  },

  created () {
    this.measure = this.value
    this.paramsToGet = Object.keys(this.value)
    this.measure[this.paramsToGet[1]] = this.data[0]['uomId']
    if (typeof this.measure[this.paramsToGet[2]] !== 'undefined') {
      this.measure[this.paramsToGet[2]] = this.data[0]['uomId']
    }
  },

  methods: {
    eventEmiter: debounce(
      function () {
        /**
         * Emits the event with the object of the measure.
         * @event input
         * @type {Object}
         */
        this.$emit('input', this.measure)
      },
      500),

    /**
     * Translates the received parameter
     * @param {Array}
     * @returns {String}
     * @public
     **/
    translateTemplate (value) {
      return this.$i18n.t(`template.${value}.label`)
    },

  /**
   * Translates the received parameter
   * @param {String}
   * @returns {String}
   * @public
   **/
    translateMessage (value) {
      if (this.isPlaceholderActivated) {
        return (value) ? this.$i18n.t(`messages.placeholder${value}.label`) : this.$i18n.t(`messages.placeholder.label`)
      }
      return ''
    },

    /**
    * Filters the data shown in the select
    * @param {String}
    * @public
    * @returns {Boolean}
    **/
    selectFilter (unit) {
      return this.unitSystem ? unit.uomSystemId.toLowerCase() ===
      this.unitSystem.toLowerCase() && unit.uomQuantityType.toLowerCase() ===
      this.magnitudeType.toLowerCase() : unit.uomQuantityType.toLowerCase() ===
      this.magnitudeType.toLowerCase()
    }
  }
}
</script>

<style scoped>
</style>



