<template>
	<div>
   <title-component
   :element="titlePage"
   titleClass="is-size-2"
   :isPage="true">
   </title-component>
		<section class="columns">
			<div class="column">
				<b-field grouped>
					<b-field label="Enable Search">
            <b-switch
              v-model="searchable"
						  type="is-info"
            ></b-switch>
					</b-field>
					<b-field label="Number of Children">
						<b-switch
              v-model="numberChildren"
						  type="is-info"
            ></b-switch>
					</b-field>
					<b-field label="Show Icons">
						<b-switch
              v-model="showIcon"
              type="is-info"
            ></b-switch>
					</b-field>
				</b-field>
				<b-field
          label="Search Attribute"
          v-show="searchable"
        >
					<b-select
            placeholder="Select Attribute"
					  v-model="searchAtt"
          >
            <option
              v-for="(att, index) in ['label', 'id', 'icon']"
						  :value="att"
						  :key="index"
            >
              {{ att }}
            </option>
					</b-select>
				</b-field>
			</div>
			<section class="column">
				<b-message
          title="Console"
          :closable="false"
          >
					<b-message
            :title="eventname"
            size="is-small"
            :closable="false"
          >
            <pre
              class="scroll-able event-size"
            >{{ eventListener }}</pre>
          </b-message>
				</b-message>
			</section>
		</section>
		<hr>
		<div class="columns row-height">
			<div class="column">
				<ui-navigation-tree
          :accordion="accordion"
          :data="dataTree"
          :defaultExpandedKeys="[dataTree[0].id]"
				  :number-children="numberChildren"
          :searchable="searchable"
          :search-att="searchAtt"
				  :show-icon="showIcon"
				  @selected-node="selectedEvent"
        ></ui-navigation-tree>
			</div>
			<div class="column">
				<ui-navigation-tree
          :accordion="accordion"
          :data="dataTree"
          :defaultExpandedKeys="[dataTree[0].id]"
          :number-children="numberChildren"
          :searchable="searchable"
          :search-att="searchAtt"
				  :show-icon="showIcon"
				  :show-checkbox="true"
				  @checked-node="checkedEvent"
        ></ui-navigation-tree>
			</div>
		</div>
      <hr>
      <div class="positionRight">
        <b-tooltip
        label="Changelog"
        :active="true"
        position="is-left"
        type="is-dark">
          <ui-panel-container
            :panelHeight= "150"
            :panelWidth= "600"
             unitHeight="px"
             unitWidth="px"
            backgroundColor="white"
            panelBorder="ridge"
            panelBorderColor="black"
            :collapseButtonEnabled="true"
            collapseButtonPosition="left"
            collapseButtonBackgroundColor="#C2C2C2"
            :style="collapseLeft"
            @on-collapse="collapsePanelLeft">
              <markdown></markdown>
            </ui-panel-container>
        </b-tooltip>
      </div>
	</div>
</template>

<script>
import UiNavigationTree from '../../../components/common/UINavigationTree/index.js'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import titleComponent from '../titleComponent.vue'
import { updates } from '../../../libraryUpdates.js'
import readme from '../../../components/common/UINavigationTree/changelog.md'

export default {
  components: {
    UiNavigationTree,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },
  data () {
    return {
      /**
       * Version data
       */
      titlePage: updates.NavigationTree,
      collapseLeft: {},
      /**
       *  Data tree configurations
       */
      dataTree: [
        {
          id: '1',
          label: 'Level one 1',
          index: '12',
          disabled: true,
          children: [
            {
              id: '2',
              label: 'Level two 1-1',
              children: [
                {
                  id: '3',
                  label: 'Level three 1-1-1',
                  icon: 'fas fa-dot-circle'
                }
              ]
            }
          ]
        },
        {
          id: '4',
          label: 'Level one 2',
          children: [
            {
              id: '5',
              label: 'Level two 2-1',
              labelSearch: 'LEVEL TWO 2-1',
              children: [
                {
                  id: '13',
                  label: 'Level three 2-1-1',
                  icon: 'fas fa-dot-circle'
                }
              ]
            },
            {
              id: '6',
              label: 'Level two 2-2',
              labelSearch: 'LEVEL TWO 2-2',
              children: [
                {
                  id: '7',
                  label: 'Level three 2-2-1',
                  icon: 'fas fa-ban'
                }
              ]
            }
          ]
        },
        {
          id: '8',
          label: 'Level one 3',
          children: [
            {
              id: '9',
              label: 'Level two 3-1',
              children: [
                {
                  id: '10',
                  label: 'Level three 3-1-1',
                  icon: 'fas fa-ban'
                }
              ]
            },
            {
              id: '11',
              label: 'Level two 3-2',
              children: [
                {
                  id: '12',
                  label: 'Level three 3-2-1',
                  icon: 'fas fa-adjust'
                },
                {
                  id: '14',
                  label: 'Level three 3-2-1',
                  icon: 'far fa-circle'
                }
              ]
            }
          ]
        }
      ],
      eventListener: '',
      eventname: '',
      searchable: true,
      numberChildren: false,
      showIcon: false,
      searchAtt: 'labelSearch',
      accordion: true
    }
  },
  methods: {
    selectedEvent (data) {
      this.eventname = 'selected-node'
      this.eventListener = data
    },
    checkedEvent (data) {
      this.eventname = 'checked-node'
      this.eventListener = data
    },
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  $rowHeight: 40vh;
  .row-height {
    height: $rowHeight;
  }

  .event-size {
    height: $rowHeight - 15vh;
  }

  .scroll-able {
    overflow-y: auto;
  }

  .positionRight {
  display: flex;
  justify-content: flex-end;
  }
</style>



