<script>
/**
 * This component renders a map, that have the following feature:
 * * Sets the center of the map
 * * Sets the zoom in the map
 * * Sets the size of the map
 * * Puts markers, areas and lines on the map
 * * Sets icons on the markers
 * * Sets infoWindows on the markers, areas and lines
 * * Sets delay on the renders of markers, areas and lines
 * * Sets custom structure or default structure on the infoWindows
 * * Sets animations on the markers
 * @version 0.1.0
 * @since 0.1.0
 * @author Cruz Ortiz [gitlab:@cortiz99]
 * @copyright Karanta, CA. 2018
 */
export default {
  name: 'UiMap',

  props: {
    /**
     * Centers the map on a location
     * @prop {Object} center
     * @example {
       lat: ...,
       lng: ...
      }
     */
    center: {
      type: Object,
      required: true
    },

    /**
    * Sets the height of the map
    * @prop {Number} [height = 100]
    */
    height: {
      type: Number,
      required: false,
      default: 100
    },

    /**
     * Sets additional configutation to the map
     * @prop {Object} [additionalMapConfig = undefined]
     * @example {
      mapTypeId: ...,
      maxZoom: ...,
      minZoom: ...,
      ...
      }
     */
    additionalMapConfig: {
      type: Object,
      required: false,
      default: undefined
    },

    /**
    * Sets the markers properties on the map
    * @prop {Object|Array} [markers = undefined]
    * @example {
      position: {
        lat: 15.0000,
        lng: -66.000000
      },
      icon: 'https://developers.google.com/maps/documentation/javascript/examples/full/images/beachflag.png',
      label: 'marker 2',
      animation: window.google.maps.Animation.BOUNCE,
      info: {
        title: 'Marker 2',
        prop1: 'value 1',
        prop2: 'value2'
      }
    }
    */
    markers: {
      type: [Object, Array],
      required: false,
      default: undefined
    },

    /**
    * Sets a global animations for the markers
    * @prop {String} [markersAnimation = undefined]
    */
    markersAnimation: {
      type: String,
      required: false,
      default: undefined,
      validator: (value) => {
        return ['BOUNCE', 'DROP', undefined, null].indexOf(value.toUpperCase()) !== -1
      }
    },

    /**
    * Sets a delay to show the markers on the map
    * @prop {Number} [markersDelay = 0]
    */
    markersDelay: {
      type: Number,
      required: false,
      default: 0
    },

    /**
    * Sets the polygons properties on the map
    * @prop {Object|Array} [polygons = undefined]
    * @example {
      paths: [
          {lat: 21.0000000, lng: -66.0000000},
          {lat: 9.0000000, lng: -60.0000000},
          {lat: 9.0000000, lng: -72.0000000}
      ],
      strokeColor: 'blue',
      strokeOpacity: 0,
      fillColor: 'blue',
      fillOpacitiy: 1,
      title: 'Area2',
      slotContent: '<p>Some <strong>HTML</strong> structure </p>'
    }
    */
    polygons: {
      type: [Object, Array],
      required: false,
      default: undefined
    },

    /**
     * Sets a delay to show the polygons on the map
     * @prop {Number} [polygonsDelay = 0]
     */
    polygonsDelay: {
      type: Number,
      required: false,
      default: 0
    },

    /**
    * Sets the polylines properties on the map
    * @prop {Object|Array} [polylines = undefined]
    * @example {
      path: [
        {lat: 9.0000000, lng: -50.0000000},
        {lat: 15.0000000, lng: -66.0000000},
        {lat: 9.0000000, lng: -80.0000000}
      ],
      strokeColor: '#00da8c',
      strokeOpacity: 1,
      strokeWeight: 3,
      info: {
        title: 'Polyline 2',
        prop1: 'value 1',
        prop2: 'value 2'
      }
    }
    */
    polylines: {
      type: [Object, Array],
      required: false,
      default: undefined
    },

    /**
     * Sets a delay to show the polylines on the map
     * @prop {Number} [polylinesDelay = 0]
     */
    polylinesDelay: {
      type: Number,
      required: false,
      default: 0
    },

    /**
     * Sets the width of the map
     * @prop {Number} [width = 100]
     */
    width: {
      type: Number,
      required: false,
      default: 100
    },

    /**
     * Specifies the unit of measure of the height and width of the map
     * @prop {String} [sizeUnit = '%']
     */
    sizeUnit: {
      type: String,
      required: false,
      default: '%',
      validator: function (value) {
        return ['px', '%', 'rem'].indexOf(value.toLowerCase()) !== -1
      }
    },

    /**
    * Sets the zoom on the map
    * @prop {Number} [zoom = 4]
    */
    zoom: {
      type: Number,
      required: false,
      default: 4,
      validator: function (value) {
        return (value < 15) && (value > 0)
      }
    }
  },

  data () {
    return {
      /**
       * Instance of the map
       * @type {Map}
       */
      gmap: null,

      /**
       * Instances of the markers
       * @type {Array}
       */
      gmarkers: [],

      /**
       * Instances of the polygons
       * @type {Array}
       */
      gpolygons: [],

      /**
       * Instances of the polylines
       * @type {Array}
       */
      gpolylines: []
    }
  },
  computed: {
    /**
     * Update map markers
     */
    updateMarkers () {
      let markers = this.markers
      return markers
    }
  },

  mounted () {
    this.initMap()
  },

  updated () {
    this.initMap()
  },

  methods: {
    /**
     * Renders the main element of the component
     * @param  {Function} h Represents the render function
     * @return {VNode}   The main VNode
     * @public
     */
    blockRender (h) {
      let vm = this
      return (
      <div
          id="map"
          class="map-container"
          {...this.markers}
          {...this.polygons}
          {...this.polylines}
          style={{width: `${vm.width}${vm.sizeUnit.toLowerCase()}`, height: `${vm.height}${vm.sizeUnit.toLowerCase()}`, backgroundColor: 'lightgray'}}>
      </div>
      )
    },

    /**
    * Builder of the goolge map
    * @public
    */
    initMap () {
      if (window.google) {
        this.gmap = new window.google.maps.Map(this.$el, {
          zoom: this.zoom,
          center: this.center,
          ...this.additionalMapConfig
        })
        if (this.markers) {
          let markers = this.markers
          // Adding a markers
          setTimeout(() => {
            if (Array.isArray(markers)) {
              markers.map((marker) => {
                this.gmarkers.push(new window.google.maps.Marker({
                  ...marker,
                  map: this.gmap
                }))
                // Adding an animation
                this.setMarkerAnimation(this.gmarkers[this.gmarkers.length - 1])
                // Adding a infoWindow
                this.setMarkerInfoWindow(this.gmap, this.gmarkers[this.gmarkers.length - 1])
              })
            } else {
              this.gmarkers.push(new window.google.maps.Marker({
                ...markers,
                map: this.gmap
              }))
              // Adding an animation
              this.setMarkerAnimation(this.gmarkers[this.gmarkers.length - 1])
              // Adding a infoWindow
              this.setMarkerInfoWindow(this.gmap, this.gmarkers[this.gmarkers.length - 1])
            }
          }, this.markersDelay)
        }
        if (this.polygons) {
          let polygons = this.polygons
          // Adding a polygons
          setTimeout(() => {
            if (Array.isArray(polygons)) {
              polygons.map((polygon) => {
                this.gpolygons.push(new window.google.maps.Polygon({
                  ...polygon,
                  map: this.gmap
                }))
                // Adding a infoWindow
                this.setPolygonInfoWindow(this.gmap, this.gpolygons[this.gpolygons.length - 1])
              })
            } else {
              this.gpolygons.push(new window.google.maps.Polygon({
                ...polygons,
                map: this.gmap
              }))
              // Adding a infoWindow
              this.setPolygonInfoWindow(this.gmap, this.gpolygons[this.gpolygons.length - 1])
            }
          }, this.polygonsDelay)
        }
        if (this.polylines) {
          let polylines = this.polylines
          // Adding a polylines
          setTimeout(() => {
            if (Array.isArray(polylines)) {
              polylines.map((polyline) => {
                this.gpolylines.push(new window.google.maps.Polyline({
                  ...polyline,
                  map: this.gmap
                }))
                this.setPolygonInfoWindow(this.gmap, this.gpolylines[this.gpolylines.length - 1])
              })
            } else {
              this.gpolylines.push(new window.google.maps.Polyline({
                ...polylines,
                map: this.gmap
              }))
              this.setPolygonInfoWindow(this.gmap, this.gpolylines[this.gpolylines.length - 1])
            }
          }, this.polylinesDelay)
        }
      }
    },

    /**
    * Sets the global animation for the markers
    * @param {Marker} marker [description]
    * @public
    */
    setMarkerAnimation (marker) {
      if (this.markersAnimation) {
        let animation = (this.markersAnimation.toUpperCase() === 'BOUNCE') ? 'BOUNCE' : 'DROP'
        marker.setAnimation(window.google.maps.Animation[animation])
      }
    },

    /**
    * Sets the Marker infoWindow of the marker
    * @param {Map} map    A google map element
    * @param {Marker} marker A google marker element
    * @public
    */
    setMarkerInfoWindow (map, marker) {
      let vm = this
      // Creating an infowindow
      let infoWindow = new window.google.maps.InfoWindow()
      marker.addListener('click', function (event) {
        let content = `<div class="content">
        <h1 class="is-size-4 has-text-weight-bold is-uppercase">
        ${marker.title || (marker.info && marker.info.title) || ''}
        </h1>`
        if (!marker.slotContent) {
          content += `
          <table class="table is-hoverable">
          <tbody>`
          if (marker.info) {
            for (let data in marker.info) {
              if (data !== 'title') {
                content += `
                <tr>
                <td>${data}</td>
                <td>${marker.info[data]}</td>
                </tr>`
              }
            }
          }
          content += `</tbody></table>`
        } else {
          content += marker.slotContent
        }
        content += `<p class="is-size-7 has-text-weight">${vm.translate('template', 'locationClicked')}: ${event.latLng.lat()}, ${event.latLng.lng()} </p>`
        // Adding the content to the infowindow
        infoWindow.setContent(content)
        // Changing the point to the infowindow
        infoWindow.setPosition(event.latLng)
        // Adding the infowindow to the map. infoWindow.open(map, marker)
        infoWindow.open(map, marker)
      })
    },

    /**
    * Sets the infoWindow on the polygons
    * @param {Map} map     A google map element
    * @param {Marker} polygon A google polygon element
    * @public
    */
    setPolygonInfoWindow (map, polygon) {
      let vm = this
      // Creating an infoWindow
      let infoWindow = new window.google.maps.InfoWindow()
      // Listening to the click on the area
      polygon.addListener('click', function (event) {
        // Building the content
        let content = `<div class="content">
        <h1 class="is-size-4 has-text-weight-bold is-uppercase">
        ${polygon.title || (polygon.info && polygon.info.title) || ''}
        </h1>`
        if (!polygon.slotContent) {
          content += `
          <table class="table is-hoverable">
          <tbody>`
          if (polygon.info) {
            for (let data in polygon.info) {
              if (data !== 'title') {
                content += `
                <tr>
                <td>${data}</td>
                <td>${polygon.info[data]}</td>
                </tr>`
              }
            }
            content += `</tbody></table></div>`
          }
        } else {
          content += polygon.slotContent || ''
        }
        // Adding the location clicked
        content += `
        <br>${vm.translate('template', 'locationClicked')}: ${event.latLng.lat()}, ${event.latLng.lng()}</br>`
        // Adding the content to the infoWindow
        infoWindow.setContent(content)
        // Changing the point of the infoWindow is shown
        infoWindow.setPosition(event.latLng)
        // Adding the infoWindow to the map
        infoWindow.open(map)
      })
    },

    /**
     * Generic translate function
     * @param  {String} seccion  General property on the language file
     * @param  {String} property Specific property of the language file
     * @return {String}          String with the translated message
     * @public
     */
    translate (seccion, property) {
      return this.$i18n.t(`${seccion}.${property}.label`)
    }
  },

  // render/template
  render: function (h) {
    let vm = this
    return (
        vm.blockRender(h)
    )
  }

}
</script>

<style>
</style>



