<template>
  <div class="columns">
    <div class="column">
   <title-component 
   :element="titlePage"
   titleClass="is-size-2"
   :isPage="true">
   </title-component>
      <hr>
      <div class="columns">

        
        <div class="column is-2">
          <b-field label="Image shadow">
            <div class="control">
                <b-select 
                v-model="shadow"
                placeholder="Select a image shadow" 
                rounded>
                  <option value="none">none</option>
                  <option value="shadow-small">shadow-small</option>
                  <option value="shadow">shadow</option>
                  <option value="shadow-large">shadow-large</option>
                </b-select>
            </div>
          </b-field>
        </div>
        <div class="column is-3">
           <b-field label="Shadow color (RGB, Hexadecimal, color)">
           <div class="columns">
              <div class="column">
                <div class="control">
                   <input class="input" type="color" v-model="hoverShadowColor">
                </div>
              </div>
             
           </div>
            
          </b-field>
        </div>
        <div class="column is-2">
           <b-field label="Image shape">
            <div class="control">
                <b-select 
                v-model="shapes"
                placeholder="Select a shape image" 
                rounded>
                  <option value="rounded">rounded</option>
                  <option value="circle">circle</option>
                  <option value="thumbnail">thumbnail</option>
                </b-select>
            </div>
          </b-field>
        </div>
        <div class="column is-2">
          <b-field label="Width">
            <b-input 
              type="number" 
              placeholder="0" 
              :value="width" 
              rounded
              @input="(event) => {width= +event}"></b-input>
          </b-field>
        </div>

        <div class="column is-2">
          <b-field label="Height">
            <b-input type="number" 
            placeholder="0" 
            :value="height"
            @input="(event) => {height= +event}" 
            rounded>
            </b-input>
          </b-field>
        </div>

        <div class="column is-2">
          <b-field label="Hover shadow">
            <b-switch v-model="hover"rounded></b-switch>
          </b-field>
        </div>
        
      </div>
      <br>
        
       <ui-container
       :align="align"
       :sizeCloseButton="sizeCloseButton"
       :closeable= "closeable"
       > 
          <ui-image
          :imagePath="imagePath"
          :shadow= "shadow"
          :shape= "shapes"
          :heightImage="height"
          :widthImage="width"
          :hover="hover"
          :hoverShadowColor="hoverShadowColor"
          >
          </ui-image>
        </ui-container>
      <hr>
      <div class="positionRight">
        <b-tooltip
        label="Changelog"
        :active="true"
        position="is-left"
        type="is-dark">
          <ui-panel-container
            :panelHeight= "150"
            :panelWidth= "600"
             unitHeight="px"
             unitWidth="px"
            backgroundColor="white"
            panelBorder="ridge"
            panelBorderColor="black"
            :collapseButtonEnabled="true"
            collapseButtonPosition="left"
            collapseButtonBackgroundColor="#C2C2C2"
            :style="collapseLeft"
            @on-collapse="collapsePanelLeft">
              <markdown></markdown>
            </ui-panel-container>
        </b-tooltip>
      </div>
    </div>
</div>
</template>

<script>
import UIImage from '../../../components/common/UIImage/index.js'
import UIContainer from '../../../components/common/UIContainer/index.js'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import titleComponent from '../titleComponent.vue'
import { updates } from '../../../libraryUpdates.js'
import readme from '../../../components/common/UIImage/changelog.md'

export default {
  name: 'UiImagePage',
  components: {
    'UiImage': UIImage,
    'UiContainer': UIContainer,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },

  data () {
    return {
      /**
       * Version data
       */
      titlePage: updates.Image,
      collapseLeft: {},
      /*
      Props of the UIContainer
       */
      align: 'center',
      sizeCloseButton: 'medium',
      closeable: false,
      /*
      Props of the UIImage
       */
      imagePath: 'src/pages/common/UIImage/1.jpg',
      shadow: null,
      shapes: null,
      width: 400,
      height: 400,
      hover: false,
      hoverShadowColor: '#000000'
    }
  },
  methods: {
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>
<style>
.positionRight {
  display: flex;
  justify-content: flex-end;
}
</style>


