<template>
  <div>
   <title-component 
   :element="titlePage"
   titleClass="is-size-2"
   :isPage="true">
   </title-component>
   <hr>
    <div class="grid-container">
      <div>
        <div class="columns">
        <div class="column is-1">
         <b-field label="Collapsed">
           <b-switch 
           v-model="isCollapsed"
           rounded>
           </b-switch>
         </b-field>
        </div>
        <div class="column is-2">
          <b-field label="Multilined  tooltip ">
           <b-switch 
           v-model="multilinedTooltip"
           rounded>
           </b-switch>
         </b-field>
        </div>
        <div class="column is-2">
         <b-field 
         label="Tooltip Type">
            <b-select
            placeholder="Select a tooltip UIButton class"
            v-model="tooltipType"
            rounded>
                <option v-for="(item,key) in dynamicTooltipType"
                :key="key">
                {{item}}
                </option>
               
            </b-select>
          </b-field>
        </div>
        <div class="column is-2">
          <b-field label="Tooltip position">
            <b-select
            placeholder="Select the tooltip position"
            v-model="tooltipPosition"
            rounded>
              <option 
              v-for="(item,key) in dynamicTooltipPosition"
              :key="key" >
              {{item}}
              </option>
            </b-select>
          </b-field>
        </div>
        <div class="column is-2">
          <b-field label="Tooltip size">
            <b-select
            placeholder="Select the tooltip size"
            v-model="tooltipSize"
            rounded>
              <option v-for="(item,key) in dynamicTooltipSize"
              :key="key">
              {{item}}
              </option>
            </b-select>
          </b-field>
        </div>
        <div class="column is-2">
          <b-field label="Tooltip label">
            <b-input
            type="text"
            v-model="tooltipTitle"
            rounded>
              
            </b-input>
          </b-field>
        </div>
      </div>
      <div class="columns">
        <div class="column is-2 is-offset-3">
          <b-field label="Height">
            <b-input
            type="number"
            v-model="height" 
            rounded>
            </b-input>
          </b-field>
        </div>
        <div class="column is-2">
           <b-field label="Width">
            <b-input
            type="number"
            v-model="width" 
            rounded>
            </b-input>
          </b-field>
        </div>
        <div class="column is-2">
          <b-field label="Units">
            <b-select 
            v-model="unit"
            rounded>
              <option 
              v-for="(item,key) in ['px','%','rem']"
              :key="key">{{item}}
              </option>
            </b-select>
          </b-field>
        </div>
      </div>
      <div class="containerCentered">
        <ui-logo
        :isCollapsed="isCollapsed"
        :srcImageCollapsed="srcImageCollapsed"
        :srcImageNotCollapsed="srcImageNotCollapsed"
        :widthLogoCollapsed="dynamicWidth"
        :heightLogoCollapsed="dynamicHeight"
        :widthLogoNotCollapsed="dynamicWidth"
        :heightLogoNotCollapsed="dynamicHeight"
        :tooltipTitle="tooltipTitle"
        :tooltipSize="tooltipSize"
        :tooltipType="tooltipType"
        :tooltipPosition="tooltipPosition"
        :multilinedTooltip="multilinedTooltip"
        :isTooltipVisible="isTooltipVisible"
        :sizeUnit="unit">
        </ui-logo>
      </div>
      </div>
      
      <div class="positionRight">
        <b-tooltip
        label="Changelog"
        :active="true"
        position="is-left"
        type="is-dark">
          <ui-panel-container
            :panelHeight= "550"
            :panelWidth= "600"
             unitHeight="px"
             unitWidth="px"
            backgroundColor="white"
            panelBorder="ridge"
            panelBorderColor="black"
            :collapseButtonEnabled="true"
            collapseButtonPosition="left"
            collapseButtonBackgroundColor="#C2C2C2"
            :style="collapseLeft"
            @on-collapse="collapsePanelLeft">
              <markdown></markdown>
            </ui-panel-container>
        </b-tooltip>
      </div>
    </div>
  
    
  </div>
</template>
<script>
import UILogo from '../../../components/common/UILogo/index.js'
import { updates } from '../../../libraryUpdates.js'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import readme from '../../../components/common/UILogo/changelog.md'
import titleComponent from '../titleComponent.vue'

export default {
  name: 'UiLogoPage',

  components: {
    'UiLogo': UILogo,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },

  data () {
    return {
      srcImageCollapsed: 'src/pages/common/UILogo/collapsed.svg',
      srcImageNotCollapsed: 'src/pages/common/UILogo/notCollapsed.svg',
      isCollapsed: true,
      tooltipTitle: 'MYDAS',
      isTooltipVisible: true,
      tooltipType: 'is-primary',
      tooltipPosition: 'is-right',
      tooltipSize: 'is-small',
      multilinedTooltip: false,
      height: 250,
      width: 250,
      unit: 'px',
      collapseLeft: {},
      titlePage: updates.Logo
    }
  },

  methods: {
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  },

  computed: {
    dynamicTooltipSize () {
      let size = [
        'is-small',
        'is-medium',
        'is-large'
      ]

      return size
    },

    dynamicTooltipPosition () {
      let position = [
        'is-right',
        'is-left',
        'is-bottom',
        'is-top'
      ]

      return position
    },

    dynamicTooltipType () {
      let types = [
        'is-primary',
        'is-success',
        'is-danger',
        'is-warning',
        'is-link',
        'is-info',
        'is-black'
      ]

      return types
    },

    dynamicHeight () {
      return parseInt(this.height)
    },

    dynamicWidth () {
      return parseInt(this.width)
    }
  }
}
</script>

<style scoped>
  .containerCentered {
    display: flex;
    justify-content: center;
  }

  .grid-container {
    display: grid;
    grid-template-columns: auto auto;
  }

  .positionRight {
  display: flex;
  justify-content: flex-end;
}
</style>


