<!-- <i18n src="../../../languages/UIGenericTabulatedData.json"></i18n> -->
<template>
    <section>
        <!-- Contain superior elements to table (search section, toolbar options and pagination size)  -->

         <div class="columns">
           <div class="column">
             <div class="columns">
             <div class="column">
               <!-- @slot Slot in the top left of the table -->
               <slot name= "leftTop"></slot>
             </div>
             <div class="column">
               <!-- @slot Slot in the top center of the table -->
               <slot name = "centerTop"></slot>
             </div>
               <div 
               class="column"
               v-if="enabledRowPerPage">
                <b-select
                  v-model="perPage"
                  :disabled="!dataTable.length"
                  class="rowPerPage"
                  @input="loadAsyncData" expanded>
                    <option v-for="rowsQty in QtyPerPage"
                      :value="rowsQty"
                      :key="rowsQty">
                      {{ $t('template.rowsPerPage.label', { 'qty': rowsQty }) }}
                    </option>
                </b-select>
              </div>
             </div>
           </div>
         </div>
        <div class="column">
          <!-- @slot Slot in the top bottom of the table -->
          <slot name="bottomTop"></slot>
        </div>
         <b-table
          :data="dataTable"
          :loading="loading"
          :selected.sync="rowSelected"
          :paginated="enabledRowPerPage"
          :backend-pagination="enabledAsynchronousTable"
          :total="total"
          :per-page="perPage"
          :current-page.sync="page"
          :backend-sorting="enabledAsynchronousTable"
          :default-sort-direction="sortOrder"
          :default-sort="[sortField, sortOrder]"
          :striped="striped"
          :narrowed="narrowed"
          :bordered="bordered"
          :hoverable="true"
          @select="selectRow"
          @page-change="onPageChange"
          @sort="onSort"
          :checkable="checkable"
          @check="checkRow"
          v-on="tableListeners">

          <div
            slot-scope="props"
            slot="empty"
            class="has-text-centered">
              {{ $t('template.emptyTable.label') }}
          </div>
          <b-tooltip
            slot-scope="props"
            slot="header"
            :active="!!props.column.meta"
            :label="props.column.meta"
            :type="dynamicTypeTooltip"
            :position="dynamicPositionTooltip"
            :size="dynamicTooltipSize"
            :multilined="tooltipMultilined">
              {{ props.column.label }}
          </b-tooltip>
          <template slot-scope="props">
            <template
              v-for="group in columnsConfig">
              <b-table-column
                v-for="column in group.children"
                  :key="column.propTag"
                  :field="column.propTag"
                  :label="translateMessage(column.propTag, 'label')"
                  :meta="translateMessage(column.propTag, 'meta')"
                  :visible="column.tabulatedData.visibleOnTable"
                  :sortable="column.tabulatedData.sortable"
                  :bordered="bordered"
                  :width="(column.tabulatedData.width) ? column.tabulatedData.width : ''"
                  :numeric="(column.tabulatedData.type === 'number') ? true : false ">
                  <span :class="[column.tabulatedData.classes, (column.tabulatedData.functionClass) ? column.tabulatedData.functionClass(props.row[column.propTag]) : '']" >
                      <template v-if="validIcon(column.iconf)">
                          <template v-if="column.tabulatedData.iconConf.showText && !column.tabulatedData.iconConf.startPosition">{{ props.row[column.propTag] | formatType(column.tabulatedData.type) }}</template>
                          <b-icon
                            :pack="column.tabulatedData.iconConf.family"
                            :icon="evalCondition(props.row[column.propTag], column.tabulatedData.iconConf.defaultValue, column.tabulatedData.iconConf.iconConditions)">
                          </b-icon>
                          <template v-if="column.tabulatedData.iconConf.showText && column.tabulatedData.iconConf.startPosition"> {{ props.row[column.propTag] | formatType(column.tabulatedData.type) }}</template>
                      </template>
                      <template v-else>
                          {{ (props.row[column.propTag] === null) ? '' : props.row[column.propTag] | formatType(column.tabulatedData.type, column.tabulatedData.characterNumber) }}
                      </template>
                  </span>
              </b-table-column>
            </template>
          </template>
          <div
            slot="footer"
            v-if="enabledAsynchronousTable"
            class="has-text-centered">
            {{ $t('template.tableFooter.label', { 'qty': dataTable.length, 'total': total }) }}
          </div>
          <div
            slot="footer"
            v-else
            class="has-text-centered">
            <p v-if="enabledRowPerPage">
              {{ $t('template.tableFooter.label', { 'qty': perPage, 'total': dataTable.length }) }}  
            </p>
            <p v-if="!enabledRowPerPage">
              {{ $t('template.tableFooter.label', { 'qty': dataTable.length, 'total': dataTable.length }) }}  
            </p>
          </div>
        </b-table>
    </section>
</template>

<script>
import {
  rowsPerPage,
  possibleRowsPerPage,
  defaultDateFormatter
} from '../../../config'
/**
 * Show tabulated data from configuration in props columnsConfig. The pagination and the sort is remote. It use a async method to charge data.
 * @vue UIGenericTabulatedData.vue
 * <ui-generic-tabulated-data
 *    :columns-config="columnsConfig"
 *    entity="MOVIE"
 *    :striped="true"
 *    :narrowed="true"
 *    default-sort-field="vote_count"
 *    default-sort-order="desc"
 *    :default-page="2"
 *    :dataTable = "dataList"
      :loading = "loading"
      :total = "total"
      @on-row-select="selectRow"
      @on-load-data= "loadDataTable"
 *    @on-row-select="selectRow"
 *    />
 * @version 0.1.0
 * @since 0.1.0
 * @author Eddy Cortez [corteze@karanta.com.ve]
 * @copyright Karanta, CA. 2018
 */
export default {
  /**
   * @property {string} name Component name
  */
  name: 'UiGenericTabulatedData',

  /**
   * Component filters
  */
  filters: {
    /**
    * Filter to truncate string, accepts a length parameter
    */
    /**
     * Set format to text, according to data type and characters count
     * @param {string} text Value to format
     * @param {string} dataType Data type of text
     * @param {number} length Max number of characters to return, if dataType is string
     * @returns {(number|string|date)} Value formatted
     * @public
     */
    formatType (text, dataType, length) {
      let localText = text || ''
      let localLength = length || localText.length
      switch (dataType.toLowerCase()) {
        case 'date':
          if (localText === '' || localText === null) {
            localText = '-'
          } else {
            let objectDate = new Date(localText)
            localText = defaultDateFormatter(objectDate)
          }
          break
        case 'string':
          localText = (localText.length > 0 && localText.length > localLength) ? localText.substr(0, localLength) + '...' : localText
          break
        case 'number':
          localText = (text === 0) ? 0 : text
          break
        default:
          /* Nothing to do */
      }
      return localText
    }
  },

  props: {

    /**
     * Sets bordered property for the table
     * @prop {Boolean} [bordered=false]
     */
    bordered: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * Sets checkable property for table
     * @prop {Boolean} [checkable=false]
     */
    checkable: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * Columns configurations
     * @prop {Array} columnsConfig
    */
    columnsConfig: {
      type: Array,
      required: true
    },

    /**
     * Entity for show data
     * @prop {string} entity
    */
    entity: {
      type: String,
      required: true
    },

    /**
     * Sets striped property for table
     * @prop {boolean} [striped=true]
    */
    striped: {
      type: Boolean,
      default: true,
      required: false
    },

    /**
     * Sets narrowed property for table
     * @prop {boolean} [narrowed=true]
    */
    narrowed: {
      type: Boolean,
      default: true,
      required: false
    },

    /**
     * Sets field for sort, by default
     * @prop {string} defaultSortField
    */
    defaultSortField: {
      type: String,
      required: true
    },

    /**
     * Sets order for sort, by default
     * @prop {string} [defaultSortOrder='asc']
    */
    defaultSortOrder: {
      type: String,
      default: 'asc',
      required: false
    },

    /**
     * Sets the page to show in the table, initially
     * @prop {number} [defaultPage=1]
    */
    defaultPage: {
      type: Number,
      default: 1,
      required: false
    },

   /**
     * Data to show in table
     * @prop {Array}
    */
    dataTable: {
      type: Array,
      required: true
    },

    /**
     * Property that enables the asynchronous table
     * @prop {Boolean} [enabledAsynchronousTable = true]
     */
    enabledAsynchronousTable: {
      type: Boolean,
      required: false,
      default: true
    },

    /**
     * Property that enables the rowPerPage on the table
     * @prop {Boolean} [enabledRowPerPage = true]
     */
    enabledRowPerPage: {
      type: Boolean,
      required: false,
      default: true
    },

    /**
     * Total number of records to show in the table
     * @prop {Number} [total=0]
    */
    total: {
      type: Number,
      default: 0
    },

    /**
     * Switch to show, or not, the table content
     * @prop {Boolean} [loading=false]
    */
    loading: {
      type: Boolean,
      default: false
    },

    /**
     * Sets property type on the tooltip of the table
     * @prop {String} [tooltipType= 'is-primary']
     */
    tooltipType: {
      type: String,
      required: false,
      default: 'is-primary'
    },

    /**
     * Sets the position in relation to the element
     * @prop {String} [tooltipPosition= 'is-top']
     */
    tooltipPosition: {
      type: String,
      required: false,
      default: 'is-top'
    },

    /**
     * Sets the tooltip size (only works for multilined tooltips)
     * @prop {String} [tooltipSize= 'is-medium']
     */
    tooltipSize: {
      type: String,
      required: false,
      default: 'is-medium'
    },

    /**
     * Determines if the the tooltip is multilined
     * @prop {Boolean} [tooltipMultilined= false]
     */
    tooltipMultilined: {
      type: Boolean,
      required: false,
      default: false
    },
    /**
     * Selected object in the data
     * @prop {Object} [selected = {}]
     */
    selected: {
      type: Object,
      required: false,
      default: () => { return {} }
    }
  },

  /**
   * Data representing the model from component
   * @property {function} data
   * @returns {object} List of properties of model
  */
  data () {
    return {

      /**
       * Object selected in the table
       * @prop {object} [rowSelected={}]
      */
      rowSelected: {},

      /**
       * Default page to show in the table
       * @prop {number} [page=1]
      */
      page: 1,

      /**
       * Pagination size
       * @prop {number} [perPage=20]
      */
      perPage: 20,

      /**
       * Array with the possibilities that the user can use, to set the pagination size
       * @prop {Array} [QtyPerPage]
      */
      QtyPerPage: possibleRowsPerPage,

      /**
       * Order direction
       * @prop {string} [sortOrder='']
      */
      sortOrder: '',

      /**
       * Order field
       * @prop {string} [sortField='']
      */
      sortField: '',

      /**
       * Rows are checked on the table
       * @prop {Array}
       */
      checkedRows: []
    }
  },

  computed: {

    /**
     * Computed property that sets the tooltip type
     * @return {String}
     */
    dynamicTypeTooltip () {
      let types = [
        'is-primary',
        'is-danger',
        'is-info',
        'is-link',
        'is-success',
        'is-warning',
        'is-white',
        'is-dark',
        'is-light',
        'is-black'
      ]

      if (types.indexOf(this.tooltipType.toLowerCase()) === -1) {
        console.error('Wrong tooltip type')
        return 'is-primary'
      }

      return this.tooltipType
    },

    /**
     * Computed property that sets the tooltip position
     * @return {String}
     */
    dynamicPositionTooltip () {
      let positions = [
        'is-top',
        'is-bottom',
        'is-left',
        'is-right'
      ]

      if (positions.indexOf(this.tooltipPosition.toLowerCase()) === -1) {
        console.error('Wrong tooltip position')
        return 'is-top'
      }

      return this.tooltipPosition
    },

    /**
     * Computed property that sets the tooltip size
     * @return {String}
     */
    dynamicTooltipSize () {
      let sizes = [
        'is-small',
        'is-medium',
        'is-large'
      ]

      if (sizes.indexOf(this.tooltipSize.toLowerCase()) === -1) {
        console.error('Wrong tooltip size')
        return 'is-medium'
      }

      return this.tooltipSize
    },
    /**
     * Emits the table event by default
     */
    tableListeners () {
      return {
        ...this.$listeners
      }
    }
  },
  watch: {
    /**
     * Make two-way binding with selected prop
     * @public
     */
    rowSelected (newValue) {
      /**
       * Updates the prop selected using the .sync modifier
       * @event update:selected
       * @type {Object}
       */
      this.$emit('update:selected', newValue)
    },
    /**
     * Updates the data with the prop
     */
    selected (newValue) {
      this.rowSelected = newValue
    },
    /**
     * Updates the sort field
     */
    defaultSortField (newValue) {
      this.sortField = newValue
    },
    /**
     * Updates the sort order
     */
    defaultSortOrder (newValue) {
      this.sortOrder = newValue
    }
  },
/**
   * UIGenericTabulatedData.vue `created` hook.
   */
  created () {
    // Sets values to model, from configuration file and params received
    this.perPage = rowsPerPage
    this.sortField = this.defaultSortField
    this.sortOrder = this.defaultSortOrder
    if (this.defaultPage) this.page = this.defaultPage
  },
  /**
   * Component functions
  */
  methods: {
    /**
     * Emits the on-check-row event in the parent, with this argument
     * @param  {Array} listCheckedRows  Array of the checked rows
     * @public
     */
    checkRow (listCheckedRows) {
      this.$emit('on-check-row', listCheckedRows)
    },

    /**
     * Emits the on-row-select event in the parent, with these arguments.
     * @param {Object} row Row selected
     * @param {Object} row Old row selected
     * @public
     */
    selectRow (row, oldRow) {
      this.$emit('on-row-select', row, oldRow)
    },

    /**
     * Validates the type the column icon
     * @param  {Object} icon The type column Icon
     * @return {Boolean}
     * @public
     */
    validIcon (icon) {
      if (icon !== null && typeof icon === 'object') {
        return true
      }

      return false
    },

    /**
     * Evaluates possible options from icon to show in table columns
     * @param {string} value Value to evaluate
     * @param {string} defaultValue Default value if value var is null
     * @param {Object} iconConditions Possible option to value match
     * @returns {string} String with icon to show in cell
     * @public
     */
    evalCondition (value, defaultValue, iconConditions) {
      value = value || defaultValue
      let iconValue = ''
      if (Array.isArray(iconConditions)) {
        iconConditions.forEach(condition => {
          if (value.toLowerCase() === condition.value.toLowerCase()) {
            iconValue = condition.icon
          }
        })
      }
      return iconValue
    },

    /**
     * Async load data in model of component
     * @public
     */
    loadAsyncData () {
      let searchParameters = {
        entity: this.entity,
        sortField: this.sortField,
        sortOrder: this.sortOrder,
        page: this.page - 1,
        perPage: this.perPage
      }

      if (this.enabledAsynchronousTable) {
        this.$emit('on-load-data', searchParameters)
      }
    },

    /**
     * Sets the value of page in the model and call loadAsyncData event
     * @param {number} page Page number to show
     * @public
     */
    onPageChange (page) {
      this.page = page

      if (this.enabledAsynchronousTable) {
        this.loadAsyncData()
      }
    },

    /**
     * Sets the value of field and order in the model, then, call loadAsyncData event
     * @param {string} field Field to order
     * @param {string} order Direction to order
     * @public
     */
    onSort (field, order) {
      this.sortField = field
      this.sortOrder = order

      if (this.enabledAsynchronousTable) {
        this.loadAsyncData()
      }
    },

    /**
     * Translates the message for a field, using the i18n library
     * @param {string} field Field to translate
     * @param {string} value Value to translate (title, metadata, etc)
     * @returns {string} String with traduction of field, according to language file
     * @public
     */
    translateMessage (field, value) {
      return this.$i18n.t(`entities['${this.entity}']['${field}']['${value}']`)
    }
  }
}
</script>

<style scoped>

</style>



