<template>
  <div class="columns">
    <div 
    class="column"
    id="containerHeight" 
    :style="dynamicHeight">
      <div class="columns">
        
        <div 
        :class="[{'column has-text-centered titleWizard':true},dynamicClassOffsetColumn]" 
        :style="[dynamicBackgroundColor,dynamicBorder,dynamicRoundedWizard]">
          <ui-title 
          :title="configurationTitle.title"          
          :color="configurationTitle.color"
          :weight="configurationTitle.weight"
          :align="configurationTitle.align"
          :size="configurationTitle.size"
          :transformation="configurationTitle.transformation">
          <!-- @slot Use this slot title -->
            <slot name="title"></slot>  
          </ui-title>

        </div>
      </div>
      <!-- Menu and wizard content -->
      <div 
      class="columns" 
      style="height:95%">
        
        <!-- Menu -->
        <div 
        class="column has-text-centered menuWizard" 
        :style="[dynamicBackgroundColor,dynamicRoundedWizard,dynamicBorder]">
         
         <!-- <ui-menu> -->
          <div 
          class="columns menuWizard" 
          style="height: 95%">
             
             <div class="column menu">
               <ui-menu
                :active-text-color="configurationMenu.activeTextColor"
                class="el-menu-vertical-demo"
                :background-color="configurationMenu.backgroundColor"
                :collapse="configurationMenu.collapse"
                :default-active="configurationMenu.defaultActive"
                :menu-items="configurationMenu.menuItems"
                :mode="configurationMenu.mode"
                :router="configurationMenu.router"
                :text-color="configurationMenu.textColor"
                :unique-opened="configurationMenu.uniqueOpened"
                @select="selectItemMenu">
                
              </ui-menu>
             </div>
          </div>
          <!-- <ui-toolbar> -->
          <div 
          class="columns" 
          style="border-top-style: groove">
            <div class="column">
              <ui-container
              :align="align"
              :closeable="false">
                <ui-toolbar
                :buttonConfigurations="configurationToolBar.buttonConfigurations"
                :gapContentToolBar="configurationToolBar.gap"
                @click="clickToolbar"
                ></ui-toolbar>
              </ui-container>
            </div>
          </div>

        </div>
        <!-- Content -->
        <div 
        :class="[dynamicClassSizeColumn, {'column has-text-centered contentWizard': true}]" 
        :style="[dynamicBackgroundColorContent,dynamicBorderContent,dynamicRoundedWizard]">
        <!-- @slot Use this slot content -->
          <slot name="content"></slot>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
 import UIToolBar from '../../../components/common/UIToolBar/index.js'
 import UIContainer from '../../../components/common/UIContainer/index.js'
 import UIMenu from '../../../components/common/UIMenu/index.js'
 import UITitle from '../../../components/common/UITitle/index.js'

 import {
  defaultHeightVh,
  defaultHeightPorcentual,
  defaultHeightRem,
  defaultHeightPx,
  defaultColumnSizeWizard,
  borders,
  unitsHeight
} from './config'
 /**
 * Component that represents a structure of a wizard with the following functions:
 * 1) Wizard bordered
 * 2) Color background
 * 3) Sets the type border on the wizard
 * 4) Sets the height of the wizard
 * 5) Sets the color border of the wizard
 * 6) has all functions of the UITitle on its title
 * @vue UIWizard.vue
 * @version 0.1.0
 * @since 0.1.0
 * @author Alfredo Garcia [gitlab:@garciareal45]
 * @copyright Karanta, CA. 2018
 */
 export default {

   name: 'UiWizard',

   components: {
     'UiToolbar': UIToolBar,
     'UiContainer': UIContainer,
     'UiMenu': UIMenu,
     'UiTitle': UITitle
   },
 
   props: {
 
      /**
       * Property that determines the content size of the <ui-wizard>
       * <ui-wizard>
       * @prop {Number} [sizeColumn= 9]
       */
     sizeColumn: {
       type: Number,
       required: false,
       default: 9
     },

      /**
       * Property that determines the height of the <ui-wizard>
       * @prop {Number} [heightWizard= 70]
       */
     heightWizard: {
       type: Number,
       required: false,
       default: 70
     },

     /**
      * Property that sets the unit height of the wizard
      * @prop {String} [unitHeight=vh]
      */
     unitHeight: {
       type: String,
       required: false,
       default: 'vh'
     },

      /**
       * Property that sets the configuration of the <ui-toolbar>
       * @prop {Object} [configurationToolBar={}]
       */
     configurationToolBar: {
       type: Object,
       required: false,
       default: function () {
         return {}
       }
     },

     /**
      * Property that sets the background color on the menu zone and
      * title zone of the <ui-wizard>
      * @prop {String} [backgroundColor= #C4C4C4]
      */
     backgroundColor: {
       type: String,
       required: false,
       default: '#C4C4C4'
     },

     /**
      * Property that sets the backgroud color on the content zone of the
      * <ui-wizard>
      * @prop {String} [backgroundColorContent= #FFFFFF]
      */
     backgroundColorContent: {
       type: String,
       required: false,
       default: '#FFFFFF'
     },

     /**
      * Property that sets the border style of the <ui-wizard>
      * @prop {String}  [borderStyle= none]
      */
     borderStyle: {
       type: String,
       required: false,
       default: 'none'
     },

     /**
      * Property that sets the color border of the <ui-wizard>, that color can be
      * hexadecimal, color or RGB
      * @prop {String} [borderColor= black]
      */
     borderColor: {
       type: String,
       required: false,
       default: 'black'
     },

      /**
       * Property that sets the configuration of the <ui-menu>
       * @prop {Object} [configurationMenu={}]
       */
     configurationMenu: {
       type: Object,
       required: false,
       default: function () {
         return {}
       }
     },

     /**
      * Property that sets the configuration of the <ui-title>
      * @prop {Object} [configurationTitle={}]
      */
     configurationTitle: {
       type: Object,
       required: false,
       default: function () {
         return {}
       }
     },

     /**
      * Property that rounds out the border of the <ui-wizard>
      * @prop {Boolean} [rounded= true]
      */
     rounded: {
       type: Boolean,
       required: false,
       default: true
     }

   },

   data () {
     return {
       align: 'center'
     }
   },

   computed: {

      /**
       * Computed Property that determines the offset columns class
       * @return {String}
       */
     dynamicClassOffsetColumn () {
       return (this.sizeColumn >= 2 && this.sizeColumn <= defaultColumnSizeWizard) ? 'is-offset-' + (12 - this.sizeColumn) : 'is-offset-3'
     },

      /**
       * Computed property that determines the size columns class
       * @return {String}
       */
     dynamicClassSizeColumn () {
       if (this.sizeColumn < 2 || this.sizeColumn > defaultColumnSizeWizard) {
         console.error('Wrong size column')
         return 'is-' + defaultColumnSizeWizard
       }
       return 'is-' + this.sizeColumn
     },

      /**
       * Computed property that sets the height of the wizard
       * @return {Object}
       */
     dynamicHeight () {
       let height = {height: this.heightWizard + this.unitHeight}
       if (unitsHeight.indexOf(this.unitHeight.toLowerCase()) !== -1) {
         switch (this.unitHeight) {
           case 'vh':
             if (this.heightWizard < 40) {
               console.error('Wrong height')
               return {height: defaultHeightVh + this.unitHeight}
             }

             return height
 
           case 'px':
             if (this.heightWizard < 200) {
               console.error('Wrong height')
               return {height: defaultHeightPx + this.unitHeight}
             }
 
             return height

           case 'rem':
             if (this.heightWizard < 30) {
               console.error('Wrong height')
               return {height: defaultHeightRem + this.unitHeight}
             }

             return height

           case '%':
             if (this.heightWizard < 30) {
               console.error('Wrong height')
               return {height: defaultHeightPorcentual + this.unitHeight}
             }

             return height
         }
       } else {
         console.error('Wrong unit of height')
         return {height: this.heightWizard + 'px'}
       }
     },

      /**
       * Computed property that sets the position of the <ui-toolbar>
       * @return {Object}
       */
     dynamicPositionToolBar () {
       return {
         display: 'flex',
         'flex-direction': 'column',
         'justify-content': 'space-between'
       }
     },

     /**
      * Computed property that sets the background color of the
      * <ui-wizard>
      * @return {Object}
      */
     dynamicBackgroundColor () {
       return {
         background: this.backgroundColor
       }
     },

     /**
      * Computed property that sets the background content color of the
      * <ui-wizard>
      * @return {Object}
      */
     dynamicBackgroundColorContent () {
       return {
         background: this.backgroundColorContent
       }
     },

     /**
      * Computed property that sets the menu border, title border
      * @return {Object}
      */
     dynamicBorder () {
       if (borders.indexOf(this.borderStyle.toLowerCase()) === -1) {
         console.error('Wrong border')
       } else {
         return {
           'border-style': this.borderStyle.toLowerCase(),
           'border-color': this.borderColor
         }
       }
     },

     /**
      * Computed property that sets the content border of the
      * <ui-wizard>
      * @return {Object}
      */
     dynamicBorderContent () {
       let border = (borders.indexOf(this.borderStyle.toLowerCase()) !== -1) ? this.borderStyle.toLowerCase() : 'solid'
       return {
         'border-top-style': 'none',
         'border-right-style': border,
         'border-left-style': 'none',
         'border-bottom-style': border,
         'border-color': this.borderColor
       }
     },

     /**
      * Computed property that rounds out the border of the <ui-wizard>
      * @return {Object}
      */
     dynamicRoundedWizard () {
       if (this.rounded) {
         return {
           'border-radius': 15 + 'px'
         }
       }
     }

   },

   methods: {

    /**
     * Emits the action of the toolbar
     * @param  {String}
     * @public
     */
     clickToolbar (value) {
      /**
       * This event emits the action of the toolbar
       * @event on-toolbar
       * @type {String}
       */
       this.$emit('on-toolbar', value)
     },
     /**
      * Emits the selected item.
      * @param {Number}
      * @public
      */
     selectItemMenu (index) {
       /**
        * This event emits the selected item.
        * @event on-select-item
        * @type {Number}
        */
       this.$emit('on-select-item', index)
     }
   }

 }
</script>

<style scoped>
  
  .contentWizard {
    overflow-y: auto;
  }

  .menu {
    overflow: auto;
  }

  @media only screen and (max-width: 768px) {
    .contentWizard {
      overflow-y: scroll;
      height: 100%;
    }
  }

</style>


