<template>
  <div>
    <ui-container
      align="none"
      :sizeCloseButton="sizeCloseButton"
      closeable
      @close="childEventEmitter('close', $event)">
      <div :class="headerClass">
        <b-icon :icon="headerIcon">
        </b-icon>
        <div class="" style="display: inline-block; margin: 0px 10px">
          <h3 v-for="(name, key3) in headerLabel" :key="key3">
            {{name}}
          </h3>
        </div>
      </div>
    </ui-container>
    <ui-container :align="toolbarAlign" :closeable="false">
      <ui-toolbar
      v-if="toolbar"
      v-bind="toolbar"
      @click="childEventEmitter('click', $event)">
      </ui-toolbar>
    </ui-container>
    <br>
    <b-collapse
      v-for="(item1,key1) in value"
      class= "card ui-management-panel-preview-collapse"
      :key= "key1"
      :open= "isOpen">
      <div
        slot="trigger"
        class="card-header">
        <p class="card-header-title">
          {{translateMessage(entity, item1['classTag'])}}
        </p>
        <a class="card-header-icon">
            <b-icon :icon= "collapseIcon"></b-icon>
          </a>
      </div>
      <div class="card-content">
        <div class="content">
          <template v-for="(item2,key2) in item1['childs']">
            <div
              class= "has-text-left"
              v-if="(item2['preview']['visible'])"
              :key="key2">
              <label v-if="(item2['preview']['visibleLabel'])">
                {{`${translateMessage(entity, item2['propTag'])}: `}}
              </label>
              <span v-if = "!loading">
                {{itemToShowData[item2['propTag']]}}
              </span>
              <span v-else>
                <b-icon icon="fa fa-spinner fa-pulse">
                </b-icon>
              </span>
            </div>
          </template>
        </div>
      </div>
    </b-collapse>
  </div>
</template>

<script>
/**
 * @vue UIManagementPanelPreview.vue
 * A component made to display the data of an entity. Some of the functions are:
 * * Allows to show the data of an entity
 * * Allows to classificade the data
 * * Allows hide the classifications with a click
 * @author Cruz Ortiz [gitlab:@cortiz99]
 * @version 0.1.0
 * @since 0.1.0
 * @copyright Karanta C.A. 2018
 */
import UIContainer from '../UIContainer'
import UIToolbar from '../UIToolBar'
import { defaultDateFormatter } from '../../../config.js'
export default {
  name: 'UiManagementPanelPreview',

  components: {
    'UiContainer': UIContainer,
    'UiToolbar': UIToolbar
  },

  // Represents the property and event associated when v-model is used
  model: {
    prop: 'value',
    event: 'input'
  },

  props: {
    /**
    * Sets the configurations of the property of the element shown in the
    * preview. Most have the 3 level (classifications, params and configuration). Example: conf: {class:{param:{visibleOnTable: true, filtreable: false}}}.
    * @prop {Array} value
    */
    value: {
      type: Array,
      required: true
    },

    /**
    * Sets the icon of the tags elements
    * @prop {String} [collapseIcon = 'fa fa-chevron-down']
    */
    collapseIcon: {
      type: String,
      required: false,
      default: 'fa fa-chevron-down'
    },

    /**
     * Represents the data of the component
     * @prop {Object} data
     */
    data: {
      type: Object,
      required: true
    },

    /**
     * Represents the entity or model for the translate message
     * @prop {String} entity
     */
    entity: {
      type: String,
      required: true
    },

    /**
     * Sets the classes aplied to the header
     * @prop {String} [headerClass = 'has-text-centered is-uppercase is-size-3']
     */
    headerClass: {
      type: String,
      required: false,
      default: 'has-text-centered is-uppercase is-size-3'
    },

    /**
     * Represents the icon shown in the header
     * @prop {String} [headerIcon = null]
     */
    headerIcon: {
      type: String,
      required: false,
      default: null
    },

    /**
    * Sets the classifications open on start
    * @prop {Boolean} [isOpen = false]
    */
    isOpen: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * Shows a loading state while the data is loading
     * @prop {Boolean} [loading = false]
     */
    loading: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * Sets the size of the close button
     * @prop {String} [sizeCloseButton = medium]
     */
    sizeCloseButton: {
      type: String,
      required: false,
      default: 'medium'
    },

    /**
     * Sets the toolbar configuration
     * @prop {Object} [toolbar = null]
     */
    toolbar: {
      type: Object,
      required: false,
      default: null
    },

    /**
     * Sets the aling of the toolbar
     * @prop {String} [toolbarAlign = 'right']
     */
    toolbarAlign: {
      type: String,
      required: false,
      default: 'right'
    }
  },

  data () {
    return {
      /**
       * Represents the item that will be show on the panel management preview
       * @type {Object}
       */
      itemToShowData: {}
    }
  },

  /**
   * Sets the initial conditions of the component
   */
  created () {
    this.objectCreator(this.data, this.itemToShowData)
  },

  computed: {
    /**
     * Returns only the value to show in the header
     * @return {Array} Represents the values shown in the header
     */
    headerLabel () {
      let self = this
      let headers = []
      this.value.map((classification) => {
        classification['childs'].map((prop) => {
          if (prop['preview']['header']) {
            headers.push(self.itemToShowData[prop['propTag']])
          }
        })
      })
      return headers
    }
  },

  methods: {
    /**
     * Builds an object according with the structure that can be represented on the view
     * @param  {Object} objectToTransform represent the object that will be transform
     * @param {Object} objectTransformed Represents the object where structure will be build
     * @public
     */
    objectCreator (objectToTransform, objectTransformed) {
      for (let item in objectToTransform) {
        if (typeof (objectToTransform[item]) === 'object') {
          this.objectCreator(objectToTransform[item], objectTransformed)
        }
        if (objectToTransform[item] instanceof Date) {
          objectTransformed[item] = defaultDateFormatter(objectToTransform[item])
        } else {
          objectTransformed[item] = objectToTransform[item]
        }
      }
    },

    /**
     * Repeats the events of the child elements
     * @param  {String} eventName Name of the event
     * @param  {Object|Array|Number|String} event Params of the event
     * @public
     */
    childEventEmitter (eventName, event) {
      /**
      * @event eventName
      * @type {Object|Array|Number|String}
      */
      this.$emit(eventName, event)
    },

    /**
     * Makes the translations of the key
     * @param  {String} entity Represents the entity represented on the component
     * @param  {String} key    Represents the key to be translated
     * @return {String}        The chain translated
     * @public
     */
    translateMessage (entity, key) {
      return this.$i18n.t(`entities.${entity.toUpperCase()}.${key}.label`)
    }
  }
}
</script>

<style scoped>
</style>



