<template>
<div>
  <title-component 
  :element="titlePage"
  titleClass="is-size-2"
  :isPage="true">
  </title-component>
  <hr>
  <div class="columns">
    <div class="column is-2"></div>
    <b-field label="Visible icon">
      <b-switch 
      v-model="visibleIcon"
      rounded>
      </b-switch>
    </b-field>
    &nbsp&nbsp
    <b-field label="Enable notification">
      <b-switch 
      v-model="enableNotification"
      rounded>
      </b-switch>
    </b-field>
    &nbsp&nbsp
    <b-field label="Visible notification">
      <b-switch 
      v-model="visibleNotification"
      rounded>
      </b-switch>
    </b-field>
    &nbsp&nbsp
    <b-field label="Visible label">
      <b-switch v-model="visibleLabel">
      </b-switch>
    </b-field>
    &nbsp&nbsp
    <b-field label="Rounded button">
      <b-switch
      v-model="isRounded"
      rounded>
      </b-switch>
    </b-field>
    &nbsp&nbsp
    <b-field label="Visible tooltip">
      <b-switch
      v-model="visibleToolTip"
      rounded>
      </b-switch>
    </b-field>
    &nbsp&nbsp
    <b-field label="Multilined tooltip">
      <b-switch
      v-model="multilinedToolTip"
      rounded>
      </b-switch>
    </b-field>
    &nbsp&nbsp
    <b-field label="Position tooltip">
      <b-select 
      v-model="positionToolTip"
      rounded>
        <option 
        v-for="(position,key) in dynamicPositionTooltip"
        :key="key">
        {{position}}
        </option>
      </b-select>
    </b-field>
    &nbsp&nbsp
    <b-field label="Button color class">
      <b-select 
      v-model="buttonClass"
      rounded>
        <option 
        v-for="(item,key) in dynamicClassButton"
        :key="key">
        {{item}}
        </option>
      </b-select>
    </b-field>
    &nbsp&nbsp
    <b-field label="Button size">
      <b-select 
      v-model="buttonSize"
      rounded>
        <option 
        v-for="(item,key) in dynamicButtonSize"
        :key="key">
          {{item}}
        </option>
      </b-select>
    </b-field>
    &nbsp&nbsp
    <b-field label="Tooltip type">
      <b-select
      v-model="typeToolTip"
      rounded>
        <option 
        v-for="(item,key) in dynamicTooltipType"
        :key="key">
          {{item}}
        </option>
      </b-select>
    </b-field>
     &nbsp&nbsp
    <b-field label="Tooltip size">
      <b-select
      v-model="sizeToolTip"
      rounded>
        <option 
        v-for="(item,key) in dynamicTooltipSize"
        :key="key">
          {{item}}
        </option>
      </b-select>
    </b-field>
    &nbsp&nbsp
    <b-field label="Notification color class">
      <b-select
      v-model="addedClass"
      rounded>
        <option 
        v-for="(item,key) in dynamicNotificationClass"
        :key="key">
          {{item}}
        </option>
      </b-select>
    </b-field>
  </div>
  <br>
  <div class="columns">
    <div class="column has-text-centered">
      <ui-button 
        :buttonType="buttonType"
        :visibleLabel="visibleLabel"
        :iconType="iconType"
        :visibleIcon="visibleIcon"
        :addedClass="addedClass"
        :buttonClass= "buttonClass"
        :buttonSize= "buttonSize"
        :buttonIsRounded="isRounded"
        :buttonIsLarge="true"
        :visibleToolTip="visibleToolTip"
        :positionToolTip="positionToolTip"
        :toolTipSize="sizeToolTip"
        :typeToolTip="typeToolTip"
        :multilinedToolTip="multilinedToolTip"
        :sizeIcon="sizeIcon"
        :enableNotification="enableNotification"
        :visibleNotification.sync="visibleNotification"
        @click="buttonClicked"
      ></ui-button> 
    </div>
  </div>
  <hr>
      <div class="positionRight">
        <b-tooltip
        label="Changelog"
        :active="true"
        position="is-left"
        type="is-dark">
          <ui-panel-container
            :panelHeight= "150"
            :panelWidth= "600"
             unitHeight="px"
             unitWidth="px"
            backgroundColor="white"
            panelBorder="ridge"
            panelBorderColor="black"
            :collapseButtonEnabled="true"
            collapseButtonPosition="left"
            collapseButtonBackgroundColor="#C2C2C2"
            :style="collapseLeft"
            @on-collapse="collapsePanelLeft">
              <markdown></markdown>
            </ui-panel-container>
        </b-tooltip>
      </div>
</div>
</template>

<script>
import UIButton from '../../../components/common/UIButton/index.js'
import { updates } from '../../../libraryUpdates.js'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import readme from '../../../components/common/UIButton/changelog.md'
import titleComponent from '../titleComponent.vue'
export default {
  name: 'UIButtonPage',

  components: {
    'UiButton': UIButton,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },

  data () {
    return {
      collapseLeft: {},
      titlePage: updates.Button,
      buttonType: 'edition',
      visibleLabel: true,
      buttonClass: 'is-warning',
      addedClass: 'is-inverted',
      iconType: 'fas fa-home',
      sizeIcon: 'is-small',
      buttonSize: 'is-large',
      visibleIcon: true,
      isRounded: true,
      visibleToolTip: true,
      positionToolTip: 'is-top',
      typeToolTip: 'is-primary',
      sizeToolTip: 'is-medium',
      multilinedToolTip: false,
      enableNotification: false,
      visibleNotification: false
    }
  },

  computed: {

    dynamicIconSize () {
      let sizes = [
        'is-small',
        'is-medium',
        'is-large'
      ]

      return sizes
    },

    dynamicTooltipSize () {
      let sizes = [
        'is-small',
        'is-medium',
        'is-large'
      ]

      return sizes
    },

    dynamicTooltipType () {
      let types = [
        'is-white',
        'is-black',
        'is-light',
        'is-dark',
        'is-primary',
        'is-info',
        'is-success',
        'is-warning',
        'is-danger'
      ]

      return types
    },
    dynamicPositionTooltip () {
      let positions = [
        'is-top',
        'is-bottom',
        'is-right',
        'is-left'
      ]

      return positions
    },

    dynamicClassButton () {
      let classes = [
        'is-primary',
        'is-danger',
        'is-success',
        'is-warning',
        'is-link',
        'is-info'
      ]

      return classes
    },

    dynamicNotificationClass () {
      let classes = [
        'is-inverted',
        'is-outlined',
        'is-primary',
        'is-danger',
        'is-success',
        'is-warning',
        'is-link',
        'is-info',
        'is-light'
      ]
      return classes
    },

    dynamicButtonSize () {
      let sizes = [
        'is-small',
        'is-medium',
        'is-large'
      ]

      return sizes
    }
  },
  methods: {
    buttonClicked (evento) {
      console.log(evento)
    },
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    }
  }
}
</script>
<style>
.positionRight {
  display: flex;
  justify-content: flex-end;
}
</style>



