<template>
<div>
  <b-field class="file">
      <b-upload
        v-model="file"
        v-bind="$attrs"
        :value="value"
        :accept="fileFormat.join(',')"
        @input="emitFileData">
          <a class="button"
          :class="buttonType">
              <b-icon icon="upload"
              :type="iconType"
              v-if="visibleIcon"></b-icon>
              <span v-if="visibleLabel">{{translateLabel(uploadMessage)}}</span>
          </a>
      </b-upload>
      <span class="file-name" v-if="file">
          {{ file[0]['name'] }}
      </span>
  </b-field>
</div>
</template>
<script>
/**
 * This component allows to upload files
 * Sends the file data to the father component
 * @Vue UIUploadFile.vue
 * @version 1.0.0
 * @author Rafael Marcano [gitlab:@RM02]
 * @copyright Karanta, CA. 2019
 */
export default {
  name: 'UiUploadFile',

  model: {
    prop: 'value',
    event: 'uploadedFile'
  },

  props: {
    /**
     * Prop that determines a range of file format to upload
     * @prop {Array}
     */
    fileFormat: {
      type: Array,
      required: false,
      default: null
    },
    /**
     * Prop that determines the color of the drag area
     * @prop {String}
     */
    buttonType: {
      type: String,
      required: false,
      default: 'is-info'
    },
    /**
     * Prop that determines the message on the component
     */
    uploadMessage: {
      type: String,
      required: false,
      default: 'upload'
    },
    /**
    * Prop that allows to enable the visibility of button label
    * @prop {Boolean} [default= false]
    */
    visibleLabel: {
      type: Boolean,
      required: false,
      default: true
    },

    /**
    * Prop that allows to enable the visibility of icon
    * @prop {Boolean} [default= true]
    */
    visibleIcon: {
      type: Boolean,
      required: false,
      default: true
    },
    /**
    * Prop that determines the type of icon on the button
    * @prop {Boolean}
    */
    iconType: {
      type: String,
      required: false,
      default: 'is-success'
    },
    /**
     * Represent data to be associated with v-model
     * @prop {Array} [value = null]
     */
    value: {
      type: Array,
      required: false,
      default: null
    }
  },

  data () {
    return {
      /**
       * file data
       * @type {Array}
       */
      file: null
    }
  },

  created () {
  /**
   * Sets the inicial configuration to the component.
   */
    this.file = this.value
  },

  watch: {
    /**
     * watch property value when it changes
     */
    value (newValue) {
      this.file = newValue
    }
  },

  methods: {
    /**
     * Emits event with file data to the father component
     * @param {Array} data file data
     */
    emitFileData (data) {
      /**
       * @event uploadedFile
       * @type {Array}
       */
      this.$emit('uploadedFile', this.file)
    },
      /**
      * Translates the received parameter
      * @returns {String}
      * @param {String}
      * @public
      */
    translateLabel (value) {
      return this.$i18n.t(`template.${value}.label`)
    }
  }
}
</script>



