<script>
export default {
  /**
   * @vue UIMenu.vue
   * This component create a navigation menu through
   * a object with a structure that is defined in the documentation
   * this component has the following function:
   * * Allow to create a navegation menu
   * * Emit the index of menu item selected
   * @version 0.1.0
   * @since 0.1.0
   * @author Nelson Rodriguez [gitlab:@nelrod]
   * @copyright Karanta, CA. 2018
   */
  name: 'UiMenu',
  props: {
    /**
     * Color of the menu item name that is active
     * @prop {String} activeTextColor
     */
    activeTextColor: {
      type: String,
      required: false
    },

    /**
     * Color of the background of navigation menu
     * @prop {String} backgroundColor
     */
    backgroundColor: {
      type: String,
      required: false
    },

    /**
     * Property that allows collapse of the navigation menu
     * @prop {Boolean} [collapse = false]
     */
    collapse: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * Index of the menu item to activate
     * @prop {String} defaultActive
     */
    defaultActive: {
      type: String,
      required: false
    },

    /**
     * Structure used to create the navigation menu
     * @prop {Array} menuItems
     */
    menuItems: {
      type: Array,
      required: true
    },

    /**
     * Property that establishes
     * event that open submenu
     * @prop {String} [menuTrigger = 'hover']
     */
    menuTrigger: {
      type: String,
      required: false,
      default: 'hover'
    },

    /**
     * Property that establishes navigation
     * menu orientation
     * @prop {String} [mode = 'horizontal']
     */
    mode: {
      type: String,
      required: false,
      default: 'horizontal'
    },

    /**
     * Whether vue-router is activated.
     * If true, index will be used as 'path'
     * to activate the route action
     * @prop {Boolean} [router = false]
     */
    router: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * Color of the text in the navigation menu
     * @prop {String} textColor
     */
    textColor: {
      type: String,
      required: false
    },

    /**
     * Property that establishes if
     * only one sub-menu can be active
     * @prop {Boolean} [uniqueOpened = true]
     */
    uniqueOpened: {
      type: Boolean,
      required: false,
      default: true
    }
  },
  methods: {

    /**
     * Emit the event with the index of the menu item selected
     * @param {String} index identifier of the selected item
     * @public
     */
    elementSelected (index) {
      /**
       * this event emits the index of the menu
       * @event select
       * @type {string}
       */
      this.$emit('select', index)
    },

    /**
     * Create the vnode with the navigation menu items
     * @param {Array} menuItems Structure used to create the navigation menu
     * @param {Function} createElement create the vnode
     * @returns {VNode} Vue component tree
     * @public
     */
    createSubmenu (menuItems, createElement) {
      let self = this
      return menuItems.map((item) => {
        if (item.children) {
          return createElement(
            'el-submenu',
            {
              props: item.props
            },
            [
              createElement(
                'template',
                {
                  slot: 'title'
                },
                [
                  createElement('i', { class: ['menu-icon', item.icon] }),
                  createElement('span', { slot: 'title' }, self.translate(item.label))
                ]
              ),
              self.createSubmenu(item.children, createElement)
            ]
          )
        } else {
          return createElement(
            'el-menu-item',
            {
              props: item.props
            },
            [
              createElement('i', { class: ['menu-icon', item.icon] }),
              createElement(
                'span',
                {
                  slot: 'title'
                },
                self.translate(item.label)
              )
            ]
          )
        }
      })
    },
    /**
     * translates the label of the menu item
     * @param {String} message Represents the label to be translated
     * @return {String} Label translated
     * @public
     */
    translate (message) {
      return this.$i18n.t(`template.${message}.label`)
    }
  },
  render (createElement) {
    let self = this
    return createElement('el-menu',
      {
        props: {
          activeTextColor: self.activeTextColor,
          backgroundColor: self.backgroundColor,
          collapse: self.collapse,
          defaultActive: self.defaultActive,
          menuTrigger: self.menuTrigger,
          mode: self.mode,
          router: self.router,
          textColor: self.textColor,
          uniqueOpened: self.uniqueOpened
        },
        on: {
          select: self.elementSelected
        }
      },
      [
        self.createSubmenu(self.menuItems, createElement)
      ]
    )
  }
}
</script>

<style scoped>
  .menu-icon {
    margin: 0px 5px;
  }
</style>


