<script>

/**
 * @vue UIBreadcrumb.vue
 * this component shows in a friendly way the navigation routes,
 * which are received in an array
 * @version 0.1.0
 * @since 0.1.0
 * @author Nelson Rodriguez [gitlab:@nelrod]
 * @copyright Karanta, CA. 2018
 */
export default {
  name: 'UiBreadcrumb',
  props: {

    /**
     * Changes the icon separators of the breadcrumb
     * @prop {String} [iconSeparator = '']
     */
    iconSeparator: {
      type: String,
      required: false,
      default: ''
    },

    /**
     * Sets of links to be rendered
     * @prop {Array} links
     */
    links: {
      type: Array,
      required: true
    },

    /**
     * Activates vue-router in the breadcrumb
     * @prop {Boolean} [router = false]
     */
    router: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * Changes the size of the breadcrumb
     * @prop {String} [size = '']
     */
    size: {
      type: String,
      required: false,
      default: ''
    },

    /**
     * Changes the visibility of the icons of the elements in the breadcrumb
     * @prop {Boolean} [visibleIcon = true]
     */
    visibleIcon: {
      type: Boolean,
      required: false,
      default: true
    }
  },
  methods: {
    /**
     * Creates a vnode that emits event when it's clicked
     * @param {Function} createElement creates the vnode
     * @returns {Array} Array of vnode
     * @public
     */
    eventChildren (createElement) {
      const self = this
      let children = this.links.map((link, key) => {
        return createElement(
          'li',
          {
            class: (self.links.length - 1) === key ? 'is-active' : '',
            on: {
              click: function () {
                if ((self.links.length - 1) !== key) {
                  /**
                   * @event select
                   * @type {String}
                   */
                  self.$emit('select', link.path)
                }
              }
            }
          },
          [
            createElement(
              'a',
              [
                createElement(
                  'span',
                  {
                    class: [
                      'icon',
                      'is-small'
                    ],
                    style: {
                      display: self.visibleIcon ? '' : 'none'
                    }
                  },
                  [
                    /**
                     * Icon
                     */
                    createElement(
                      'i',
                      {
                        class: {
                          [link['icon']]: true
                        },
                        attrs: {
                          'aria-hidden': true
                        }
                      }
                    )
                  ]
                ),
                /**
                 * Label
                 */
                createElement(
                  'span',
                  link.label
                )
              ]
            )
          ]
        )
      })
      return children
    },

    /**
     * Creates a vnode of the breadcrumb
     * @param {Function} createElement creates the vnode
     * @return {Vnode} Vue component
     * @public
     */
    renderBreadcrumb (createElement) {
      const self = this
      let children = this.router ? this.routerChildren(createElement) : this.eventChildren(createElement)
      return createElement(
        'nav',
        {
          class: [
            'breadcrumb',
            self.size,
            self.iconSeparator
          ],
          attrs: {
            'arial-label': 'breadcrumbs'
          }
        },
        [
          createElement(
            'ul',
            children
          )
        ]
      )
    },
    /**
     * Creates a vnode that changes the navigation route when it's clicked
     * @param {Function} createElement create the vnode
     * @returns {Array} Array of vnode
     * @public
     */
    routerChildren (createElement) {
      const self = this
      let children = this.links.map((link, key) => {
        return createElement(
          'router-link',
          {
            class: (self.links.length - 1) === key ? 'is-active' : '',
            props: {
              to: {path: link.path},
              tag: 'li'
            }
          },
          [
            createElement(
              'a',
              [
                createElement(
                  'span',
                  {
                    class: [
                      'icon',
                      'is-small'
                    ],
                    style: {
                      display: self.visibleIcon ? '' : 'none'
                    }
                  },
                  [
                    /**
                     * Icon
                     */
                    createElement(
                      'i',
                      {
                        class: {
                          [link['icon']]: true
                        },
                        attrs: {
                          'aria-hidden': true
                        }
                      }
                    )
                  ]
                ),
                /**
                 * Label
                 */
                createElement(
                  'span',
                  link.label
                )
              ]
            )
          ]
        )
      })
      return children
    }
  },
  render (createElement) {
    return this.renderBreadcrumb(createElement)
  }
}
</script>

<style>

</style>



