<template>
  <div class="content">
    <title-component :element="titlePage"
      titleClass="is-size-2"
      :isPage="true">
    </title-component>
    <hr>
    <div class="columns is-desktop">
      <div class="column">
        <label>Map Height</label>
        <b-input type='number'
          v-model="basicProps.height" />
      </div>
      <div class="column">
        <label>Map Width</label>
        <b-input type='number'
          v-model="basicProps.width" />
      </div>
      <div class="column is-1">
        <label>Size Unit</label>
        <b-select v-model="basicProps.sizeUnit">
          <option v-for="unit in ['px','rem', '%']"
            :value="unit">
            {{unit}}
          </option>
        </b-select>
      </div>
      <div class="column is-2">
        <label>Add Markers</label>
        <br>
        <button
          class="button is-primary"
          @click="addMarkers(count)">
          Add Market
        </button>
      </div>
      <div class="column is-2">
        <label>Delete Markers</label>
        <br>
        <button
          class="button is-danger"
          @click="deleteMarkers(count)">
          Delete Market
        </button>
      </div>
    </div>
    <div class="columns">
      <div class="column is-8">
        <h3 class="is-size-3">UI Map</h3>
        <hr>
        <ui-map v-bind="props"
          :polygons="polygons"
          :polylines="polylines">
        </ui-map>
        <h3 class="is-size-3">UI Map, with delay on the elements and global animation</h3>
        <hr>
        <ui-map v-bind="props"
          markersAnimation='drop'
          :markersDelay="7500"
          :polygons="polygons"
          :polygonsDelay="2500"
          :polylines="polylines"
          :polylinesDelay="5000">
        </ui-map>
      </div>
      <div class="column">
        <b-message title="Console">
          <div class="console-content">
            <b-message title="Marker"
              size="is-small">
              <pre v-for="markers in props.markers">{{markers}}</pre>
            </b-message>
            <b-message title="Polygons"
              size="is-small">
              <pre>{{polygons[0]}}</pre>
            </b-message>
            <b-message title="Polyline"
              size="is-small">
              <pre>{{polylines[0]}}</pre>
            </b-message>
          </div>
        </b-message>
      </div>
      </div>
      <div class="colum">
        <hr>
        <div class="positionRight">
          <b-tooltip label="Changelog"
            :active="true"
            position="is-left"
            type="is-dark">
            <ui-panel-container :panelHeight="350"
              :panelWidth="550"
              unitHeight="px"
              unitWidth="px"
              backgroundColor="white"
              panelBorder="ridge"
              panelBorderColor="black"
              :collapseButtonEnabled="true"
              collapseButtonPosition="left"
              collapseButtonBackgroundColor="#C2C2C2"
              :style="collapseLeft"
              @on-collapse="collapsePanelLeft">
              <markdown></markdown>
            </ui-panel-container>
          </b-tooltip>
        </div>
    </div>
  </div>
</template>

<script>
import UIMap from '../../../components/common/UIMap/index.js'
import UIPanelContainer from '../../../components/common/UIPanelContainer/index.js'
import titleComponent from '../titleComponent.vue'
import { updates } from '../../../libraryUpdates.js'
import readme from '../../../components/common/UIMap/changelog.md'

export default {
  name: 'UiMapPage',

  components: {
    'UiMap': UIMap,
    'UiPanelContainer': UIPanelContainer,
    'markdown': readme,
    titleComponent
  },

  data () {
    return {
      /*
       * count number markers
       */
      count: 0,
      /**
       * Version data
       */
      titlePage: updates.Map,
      collapseLeft: {},
      /**
       * Polygons data on the google map.
       */
      polygons: [{
        paths: [
          [
            { lat: 12.0000000, lng: -62.0000000 },
            { lat: 12.0000000, lng: -70.0000000 },
            { lat: 6.0000000, lng: -70.0000000 },
            { lat: 6.0000000, lng: -62.0000000 }
          ],
          [
            { lat: 11.0000000, lng: -66.0000000 },
            { lat: 7.0000000, lng: -63.0000000 },
            { lat: 7.0000000, lng: -69.0000000 }
          ]
        ],
        strokeColor: 'orange',
        strokeOpacity: 1,
        fillColor: 'orange',
        fillOpacitiy: 1,
        title: 'Area1',
        info: {
          prop1: 'value 1',
          prop2: 'value2'
        }
      },
      {
        paths: [
          { lat: 21.0000000, lng: -66.0000000 },
          { lat: 9.0000000, lng: -60.0000000 },
          { lat: 9.0000000, lng: -72.0000000 }
        ],
        strokeColor: 'blue',
        strokeOpacity: 0,
        fillColor: 'blue',
        fillOpacitiy: 1,
        title: 'Area2',
        slotContent: '<p>Some <strong>HTML</strong> structure </p>'
      }],
      polylines: [{
        path: [
          { lat: 15.0000000, lng: -50.0000000 },
          { lat: 9.0000000, lng: -66.0000000 },
          { lat: 15.0000000, lng: -80.0000000 }
        ],
        strokeColor: '#009900',
        strokeOpacity: 1,
        strokeWeight: 3,
        title: 'Polyline 1',
        slotContent: '<p>Some <strong>HTML</strong> structure'
      },
      {
        path: [
          { lat: 9.0000000, lng: -50.0000000 },
          { lat: 15.0000000, lng: -66.0000000 },
          { lat: 9.0000000, lng: -80.0000000 }
        ],
        strokeColor: '#00da8c',
        strokeOpacity: 1,
        strokeWeight: 3,
        info: {
          title: 'Polyline 2',
          prop1: 'value 1',
          prop2: 'value 2'
        }
      }],
      basicProps: {
        center: {
          lat: 12.0000000,
          lng: -66.0000000
        },
        zoom: 5,
        height: 40,
        width: 100,
        sizeUnit: '%',
        markers: [{
          position: {
            lat: 8.0000000,
            lng: -66.0000000
          },
          label: 'marker',
          title: 'marker',
          animation: window.google.maps.Animation.DROP,
          slotContent: `<p>Some <strong>HTML</strong> structure</p>`
        }]
      }
    }
  },

  computed: {
    props () {
      let computedProps = this.basicProps
      return computedProps
    }
  },
  methods: {
    collapsePanelLeft (data) {
      this.collapseLeft = (data.collapse) ? {
        width: data.minWidth,
        transition: 'width 0.5s'
      } : {
        width: data.width,
        transition: 'width 0.5s'
      }
    },
    /**
     * Add map markers
     * @param {Number} cant the markers
     */
    addMarkers (count) {
      this.count += 1
      this.basicProps.markers.push({
        position: {
          lat: 8.0000000 + this.count,
          lng: -66.0000000 + this.count
        },
        label: 'marker ' + this.count,
        title: 'marker ' + this.count,
        animation: window.google.maps.Animation.DROP,
        slotContent: `<p>Some <strong>HTML</strong> structure</p>`
      })
    },
    /**
     * Delete map markers
     * @param {Number} cant the markers
     */
    deleteMarkers (count) {
      if (this.count > 0) {
        this.count -= 1
        this.basicProps.markers = this.basicProps.markers.splice(0, this.basicProps.markers.length - 1)
      }
    }
  }
}
</script>

<style>
.console-content {
  height: 15%;
  overflow: auto
}
.positionRight {
  margin-top: 80px;
  display: flex;
  justify-content: flex-end;
}
</style>



