<template>

  <div :class="dynamicPositionCollapseButton"
    :style="[dynamicPanelHeight,dynamicPanelWidth,dynamicBorder, dynamicColorBackground]">
    <!-- Collapse button left -->
    <div>
      <div v-if="collapseButtonEnabled"
        :style="[dynamicBackgroundColorCollapse, dynamicCursorClass]"
        class="collapsePanel"
        @click="orientationArrow"
        @mouseover="mouseOverOutClass"
        @mouseout="mouseOverOutClass">

        <span v-show="iconOrientation">
          <i class="fa fa-chevron-left"
            :class="[dynamicIconSizes,dynamicIconClassColor]"></i>
        </span>

        <span v-show="!iconOrientation">
          <i class="fa fa-chevron-right"
            :class="[dynamicIconSizes, dynamicIconClassColor]"></i>
        </span>
      </div>
    </div>

    <!-- Content -->
    <div class="contentOverflow">
      <!-- @slot use contentOverflow slot-->
      <slot></slot>
    </div>
  </div>
</template>
<script>

import {
  unitsHeight,
  unitsWidth,
  borders,
  minWidth

} from './config.js'
/**
 * Panel container for any content, it has the following fuctions:
 * Default slot
 * Automatic scroll
 * Collpase right and left button
 * @vue UIButton.vue
 * @version 0.1.0
 * @since 0.1.0
 * @author Alfredo Garcia [gitlab:@garciareal45]
 * @copyright Karanta, CA. 2018
 */
export default {

  name: 'UiPanelContainer',

  props: {

    /**
     * Property that sets the background color of the panel container. The color
     * types are RGB, color, hexadecimal
     * @prop {String} [backgroundColor= #CDCACA]
     */
    backgroundColor: {
      type: String,
      required: false,
      default: '#CDCACA'
    },
    /**
     * Property that sets the height of the panel  on the following units:
     * vh,px,%
     * @prop {Number} [panelHeight= 100]
     */
    panelHeight: {
      type: Number,
      required: false,
      default: 100
    },

    /**
     * Property that sets the widht of the panel on the following units:
     * vh,px,%
     * @prop {Number} [panelWidth= 100]
     */
    panelWidth: {
      type: Number,
      required: false,
      default: 100
    },

    /**
     * Property that sets the border of the panel container
     * @prop {String} [panelBorder= none]
     */
    panelBorder: {
      type: String,
      required: false,
      default: 'none'
    },

    /**
     * Property that sets the color border of the panel container
     * @prop {String} [panelBorderColor= black]
     */
    panelBorderColor: {
      type: String,
      required: false,
      default: 'black'
    },

    /**
     * Property that sets the height measure unit of the panel container
     * @prop {String} [unitHeight= %]
     */
    unitHeight: {
      type: String,
      required: false,
      default: '%'
    },

    /**
     * Property that sets the unit of measure on the panel container
     * @prop {String} [unitWidth= %]
     */
    unitWidth: {
      type: String,
      required: false,
      default: '%'
    },

    /**
     * Property that sets the position of the collapse button
     * @prop {String} [collapseButtonPosition= 'right']
     */
    collapseButtonPosition: {
      type: String,
      required: false,
      default: 'right'
    },

    /**
     * Property that enables the collapse button on the panel
     * @prop {Boolean} [collapseButtonEnabled= false]
     */
    collapseButtonEnabled: {
      type: Boolean,
      required: false,
      default: false
    },
    /**
     * Property that sets the cursor when is over the panel collapse
     * @prop {String} [cursorClass = 'pointer']
     */
    cursorClass: {
      type: String,
      required: false,
      default: 'pointer'
    },
    /**
     * Property that sets the class when the cursor is over the panel collapse
     * @prop {String} [collapseMouseOverClass = '#202020']
     */
    collapseMouseOverClass: {
      type: String,
      required: false,
      default: '#202020'
    },
    /**
     * Property that sets the background color on the collapse button
     * @prop {String} [collapseButtonBackgroundColor= '#C2C2C2']
     */
    collapseButtonBackgroundColor: {
      type: String,
      required: false,
      default: '#C2C2C2'
    },
    /**
    * Property that sets the icon sizes on the collapse panel
    * @prop {String} [iconSize= 'fa-1x']
    */
    iconSize: {
      type: String,
      default: 'fa-1x'
    },
    /**
     * Property that sets the icon color class on the collapse panel
     * @prop {String} [iconClassColor= 'has-text-primary']
     */
    iconClassColor: {
      type: String,
      default: 'has-text-primary'
    },
    /**
     * Sets if the panel initially is collapsed
     * @prop {Boolean}
     */
    isCollapsed: {
      type: Boolean,
      default: false
    },
    /**
     * Sets the direction of the icon
     * @prop {Boolean}
     */
    iconOrientation: {
      type: Boolean,
      required: false,
      default: false
    }
  },

  data () {
    return {
      /*
      * Property that sets the arrow icon orientation on the left collapse button
      * @type {Boolean}
      */
      collapse: false,
      /**
      * Property that notifies when the cursor is over the collapse panel
      * @type {Boolean}
      */
      mouseOverStatus: true,
       /**
       * Minimum width on the panel
       * @type {String}
       */
      minW: minWidth
    }
  },

  computed: {

    /**
     * Computed property that determines the height of the component on the following units:
     * vh,px,%
     * @return {Object}
     */
    dynamicPanelHeight () {
      return {
        height: this.panelHeight + this.dynamicUnitHeight
      }
    },
    /**
     * Computed property that determines the class of the cursor
     * @returns {String}
     */
    dynamicCursorClass () {
      return {
        'cursor': this.cursorClass
      }
    },
    /**
     * Computed property that determines the widht of the component on the following units:
     * vw,px,%
     * @return {Object}
     */
    dynamicPanelWidth () {
      if (this.isCollapsed) {
        this.collapse = this.isCollapsed
        /**
         * Sets the icon orientation
         * @event update:iconOrientation
         * @type {Boolean}
         */
        this.$emit('update:iconOrientation', this.isCollapsed)
        return {
          width: '10px'
        }
      }
      return {
        width: this.panelWidth + this.dynamicUnitWidth
      }
    },
    /**
     * Computed property that determines the height measure unit
     * @return {String}
     */
    dynamicUnitHeight () {
      if (unitsHeight.indexOf(this.unitHeight.toLowerCase()) === -1) {
        console.error('Wrong unit height')
      }
      return (unitsHeight.indexOf(this.unitHeight.toLowerCase()) !== -1) ? this.unitHeight.toLowerCase() : 'vh'
    },
    /**
     * Computed property that determines the width measure unit
     * @return {String}
     */
    dynamicUnitWidth () {
      if (unitsWidth.indexOf(this.unitWidth.toLowerCase()) === -1) {
        console.error('Wrong unit width')
      }
      return (unitsWidth.indexOf(this.unitWidth.toLowerCase()) !== -1) ? this.unitWidth.toLowerCase() : 'vw'
    },

    /**
     * Computed property that determines the border of the panel container
     * @return {Object}
     */
    dynamicBorder () {
      if (borders.indexOf(this.panelBorder.toLowerCase()) === -1) {
        console.error('Wrong border')
      }

      return {
        'border-style': (borders.indexOf(this.panelBorder.toLowerCase()) !== -1) ? this.panelBorder.toLowerCase() : 'none',
        'border-color': this.panelBorderColor
      }
    },

    /**
     * Computed property that determines the color of the panel container
     * @return {Object}
     */
    dynamicColorBackground () {
      return {
        'background': this.backgroundColor
      }
    },

    /**
     * Computed property that sets the position of the collapse button
     * @return {String}
     */
    dynamicPositionCollapseButton () {
      let positions = [
        'right',
        'left'
      ]

      if (positions.indexOf(this.collapseButtonPosition.toLowerCase()) === -1) {
        console.error('wrong position of the collapse button')
      }

      return (this.collapseButtonPosition.toLowerCase() === 'left') ? 'panelContainerLeft' : 'panelContainerRight'
    },

    /**
     * Computed property that sets the background color on the collapse button
     * @return {Object}
     */
    dynamicBackgroundColorCollapse () {
      return {
        'background-color': this.mouseOverStatus ? this.collapseButtonBackgroundColor : this.collapseMouseOverClass
      }
    },
    /**
     * Computed property that sets the icon size on the collapse panel
     * @return {Object}
     */
    dynamicIconSizes () {
      return {
        'fa-1x': (this.iconSize === 'fa-1x'),
        'fa-2x': (this.iconSize === 'fa-2x'),
        'fa-5x': (this.iconSize === 'fa-5x')
      }
    },
    /**
     * Computed property that sets the icon class color on the collapse panel
     * @return {Object}
     */
    dynamicIconClassColor () {
      return {
        'has-text-primary': (this.iconClassColor === 'has-text-primary'),
        'has-text-info': (this.iconClassColor === 'has-text-info'),
        'has-text-link': (this.iconClassColor === 'has-text-link'),
        'has-text-success': (this.iconClassColor === 'has-text-success'),
        'has-text-warning': (this.iconClassColor === 'has-text-warning'),
        'has-text-danger': (this.iconClassColor === 'has-text-danger'),
        'has-text-dark': (this.iconClassColor === 'has-text-dark')
      }
    }
  },

  methods: {

    /**
     * Method that sends the data about the witdth of panel container
     * @public
     * @return {Event}
     */
    collapseEvent (data) {
      /**
      * This event sends a object
      * @event on-collapse
      * @type {Object}
      */
      this.$emit('on-collapse', data)
    },
    /**
     * Method that sets the arrow orientation on the collapse button
     * @public
     */
    orientationArrow () {
      this.collapse = !this.collapse
      let data = {
        collapse: this.collapse,
        width: this.panelWidth + this.unitWidth,
        minWidth: this.minW
      }
      /**
       * Sets the icon orientation
       * @event update:iconOrientation
       * @type {Boolean}
       */
      this.$emit('update:iconOrientation', this.collapse)
      this.collapseEvent(data)
    },
    /**
     * This method notifies when the cursor is over the collapse panel
     * @public
     */
    mouseOverOutClass () {
      if (this.mouseOverStatus) {
        this.mouseOverStatus = false
      } else {
        this.mouseOverStatus = true
      }
    }
  }
}
</script>

<style scoped>
/*This class sets the flex orientation to row*/
.panelContainerLeft {
  display: flex;
  flex-direction: row;
}

/*Inverts the collapse button to the right side*/
.panelContainerRight {
  display: flex;
  flex-direction: row-reverse;
}

/*Inverts the direction of the arrow*/
.panelContainerRight span {
  transform: rotate(180deg);
}

/*Sets the overflow on the content of the panel container*/
.contentOverflow {
  width: 100%;
  overflow: auto;
}

/*This class centers vertically and horizontally the arrow*/
.collapsePanel {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
</style>



