// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import ElementUI from 'element-ui'
import router from './router/'
import lodash from 'lodash'
import VueLodash from 'vue-lodash'
import VeeValidate from 'vee-validate'
import Buefy from 'buefy'
import VueI18n from 'vue-i18n'
import { defaultLanguage, defaultDateFormatter } from './config'
import { ValidatorDictionary } from './plugins/VeeValidate/config.js'
import elEn from 'element-ui/lib/locale/lang/en'
import elEs from 'element-ui/lib/locale/lang/es'
import elPt from 'element-ui/lib/locale/lang/pt'
import ElementLocale from 'element-ui/lib/locale'
import VueHighcharts from 'vue-highcharts'
import Highcharts from 'highcharts'
import es from './lang/es.js'
import en from './lang/en.js'
import pt from './lang/pt.js'

// load these modules as your need
import loadStock from 'highcharts/modules/stock'
// import loadMap from 'highcharts/modules/map'
// import loadDrilldown from 'highcharts/modules/drilldown'
// // some charts like solid gauge require `highcharts-more.js`, you can find it in official demo.
// import loadHighchartsMore from 'highcharts/highcharts-more'
// import loadSolidGauge from 'highcharts/modules/solid-gauge'

loadStock(Highcharts)
// loadMap(Highcharts)
// loadDrilldown(Highcharts)
// loadHighchartsMore(Highcharts)
// loadSolidGauge(Highcharts)

Vue.use(VueHighcharts, { Highcharts })

let googleMapsKey = 'AIzaSyCko__qm5oM4k-7RPfLlsr7Bk3X8qUrbDw'

if (!window.google || !window.google.maps) {
  let src = googleMapsKey
    ? `https://maps.googleapis.com/maps/api/js?key=${googleMapsKey}&callback=initMap`
    : 'https://maps.googleapis.com/maps/api/js?v=3.exp'
  let googleScript = document.createElement('script')
  googleScript.src = src
  document.body.appendChild(googleScript)
}

Vue.config.productionTip = false

Vue.use(Buefy, {
  defaultIconPack: 'fa',
  defaultDateFormatter: defaultDateFormatter
})

Vue.use(VeeValidate, {
  dictionary: ValidatorDictionary,
  locale: defaultLanguage
})

Vue.use(VueLodash, lodash)

Vue.use(VueI18n)
Vue.use(ElementUI)

/**
 * @constant
 * @description Includes all the  ui components created for KPS.
 */
const KPSComponents = {}

/**
 * @description Instalation of the KPSComponents for using them as part of the library import on external projects.
 * @param {Object} Vue The Vue instance who receives the library components installation.
 * @param {Object} options Config Object
 */
KPSComponents.install = (Vue, options) => {}

export default KPSComponents

/*
 *  Translations to the Components of UILibrary
 */
const messages = {
  'es-ES': {
    ...es,
    /**
     * Translations of the Element ui
     */
    ...elEs
  },
  'en-US': {
    ...en,
    /**
     * Translations of the Element ui
     */
    ...elEn
  },
  'pt-PT': {
    ...pt,
    /**
     * Translations of the Element ui
     */
    ...elPt
  }
}

// Create VueI18n instance with options

const i18n = new VueI18n({
  locale: defaultLanguage,
  messages,
  fallbackLocale: 'es-ES'
})

ElementLocale.i18n((key, value) => i18n.t(key, value))

/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App/>',
  i18n,
  router,
  components: { App }
})



